import React, { Component } from "react";
import { Layout, Icon } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class Plumb extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Plumb</h1>
              <h4>George Bacovia</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section poezie">
                Dormeau adânc sicriele de plumb, Și flori de plumb și funerar
                veștmânt -<br />
                Stam singur în cavou… și era vânt…
                <br />
                Și scârțâiau coroanele de plumb.
                <br />
                Dormea întors amorul meu de plumb
                <br />
                Pe flori de plumb, și-am început să-l strig -<br />
                Stam singur lângă mort… și era frig…
                <br />
                Și-i atârnau aripile de plumb.
                <br />
              </div>
              <div class="section">
                <span className="title">Definiția curentului literar:</span>
                <p>
                  Simbolismul a apărut ca o reacție împotriva romantismului
                  decadent și a parnasianismului. Numele curentului este propus
                  de poezia articol "Le' Symbolisme" a lui Jean Moreas, din
                  1886.
                </p>
                <p>
                  Poezia simbolistă sugerează ideile, nu le exprimă, ajutându-se
                  de valori lexicale eufonice, de culori, gânduri și sentimente.
                  Precursorul simbolismului este considerat Charles Boudelarie,
                  autorul operei "Florile răului" în care valorifică estetică
                  urâtului.
                </p>
              </div>
              <div class="section">
                <span className="title">Trăsături:</span>
                <p>
                  "Plumb" este o poezie simbolistă datorită prezenței
                  simbolurilor, a corespondențelor, a sugestiei, a
                  muzicalității, a laitmotivelor și a refrenelor.
                </p>

                <p>
                  Teme specifice curentului ,precum natura, iubirea și moartea
                  se regăsesc în poezie. Obiectivul poeziei îl constituie
                  stările sufletești nedefinite, care neputând fi formulate clar
                  sunt transmise pe calea sugestiei.
                </p>
              </div>
              <div class="section">
                <span className="title">Apariția:</span>
                <p>
                  Poemul a fost publicat în volumul cu același nume în anul 1916
                  în revista "Literatorul"
                </p>
              </div>
              <div class="section">
                <span className="title">Semnificația titlului:</span>
                <p>
                  Titlul poeziei, " Plumb", este metaforic și simbolic sugerând
                  tema poeziei , și anume moartea. Gramatical, titlul este
                  alcătuit dintr-un substantiv simplu, nearticulat, care
                  sugerează ideea de generalitate.
                </p>
                <p>
                  În sens denotativ "plumb" semnifică metal greu, în sens
                  conotativ, denotă o apăsare sufletească, monotonie, plictis și
                  spleen. Plumbul este de culoarea gri, grîu' fiind culoarea
                  humei, sugerând toposul unde ajung oamenii și obiectele, o
                  stare de disperare, obsesia morții.
                </p>
                <p>
                  Ca și muzicalitate, titlul este compus din patru consoane și o
                  vocală, această construcție sugerează o închidere definită a
                  spațiului existențial, fără nici o posibilitate de ieșire.
                </p>
              </div>
              <div class="section">
                <span className="title">Temă:</span>
                <p>
                  Temele poeziei sunt moartea, natura, iubirea și condiția
                  omului de geniu. Eul liric se simte singur, izolat într-o
                  lume, care nu îl înțelege și îi îngrădește aspirațiile.
                </p>
              </div>
              <div class="section">
                <span className="title">Compoziția exterioară:</span>
                <p>
                  Poezia este alcătuită din două catrene, care corespund în
                  structură de adâncime celor două planuri, cel exterior din
                  prima strofă, sugerat de cuvintele: sicriu, cavouri și
                  coroane.
                </p>
                <p>
                  Iar cel interior din cea de a două strofă este sugerat de
                  sentimentul de iubire, de amor.
                </p>
              </div>
              <div class="section">
                <span className="title">Structura de adâncime:</span>
                <p>
                  <strong>În prima strofă</strong> spațiul simbolic este închis
                  și sufocant, el corespunde cu realitatea mediului în care
                  trăiește, viața în general, odaia. Cuvintele "sicriele" și
                  "cavou", "funerar" sugerează moartea. Metafora "flori de
                  plumb" este o sugestie a transformării vegetalului, implicit a
                  vieții în cenușiu, în mineral. Versul "Stăm singur în cavou...
                  și era vânt..." sugerează solitudinea eului liric, pustietatea
                  lui sufletească.
                </p>
                <p>
                  Cuvântul "plumb" apare în strofă de trei ori și se află în
                  corespondență cu stările sufletești ale poetului."Vântul" este
                  singurul element material dinamic, iar "ș" reprezintă sunetul
                  repetat obsesiv, asimilat ca și litera care șuieră. În strofă
                  regăsim motive precum cel al somnului, al izolării și al
                  decorului funebru.
                </p>
                <p>
                  <strong>În cea de a două strofă</strong> spațiul este închis,
                  interior, este un spațiu poetic interiorizat. Sentimentul de
                  iubire este sugerat de "amor întors" care semnifică moartea
                  sentimentului, inaccesibilitatea lui. Iubirea nu este
                  înălțătoare, ci rece, nu există perspective de împlinire.
                </p>
                <p>
                  Poezia reprezintă o confesiune lirică în care se folosește
                  persoană întâi singular. Mesajul transmis este concis și
                  cenzurat de orice fel de sentimentalisme. În poezie alternează
                  simbolul "plumb" cu alte cuvinte, cărora le schimbă sensul,
                  astfel "amor de plumb" sugerează un sentiment apăsător,
                  oboseală psihică.
                </p>

                <p>
                  Imperfectul verbelor "stăm, "dormea", "era", sugerează lipsa
                  oricăror stări optimiste, acțiunea lor neavând finalitate.
                  Gramatica cuvintelor este sugerată de elemente funerare,
                  precum culorile gri și negru, prezente în poezie. George
                  Bacovia însuși a mărturisit că "în poezie m-a obsedat
                  întotdeauna un subiect de culoare, pictura cuvintelor sau
                  audiția colorată. Fiecărui sentiment îi corespunde o culoare."
                </p>
                <p>
                  Toate cuvintele din poezie au o sonoritate surdă. În versul
                  "și-am început să-l strig", strigătul este accentuat prin
                  acest vers, care sugerează senzații intense, viziunile eului
                  liric asupra vieții. Eul liric dorește să evadeze din această
                  izolare, dar nu poate și rămâne doar un strigăt neajutorat, de
                  disperare, a expresiei a suferinței.
                </p>
                <p>
                  Totul la George Bacovia trimite spre imposibilitatea
                  comunicării. Plumbul apare simetric în ambele strofe, de șase
                  ori, apare ca și rimă și sugerează neputință eului liric de a
                  evada din această lume sufocantă.
                </p>
                <p>
                  Atmosfera acestei poezii este tulburătoare, mai întâi prin
                  simpla evocare a unei realități, moarte, la nivelul semantic
                  al cuvintelor folosite: sicrie, cavou, coroanele care
                  scârțâiau și prin repetiția cuvântului "plumb", cuvânt cheie
                  al întregii poezii, cuvânt magic folosit obsesiv. emoția
                  poeziei se declanșează încă din primul vers al celei de a doua
                  strofă : "Dormea întors amorul meu de plumb".
                </p>
                <p>
                  Cuvântul "întors" realizând misterul poeziei. Poetul sugerează
                  o cădere, un zbor în jos, care are specialitatea morții.
                  Aripile fiind de plumb, căderea este surdă și grea iar eul
                  liric nu mai are posibilitatea înălțării. Muzicalitatea este
                  realizată de tonalitatea minoră a imperfectelor : stăm,
                  dormeam, era și de cuvinte cu valoare de onomatopee și
                  stridente scârțâiau și frig.
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
