import React, { Component } from "react";
import { Layout, Icon, Checkbox } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
import { Row, Col } from "antd";
import { truncate } from "fs";
const { Content } = Layout;
export default class Plumb extends Component {
  render() {
    return (
      <div className="romana_intro">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <Row className="header">
              <Col className="left" xs={24} sm={24} md={12} lg={12} xl={12}>
                <h1>Educă-te!</h1>
                <h4>Nu doar cartile de joc iti aduc noroc!</h4>

                <Checkbox checked={false} disabled={true}>
                  Ultima noapte de dragoste, intaia noapte de razboi
                </Checkbox>
              </Col>
              <Col className="right" xs={24} sm={24} md={12} lg={12} xl={12}>
                <img
                  className="responsiveImage"
                  src="https://cdn.dribbble.com/users/1568450/screenshots/5430738/work_2_dribbble-01_2x.png"
                  alt=""
                />
              </Col>
            </Row>
          </div>
        </Content>
      </div>
    );
  }
}
