import React, { Component } from "react";
import { Layout, Breadcrumb } from "antd";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

// Components
import RomanaSider from "./RomanaSider";
import Intro from "./Intro";
import Plumb from "./Plumb";
import EuNuStrivesc from "./EuNuStrivesc";
import HarapAlb from "./HarapAlb";
import FloriDeMucigai from "./FloriDeMucigai";
import RigaCryptoSiLaponaEnigel from "./RigaCryptoSiLaponaEnigel";
import AciSosiPeVremuri from "./AciSosiPeVremuri";
import LeoaicaTanara from "./LeoaicaTanara";
import AlexandruLapusneanu from "./AlexandruLapusneanu";
import MoaraCuNoroc from "./MoaraCuNoroc";
import Ion from "./Ion";
import PatulLuiProcust from "./PatulLuiProcust";
import EnigmaOtiliei from "./EnigmaOtiliei";
import HanulAncutei from "./HanulAncutei";
import Morometii from "./Morometii";
export default class Romana extends Component {
  render() {
    return (
      <Layout>
        <RomanaSider />
        <Layout style={{ padding: "0 24px 24px" }}>
          <Route exact path="/romana/" component={Intro} />
          <Route path="/romana/genul-liric/plumb" component={Plumb} />
          <Route
            path="/romana/genul-liric/flori-de-mucigai"
            component={FloriDeMucigai}
          />
          <Route
            path="/romana/genul-liric/eu-nu-strivesc-corola-de-minuni-a-lumii"
            component={EuNuStrivesc}
          />
          <Route
            path="/romana/genul-liric/riga-crypto-si-lapona-enigel"
            component={RigaCryptoSiLaponaEnigel}
          />
          <Route
            path="/romana/genul-liric/aci-sosi-pe-vremuri"
            component={AciSosiPeVremuri}
          />
          <Route
            path="/romana/genul-liric/leoaica-tanara-iubirea"
            component={LeoaicaTanara}
          />
          {/* GENUL EPIC */}
          <Route path="/romana/genul-epic/harap-alb" component={HarapAlb} />
          <Route
            path="/romana/genul-epic/alexandru-lapusneanu"
            component={AlexandruLapusneanu}
          />
          <Route
            path="/romana/genul-epic/moara-cu-noroc"
            component={MoaraCuNoroc}
          />
          <Route path="/romana/genul-epic/ion" component={Ion} />
          <Route
            path="/romana/genul-epic/patul-lui-procust"
            component={PatulLuiProcust}
          />
          <Route
            path="/romana/genul-epic/enigma-otiliei"
            component={EnigmaOtiliei}
          />
          <Route
            path="/romana/genul-epic/hanul-ancutei"
            component={HanulAncutei}
          />
          <Route path="/romana/genul-epic/morometii" component={Morometii} />
        </Layout>
      </Layout>
    );
  }
}
