import React, { Component } from "react";
import { Layout } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class EuNuStrivesc extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Eu nu strivesc corola de minuni a lumii</h1>
              <h4>Lucian Blaga</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section poezie">
                Eu nu strivesc corola de minuni a lumii <br />
                şi nu ucid <br />
                cu mintea <br />
                tainele, ce le-ntâlnesc <br />
                în calea mea <br />
                în flori, în ochi, pe buze ori morminte <br />
                Lumina altora <br />
                sugrumă vraja nepătrunsului ascuns <br />
                în adâncimi de întuneric, <br />
                dar eu, <br />
                eu cu lumina mea sporesc a lumii taină - <br />
                şi-ntocmai cum cu razele ei albe luna <br />
                nu micşorează, ci tremurătoare <br />
                măreşte şi mai tare taina nopţii, <br />
                aşa îmbogăţesc şi eu întunecată zare <br />
                cu largi fiori de sfânt mister <br />
                şi tot ce-i ne-nţeles <br />
                se schimbă-n ne-nţelesuri şi mai mari <br />
                sub ochii mei -<br />
                căci eu iubesc <br />
                şi flori şi ochi şi buze şi morminte
              </div>
              <div class="section">
                <span className="title">Poezia:</span>
                <p>
                  Poezia “Eu nu strivesc corola de minuni a lumii” de Lucian
                  Blaga este o artă poetică modernă, alături de Testament de
                  Tudor Arghezi şi Joc secund de Ion Barbu. Poezia deschide
                  volumul de debut, Poemele luminii şi are rol de manifest
                  literar.
                </p>

                <p>
                  Dacă Arghezi punea în centrul artei sale poetice problema
                  limbajului şi a „cuvintelor potrivite”, Blaga accentuează
                  raportul dintre poet şi lume şi dintre poet şi creaţie. În
                  această poezie autorul face distincţia
                </p>

                <p>
                  între cele două tipuri de cunoaştere teoretizate în lucrarea
                  Cunoaşterea luciferică (1933).
                </p>

                <p>
                  <strong>Cunoaşterea paradisiacă</strong> este cunoaşterea de
                  tip raţional, care reduce misterul lumii prin intermediul
                  logicii şi al intelectului.
                </p>
                <p>
                  În schimb, <strong>cunoaşterea luciferică</strong> este bazată
                  pe intuiţie, pe imaginaţie, pe trăiri interioare, putând fi
                  echivalată cu o cunoaştere de tip poetic. În creaţia autorului
                  se observă în mod clar opţiunea pentru al doilea tip de
                  cunoaştere.
                </p>
                <p>
                  În poezia “Eu nu strivesc corola de minuni a lumii” rolul
                  poetului nu este de a descifra tainele lumii ci de a le
                  potenţa prin trăirea interioară şi prin contemplarea formelor
                  concrete prin care ele se înfăţişează.
                </p>
                <p>
                  Rolul poeziei este acela că, prin mit şi simbol, elemente
                  specifice imaginaţiei, creatorul să pătrundă în tainele
                  Universului, sporindu-le. Creaţia este un mijlocitor între eu
                  şi lume care nu reduce însă misterul cuvântului originar.
                  Acest cuvânt poetic nu este folosit însă pentru a numi ci
                  pentru a sugera.
                </p>
              </div>
              <div class="section">
                <span className="title">Tema poeziei:</span>
                <p>
                  Tema poeziei o reprezintă atitudinea poetică în faţa marilor
                  taine ale Universului conform căreia cunoaşterea lumii este
                  posibilă numai prin iubire, prin comunicare afectivă totală.
                </p>
                <p>
                  Poezia este scrisă sub forma unei confesiuni lirice, în care
                  Lucian Blaga adoptă formula lirismului subiectiv, subliniat de
                  atitudinea poetică transmisă în mod direct şi prin mărcile
                  lingvistice ale subiectivităţii: pronumele personale la
                  persoana I singular: eu (care se repetă de cinci ori pe
                  parcursul poeziei), adjectivul posesiv de persoana I: mea,
                  verbele la persoana I: nu strivesc, nu ucid, întânlesc,
                  sporesc, îmbogăţesc, iubesc.
                </p>
              </div>
              <div class="section">
                <span className="title">Titlul:</span>
                <p>
                  Titlul include o metaforă revelatorie, „corola de minuni a
                  lumii”, care semnifică ideea cunoaşterii luciferice. „Corola
                  de minuni a lumii”, imagine a perfecţiunii, a absolutului,
                  prin ideea de cerc, de întreg, semnifică misterele universale,
                  iar rolul poetului este de a adânci taina.
                </p>
              </div>
              <div class="section">
                <span className="title">Incipitul:</span>
                <p>
                  Incipitul reia titlul poeziei, aflându-se într-o relaţie de
                  sens cu versurile care încheie arta poetică:
                  <span className="citat">
                    „Eu nu strivesc corola de minuni a lumii[...] căci eu iubesc
                    / şi flori şi ochi şi buze şi morminte.”{" "}
                  </span>
                </p>
                <p>
                  Faptul că această artă poetică începe cu pronumele personal
                  „eu” atrage atenţia asupra ipostazei eului liric, care este un
                  eu stihial, exacerbat.
                </p>
                <p>
                  Încă din primul vers al poeziei se subliniază poziţia pe care
                  o adoptă poetul în legătură cu misterele lumii, el refuzând să
                  le cunoască în mod raţional, fapt exprimat prin verbul la
                  forma negativă „nu strivesc”.
                </p>
                <p>
                  Ideea se menţine pe parcursul operei prin intermediul altor
                  verbe semnificative: „nu ucid”, „sporesc”, „îmbogăţesc”,
                  „iubesc”.
                </p>
              </div>
              <div class="section">
                <span className="title">Compoziție:</span>
                <p>
                  Poezia este alcătuită din trei secvenţe poetice, prima şi
                  ultima aflându-se într-o relaţie de simetrie deoarece amândouă
                  descriu poziţia poetului în legătură cu creaţia şi misterul.
                </p>
                <p>
                  Se remarcă însă şi relaţia de opoziţie dintre aceste două
                  secvenţe şi a doua, care îi include pe creatorii care se
                  folosesc de cunoaşterea paradisiacă („lumina altora”).
                </p>

                <p>
                  <strong>Prima secvenţă </strong>oferă o definiţie a creaţiei,
                  poezia însemnând pentru Blaga intuirea în particular („eu nu
                  strivesc”) a universalului, a misterului. De asemenea, este
                  prezentată atitudinea poetică faţă de acest mister, exprimată
                  prin verbele la forma negativă: „nu strivesc, nu ucid”.
                </p>
                <p>
                  Eul liric refuză cunoaşterea paradisiacă, raţională („cu
                  mintea”) a misterului, care ar duce de fapt la dispariţia
                  acestuia. Drumul creaţiei pe care şi-l alege este exprimat
                  prin substantivul dublat de un adjectiv pronominal posesiv
                  (care exprimă tocmai ideea de opţiune): „calea mea”.
                </p>

                <p>
                  Minunile corolei sunt descrise prin patru metafore-simbol,
                  care se referă la temele creaţiei blagiene: „în flori, în
                  ochi, pe buze ori morminte”. Florile simbolizează viaţa,
                  efemeritatea, dar şi frumosul, ochii cunoaşterea, contemplarea
                  poetică a lumii, buzele iubirea, dar şi rostirea poetică, iar
                  mormintele moartea, eternitatea, două teme care au fost
                  asociate de toţi poeţii cu misterul datorită imposibilităţii
                  de a le cunoaşte integral.
                </p>

                <p>
                  <strong>A doua secvenţă</strong> este construită pe relaţia de
                  opoziţie dintre opţiunea poetului şi opţiunile altor scriitori
                  în ceea ce priveşte revelarea misterului. Această opoziţie
                  semnifică de fapt antiteză dintre cele două tipuri de
                  cunoaştere, paradisiacă şi luciferică.
                </p>

                <p>
                  Diferenţa dintre cele două atitudini poetice este redată la
                  nivelul textului de către pronumele personal „eu”, adjectivul
                  pronominal posesiv „mea” („lumina mea”) şi adjectivul
                  nehotărât „altora” („lumina altora”). Metafora luminii, care
                  este metafora centrală a volumului Poemele luminii,
                  simbolizează cunoaşterea.
                </p>

                <p>
                  Cele două tipuri de cunoaştere sunt redate prin asocierea
                  acestor elemente de opoziţie cu verbe sugestive care le pun şi
                  mai bine în evidenţă: „Lumina altora / sugrumă vraja
                  nepătrunsului ascuns”, în timp ce eul liric blagian sporeşte
                  „a lumii taină / [...] nu micşorează, ci tremurătoare /
                  măreşte şi mai tare taina nopţii.”
                </p>

                <p>
                  Elementele care ţin de mister, de imaginarul poetic sunt
                  sugerate de alte cuvinte-simbol, care fac parte din câmpul
                  semantic al misterului: „nepătrunsul ascuns” (epitet
                  metaforic, inversiune), „adâncimi de întuneric” (metaforă), „a
                  lumii taină”, „taina nopţii” (metaforă), „întunecată zare”
                  (epitet, inversiune), „largi fiori de sfânt mister” (epitete
                  metaforice), „ne-nţeles”, „ne-nţelesuri şi mai mari”. Crezul
                  poetic este reluat în ultima secvenţă, aflată în relaţie de
                  simetrie cu prima.
                </p>

                <p>
                  <strong>Finalul</strong> are un rol conclusiv (folosirea
                  conjuncţiei „căci” nu este întâmplătoare), în care poetul reia
                  imaginea corolei alcătuită din elementele misterului poetic:
                  <span className="citat">
                    „căci eu iubesc / şi flori şi ochi şi buze şi morminte.”
                  </span>
                </p>
                <p>
                  Cunoaşterea poetică este atât un act de contemplaţie (aşa cum
                  reiese din ultimele două versuri ale celei de a doua secvenţe:
                  „se schimbă-n ne-nţelesuri şi mai mari / sub ochii mei”), cât
                  şi un act de iubire: „căci eu iubesc”.
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
