import React, { Component } from "react";
import { Layout } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class Morometii extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Morometii</h1>
              <h4>Marin Preda</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section">
                <span className="title">Definiția romanului: </span>
                <p>
                  Definiția romanului: Specie a genului epic, în proză, de mare
                  întindere, având o acțiune complexă desfășurată pe mai multe
                  planuri narative cu un număr mare de personaje.
                </p>
              </div>
              <div class="section">
                <span className="title">Trăsături: </span>
                <p>
                  Opera este că specie un roman deoarece este o structură
                  narativă de mare întindere, subiectul cuprinde două planuri
                  narative paralele ale acțiunii și două nuclee. Acțiunea este
                  amplă și implică un număr mare de personaje cu ponderi
                  diferite în ansamblul epic și diversificate psihologic.
                  Cunoaște o mare varietate de forme și formule epice și tehnici
                  narative.
                </p>
              </div>
              <div class="section">
                <span className="title">Apariție: </span>
                <p>
                  Primul volum a apărut în anul 1955, impunând un personaj cu
                  totul original în literatura română, care depășește limitele
                  timpului în care se înscrie. Cel de-al doilea volum apare în
                  anul 1967.
                </p>
              </div>
              <div class="section">
                <span className="title">Titlul: </span>
                <p>
                  Anunță tema principală a volumului I, acest substantiv propriu
                  ales intenționat la numărul pluralderecționează direcția
                  cititorului către toți membrii familiei cu conflictele
                  acestora, dar și către gospodăria și pământurile lor care
                  completează imaginea a ceea ce înseamnă condiția țăranului
                  român de la începutul secolului al XX-lea. Titlul "Moromeții"
                  concentrează într-un singur cuvânt toată complexitatea
                  romanului, care își propune să dezbată, prin exeplificare,
                  ideea de întreg și de parte a întregului, de coagulare și
                  disoluție, de unitate și dezbinare.
                </p>
              </div>
              <div class="section">
                <span className="title">Temă: </span>
                <p>
                  Tema principală a romanului este "destrămarea familiei și a
                  civilizației tradiționale țărănești", alte teme, secundare,
                  fiind: paternitatea, viața, moartea, timpul, istoria (condiția
                  omului confruntat cu istoria) libertatea și constrângerea,
                  solidaritatea umană, exilul interior, iubirea, cunoașterea.
                  Autorul așează tema "familiei" în centrul romanului,
                  explicația fiind aceea că evoluția și criza acesteia sunt
                  simbolice pentru transformările din satul românesc al vremii,
                  astfel romanul unei familii este romanul disoluției satului
                  românesc.
                </p>
              </div>
              <div class="section">
                <span className="title">
                  Viziunea despre lume a autorului:{" "}
                </span>
                <p>
                  Se conturează în roman prin tematică abordată, prin conflicte,
                  evenimentele prezentate, particularitățile de compoziție și
                  mai ales prin perspectiva personajului Ilie Moromete în
                  confruntarea cu întâmplările vieții. Preda meditează asupra
                  uneia din problemele cele mai importante ale existenței
                  românești din a doua jumătate a secolului al XX-lea, asupra
                  temei fundamentale a operei, dispariția țărănimii
                  tradiționale.
                </p>
              </div>
              <div class="section">
                <span className="title">Perspectiva/Tehnică narativă: </span>
                <p>
                  Există două perspective narative, una obiectivă și altă
                  subiectivă. Cea obiectivă este a unui narator omniscient, ceea
                  ce face ca narațiunea să fie heterodiegetică (scrisă la
                  persoana a III-a); însă omnisciența autorului este câteodată
                  limitată din cauza faptului că intervine o tehnică modernă,
                  subiectivă a eroilor (în primul volum a tatălui, iar în al
                  doilea al fiilor) sau a unor informatori (personaje-martori ai
                  evenimentelor, care relatează faptele ulterior altor
                  personaje, de exemplu Parizianu povestește despre vizita lui
                  Moromete la fiii săi, la București).
                </p>
              </div>
              <div class="section">
                <span className="title">Compoziția: </span>
                <p>Structura celor două volume evidențiază trei planuri :</p>
                <p>
                  <strong>1. </strong> destinul familiei-plan narativ principal.
                </p>
                <p>
                  <strong>2. </strong> planul destinului individual.
                </p>
                <p>
                  <strong>3. </strong> planul destinului comunitatii rurale.
                </p>
                <p>
                  Desfășurarea lor epică se realizează în jurul a două
                  personaje: tatăl (în primul volum) și fiul cel mic, Niculae
                  (În al doilea). Primul volum are o compoziție închisă și este
                  alcătuit din 3 părți care cuprind 29, 18, 29 de capitole, cu
                  ritmuri epice diferite. Al doilea volum cuprinde 5 părți,
                  grupate în 91 de capitole.
                </p>
              </div>
              <div class="section">
                <span className="title">Conflictele: </span>
                <p>
                  Sunt prezente atât conflicte de natură interioară (în sufletul
                  lui Moromete), cât și de natură exterioară, între Moromete și
                  cei trei fii (conflictul dintre generatii), între cele două
                  serii de copii, între Catrina și Moromete, între Guica și
                  Catrina, între Țugurlan și sătenii etc.
                </p>
              </div>
              <div class="section">
                <span className="title">Relații Spațiale și temporale: </span>
                <p>
                  Acțiunea primului volum se petrece de la începutul verii până
                  în toamna anului 1937 în satul Siliștea-Gumesti. Volumul al
                  doilea sulprinde intervalul de dinaintea și de după izbucnirea
                  războiului, precum anii 1950 cu toate transformările suferite
                  de țărani din cauza noilor reforme comuniste.
                </p>
              </div>
              <div class="section">
                <span className="title">Povestirea: </span>
                <p>
                  Acțiunea primului volum este structurată pe mai multe planuri
                  narative. În prim plan se afla Moromeții, o familie numeroasă,
                  măcinată de nemulțumiri mocnite.
                </p>
                <p>
                  Țăran mijlocaș, Ilie Moromete încearcă să păstreze întreg, cu
                  prețul unui trai modest, pământul familiei sale, pentru a-l
                  transmite apoi băieților.
                </p>
                <p>
                  Fiii cei mari ai lui Ilie Moromete, Paraschiv, Nilă și Achim,
                  își doresc independența economică și un trai mai bun la oraș.
                  Ei se simt nedreptățiți pentru că, după moartea mamei lor,
                  Ilie Moromete s-a însurat cu altă femeie, Catrina, care avea
                  încă trei copii: Tîta, Ilinca și Niculae. Îndemnați de sora
                  lui Ilie, Măria Moromete, poreclită Guică, cei trei băieți pun
                  la cale un plan distructiv, dându-și seama că problemele
                  economice ale familiei nu vor putea fi depășite. Ei
                  intenționează să plece la București. Fără știrea familiei,
                  pentru a-și face un rost. În acest scop, ei vor să ia oile,
                  cumpărate printr-un împrumut la bancă și al căror lapte
                  constituie principala hrană a familiei, și caii,
                  indispensabili pentru munca la câmp. Prin vânzarea oilor și a
                  cailor, ar obține un capital pentru a începe viața la oraș.
                </p>
                <p>
                  Datoria la banca nefiind achitată, planul celor trei băieți dă
                  o grea lovitură familiei. Achim îi propune tatălui să-l lase
                  să plece cu oile la București, să le pască în marginea
                  orașului și să vândă laptele și brânză la un preț mai bun în
                  capitală. Moromete se lăsă convins de utilitatea acestui plan,
                  amână achitarea datoriei la bancă și vinde o parte din lotul
                  familiei pentru a-și putea plăti impozitul pe pământ,
                  fonciirea. Însă Achim vinde oile la București și așteaptă
                  venirea fraților.
                </p>
                <p>
                  După amânările generate de refuzul lui Nila de a-și lăsa tatăl
                  singur în preajma secerișului, cei doi fug cu caii și cu o
                  parte din zestrea surorilor. Moromete este nevoit să vândă din
                  nou o parte din pământ pentru a-și reface gospodăria, pentru a
                  plăti foncierea, rată la bancă și taxele de școlarizare ale
                  lui Niculae, fiul cel mic. Planurile secundare completează
                  acțiunea romanului, conferindu-i caracterul de frescă socială:
                  boala lui Botoghină, revolta țăranului sărac Țugurlan, familia
                  chiaburului Tudor Bălosu, dragostea dintre Polina și Birică,
                  discuțiile din poiana lui Iocan, rolul instituțiilor și al
                  autorităților în satul interbelic.
                </p>
                <p>
                  În volumul al doilea, vechea imagine a lui Ilie Moromete este
                  distrusă, fiind înlocuită de o alta, lipsită de glorie.
                  Autoritatea lui în sat se diminuează, iar unitatea familiei nu
                  se reface. Se derulează o istorie tulbure. Țăranii își schimbă
                  atitudinea față de Ilie Moromete. Vechile dușmanii se sting.
                  Guica murise, fără ca relațiile cu fratele ei să schimbe.
                  Moromete se apucase de negoț, treburile îi mergeau bine,
                  câștigase bani frumoși, dar, în mod inexplicabil, îl retrage
                  pe Niculae de la școală pe motiv că nu-i aduce nici un
                  beneficiu. Toată atenția și energia tatălui se concentrează în
                  încercarea de a-i aduce acasă pe băieții fugari. Astfel el
                  cumpără la loc pământurile vândute odinioară și pleacă la
                  București pentru a-i convinge pe băieții mai mari să revină în
                  sat.
                </p>
                <p>
                  Paraschiv care lucra acum ca sudor la tramvaie, Nilă, portar
                  la un bloc și Achim, care avea un mic magazin de „Consum
                  alimentar" refuză încercarea de reconcilere a tatălui. Mai
                  mult decât atât, aflând de „trădarea" soțului, Catrina îl
                  părăsește, ducându-se să locuiască „în vale", la fața ei din
                  prima căsătorie, Alboaică.
                </p>
                <p>
                  Destrămarea familiei continuă cu moartea lui Nilă în război.
                  Fetele se căsătoresc, dar familia Moromete pare atinsă de un
                  blestem, fiindcă soțul Țiței, deși scăpase din război, moare
                  într-un accident stupid în sat. Paralel cu procesul de
                  disoluție a familiei Moromete, este prezentată și destrămarea
                  satului tradițional.
                </p>
                <p>
                  Fiul cel mic, Niculae, se îndepărtează din ce în ce mai mult
                  de tatăl său, se înscrie în partidul comunist, este trimis la
                  o școală pentru activiști și se întoarce în sat cu sarcina de
                  a supraveghea strângerea cotelor și predarea lor către stat.
                  Niculae se orientează cu dificultate în țesătură de intrigi
                  pusă la cale de oportuniștii de profesie, care iscă un
                  conflict în sat. Aceste întâmplări determină destituirea
                  activistului Niculae Moromete, care se retrage pentru a-și
                  continua studiile și devine inginer horticultor.
                </p>
                <p>
                  Evenimentele se concentrează în vara anului 1951, în paralel
                  cu secerișul și treieratul grâului și cu ședința organizației
                  de partid sătești, în care este numit președinte al Sfatului
                  Popular tânărul țăran sărac, Vasile al Moașei. Pe de altă
                  parte, Ilie Moromete își pierde prestigiul de altădată,
                  autoritatea lui în sat se diminuează, vechii prieteni au murit
                  sau l-au părăsit, iar noii prieteni i se par mediocri.
                </p>
                <p>
                  Bărbatul trăiește o iubire târzie cu Fică, sora mai mică a
                  fostei lui soții, care a fost toată viața îndrăgostită de el,
                  apoi se implică în viața social-politică a satului, sprijinind
                  candidatura lui Țugurlan în funcția de președinte al Sfatului
                  Popular, pentru ca acesta să tempereze acțiunea de
                  colectivizare.
                </p>
                <p>
                  Ilie Moromete nu accepta ideea că rostul lui în lume a fost
                  greșit și că țăranul trebuie ,,să dispară ". Romanul se
                  încheie zece ani mai târziu când Niculae, ajuns inginer
                  horticol și căsătorit cu Mărioara, fiica lui Adam Fântâna,
                  care a devenit și ea asistență medicală, află de la Ilinca,
                  sora sa, că Moromete murise, se stinsese încet fără a suferi
                  de vreo boală. În ultimele clipe de viață, Moromete avea
                  slăbiciunea de a umbla prin sat. Ultima oară fusese adus acasă
                  cu roabă. Căzut la pat, el își exprimă crezul de viață
                  spunându-i doctorului: „Domnule... eu totdeauna am dus o viață
                  independentă. ".În finalul romanului, tatăl și fiul se împacă
                  în visul lui Niculae."
                </p>
              </div>
              <div class="section">
                <span className="title">Caracterizarea personajelor: </span>
                <p>
                  <strong>Ilie Moromete: </strong>Ilie Moromete esta un țăran
                  din Siliștea-Gumeșt, tatăl a șase copii, Paraschiv, Nilă,
                  Achim, Tita, Ilinca și Niculae, și soțul Catrinei. El este
                  capul familiei, însă are probleme financiare.
                </p>
                <p>
                  Este un om rațional în ceea ce privește atitudinea lui față de
                  pământ. Spre deosebire de Ion al lui Rebreanu, care era
                  dominat de instinctul de posesiune, de lăcomie pentru pământ,
                  Moromete nu este sclavul îmbogățirii, ci pământul constituie
                  pentru el simbolul libertății materiale și spirituale.
                </p>
                <p>
                  În comparație cu ceilalți țărani, Moromete nu are nimic de
                  făcut atunci când toți ceilalți vecini ai săi sunt în casă
                  muncind, fiind preocupați de problema supraviețuirii. Se
                  crează astfel țăranul mijlocaș: nu e atât de sărac încât să
                  fie tot timpul preocupat de problema supraviețuirii, aceasta
                  neafectându-i personalitatea, dar nu e nici foarte bogat încât
                  să fie dușmănit de vecinii săi mai săraci. De aceea el are
                  timp și disponibilitate către comunicare. Are o foarte mare
                  plăcere de a comunica, fiind într-o contiună așteptare a altor
                  persoane cu care să vorbească “Stătea degeaba, nu se uită în
                  mod deosebit, dar pe fața lui se vedea că n-ar fi rău dacă
                  s-ar ivi cineva”.
                </p>
                <p>
                  Când chiar nu are cu cine să vorbească sau când ajunge la
                  concluzia că nimeni nu e demn să vorbească cu el, vorbește
                  singur. Gândurile sale sunt aparent un haos total, însă el
                  face niște legături ce nu sunt accesibile oricui, iar acesta
                  legături nu sunt prezentate nici de față cu celialți, lucru ce
                  îi determină pe săteni să îl considere un ciudat.
                </p>
                <p>
                  Când vrea să pară serios, Moromete este de fapt ironic, iar
                  când este foarte serios și trăiește cu intensitate, în
                  exterior vrea să pară că se amuză. El procedează astfel în
                  scena tăierii salcâmului, dându-i răspunsuri ironice lui Nilă
                  deși era un moment foarte important. Monologul personajului
                  este aparent dezorganizat, însă există conexiuni pe care
                  cititorii nu la percep, deoarece Moromete are un alt fel de a
                  gândi, o viziune diferită asupra lumii decât restul oamenilor.
                  Acest monolog are o structură diferită: este fie interior, la
                  persoana I, fie un monolog adresat simplu, fie un monolog
                  adresat prin dedublare, când vorbește cu el însuși ca și cum
                  ar vorbii cu altă persoană. Poziția lui Moromete la masă este
                  deasupre tuturor, el având o poziție privilegiată, pe prag.
                  Statutul său era acela de cap al familiei, nimeni nu avea
                  acest drept în afară de el, iar statutul său era asumat prin
                  tradiție.
                </p>
                <p>
                  Astfel personajul nu se află în nici o tabără, el asigurând
                  echilibru și stabilitate în familie. Este întruchiparea
                  autorității, și este conștient de statutul său, acceptându-l.
                </p>
                <p>
                  Dialogul sau cu vecinul său, Tudor Baloșul, începe cu o
                  replică cu o funcție fatică, însă această discuție este un
                  pretext pentru a ajunge la un anumit subiect, Balosul vrând de
                  fapt să vorbească despre salcâm. Până în momentul când acest
                  subiect ia viața, dialogul este unul normal, însă când vine
                  vorba de salcâm, Moromete abordează un alt subiect.
                </p>
                <p>
                  Vecinul său ia acest răspuns drept un refuz, și încearcă în
                  contiunuare să îl convingă să vândă. Moromete evită
                  întrebările incomode, iar conversația pare a fi un dialog al
                  surzilor.Însă în realitate, cei doi se înțeleg, personajele
                  știind să citească în spatele cuvintelor, Moromete având
                  capacitatea de a vorbii cu subanțeles. La fierărie, locul unde
                  sătenii se întâlneau să discute politica, Ilie Mormmete are un
                  rol important, fiind privilegiat.
                </p>
                <p>
                  Când ajunge la fierărie, el îi ignoră pe țărani ce îl așteptau
                  cu nerăbdare, și intră mai întâi la frizerie, ținându-i astfel
                  în suspans. Pentru a-și da o și mai mare importanță, în
                  momentul când intră în fierărie personajul spune “Ce e, mă, ce
                  v-ați adunat aicea?!” În momentul când anunța ce o să
                  citească, se face liniște, captând atenția. El citește “ca și
                  cum ar fi ținut el un discurs”, observându-se aici un alt rol
                  jucat de Moromete.
                </p>
                <p>
                  Personajul are o plăcere de a vorbii, iar în momentul când
                  citește un discurs, își schimbă glasul, manipulând astfel
                  auditoriul. După ce termină de citit, iar oamneii încep să
                  râdă deoarece nu au înțeles discursul, Moromete le explcă.
                </p>
                <p>
                  Personajul sintetizează informațiile și la prezintă pe
                  înțelesul publicului, arătându-și astfel capacitatea de
                  sinteză. În momentul când Țugurlan, unul dintre țăranii săraci
                  ai satului se revoltă și începe să țină un discurs, Moromete
                  pune capăt întregii discuții, sintetizând problemele ridicate
                  de Țugurlan, însă îi întoarce cuvintele, aparent susținându-l,
                  când de fapt el ocolește problema reală pusă de Țugurlan.
                </p>
                <p>
                  {" "}
                  Când se întoarce de la fierărie, personajul este conștient că
                  îl așteaptă agenții îi vor cere plata foncirii, însă Moromete
                  intră în curte și începe să îi strige pe ceilalți membrii ai
                  familiei, ignorându-i pe agenți și astfel enervendu-i. Mimează
                  că este un om foarte ocupat, iar cei doi agenți sunt
                  nesemnificativi în comparație cu problemele sale “Paraschive,
                  tu unde ești, mă? Nu vezi că furca aia stă acolo lângă gard de
                  cinci săptămâni? Ia-o de acolo și bag-o în șopron! Ar putea să
                  stea acolo pân-o rugini, până te-ai înțepa în ea și tot n-ai
                  s-o ridici!” Deodată, se întoarce spre agenți, luându-i prin
                  surprindere, într-un moment nepregătit spunându-le răspicat
                  “N-am!”.
                </p>
                <p>
                  Această reprezintă tehnica lui pentru a controla situația,
                  astfel să creeze un avantaj. Însă agenții sunt învățați cu
                  țăranii, iar la început sunt destul de duri, dar pe drum ei se
                  mai înmoaie, ajungând ca la sfârșit să înceapă să facă unele
                  compromisuri.
                </p>
                <p>
                  Se crează un contrast între gravitatea situației și aparenta
                  indiferentă a lui Moromete, însă aceasta reprezintă tehnica
                  lui de intimidare. Până la urmă plătește o mie de lei, vrând
                  astfel să pară că face o favoare. Pentru ca perceptorul să
                  primească mai puțini bani, Moromete trebuie să joace tot acest
                  spectacol. Însă nu acesta este singurul scop, personajul având
                  o plăcere pentru a fraierii oamenii.
                </p>
                <p>
                  După plecarea lui Jupuitu, Moromete este cuprins de o „ciudată
                  voioșie” și-i mărturisește lui Bălosu „l-am păcălit cu două
                  sute de lei, i-am dat numai o mie." Băloșu se uită la el cu o
                  privire rece și buimacă. Nu înțelegea.” Personajul reușește
                  astfel prin talentul disimulării să iasă din încurcătură,
                  depășind problemele cu mai puțină ușurință. Moromete se arată
                  până acum precum un personaj inteligent, diferit, subtil, ce
                  consideră că problemele din familia lui sunt la fel de
                  superficiale că toate problemele din universul rural. El este
                  ironic, foarte comunicativ, însă își crează un univers paralel
                  deoarece se simte incompatibil cu universul existent. Are însă
                  loc o trensformare a lui Moromete, transformare ce este
                  avertizată în primul rând de tăierea salcâmului, și vizibilă
                  în momentul când Scămosul îi zice ce vor să facă fii lui.
                </p>
                <p>
                  Acum, el nu mai știe ce să facă, deși până în momentul de față
                  tot timpul știa cum să iasă din încurcătură. Este un bun
                  cunoscător al psihologiei umane, având o anumită intuiție, și
                  citind gândurile personajelor.
                </p>
                <p>
                  Îi spune lui Scămosul „Acuma de ce-oi fi crezând tu că nu se
                  întoarce Achim cu oile de la București! Crezi că te mint?”,
                  deși Scămosul nu pomenise nimic de acest lucru, însă Moromete
                  îi citește gândurile. Când aude adevărul, prima sa reacție
                  este aceea de a respinge ideea, iar apoi înțepenește
                  văzându-se pe chipul său panică. Ia această veste precum o
                  lovitură ce îl obilga să se trezească la realitate. Până acum,
                  personajul reușea să își ascunde trăirile interioare dar acum
                  nu mai poate, acesta fiind un prim semn al schimbării sale.
                </p>
                <p>
                  În momentul următor, conștientizează, încearcă să aducă
                  argumente și să recupereze ce a pierdut. Își dă seama că a
                  fost orb că nu a văzut toate semnele; când înțelege, se
                  schimbă și fizic, nu numai în interior.
                </p>
                <p>
                  Apoi urmează etepe în care se dezvinovățește, gândind că
                  motivul plecării fiilor săi nu este în nici un caz el. Ironia
                  folosită „ Băieții mei, Scămosule, sunt bolnavi..Să fugă de
                  acasă! De ce asta? Nu i-am lăsat eu să facă ce vor? Absolută,
                  absolută libertate le-am lăsat! Parcă dacă veneau și-mi
                  spuneau : „Mă, noi vrem să fugim de acasă”, crezi că i-aș fi
                  împiedicat eu de, Scămosule!? „De ce să fugiți frățioare?
                  le-aș fi spus. Încet nu puteți să mergeți?”, vrea să arate că
                  îi lăsă să facă ce vor.
                </p>
                <p>
                  {" "}
                  Este o ironie cu umor negru ce duce spre sarcasm și lăsă un
                  gust amar.
                </p>
                <p>
                  Personajul nu mai simte plăcerea de a păcăli deoarece se simte
                  el păcălit acum. Când ajunge acasă, și băieții vor să ia haine
                  curate din ladă, Moromete este la început calm față de
                  aceștia. Și are glasul calm și față de față sa și soția, cu
                  toate că le bate. Mai face o ultimă încercare de a menține
                  familia unită, rugându-se dă băieți să îl ajute, cu un glas
                  blând. Însă Mormmete nu mai are aceeași forță interioară, și
                  pierde controlul, bătându-i pe băieți ca o pedeapsă. Își dă
                  seama că oricum nu mai are cum să îi țină în familie.
                </p>
                <p>
                  {" "}
                  Verbele ce îl caracterizează cel mai bine pe Ilie Moromete
                  sunt „a face”, datorită faptului că băieții îi cer tot timpul
                  să acționeze și „a gândi” deoarece el preferă să gândească, să
                  reflecte, nu să facă. Însă după această trăire, schimbarea lui
                  Moromete este radicală. Schimbările profunde produse asupra
                  lui Moromete aveau să se extindă în curând asupra întregului
                  sat, „trei ani mai târziu izbucnea cel de-al Doilea Război
                  Mondial. Timpul nu mai avea răbdare”. Personajul principal al
                  romanului „Moromeții” de Marin Preda, Ilie Moromete este „un
                  contemplativ inteligent, temperat, un filosif, iubind liniștea
                  (fără de care nu se poate trăi și nu se poate face nimic
                  durabil) și mai ales iubind libertatea, independenta de
                  gândire și exprimare a opiniilor”. (Ion Rotaru)
                </p>
                <p>
                  <strong>Niculae Moromete: </strong>Personaj principal,
                  dinamic, multidimensional, exponential.
                </p>
                <p>
                  <strong>Modalități de caracterizare: </strong>
                </p>
                <p>
                  <strong>Caracterizare directă: </strong>portretul făcut de
                  narator și de alte personaje; autocaracterizarea;
                </p>
                <p>
                  <strong>Caracterizare indirectă: </strong>prin propriile
                  acțiuni, simțiri și gânduri; prin mediul în care trăiește;
                  prin comentariul naratorial; prin limbaj.
                </p>
                <p>
                  Din perspectiva volumului al doilea al romanului, precum și
                  din mărturisirile prozatorului, Niculae Moromete este unul din
                  mesagerii cei mai fideli între lumea operei și personalitatea
                  autorului. Fără îndoială, spre Niculae se îndreaptă o mare
                  afecțiune a autorului, încât, din perspectiva volumului al
                  doilea al Moromeților, precum și din mărturisirile
                  prozatorului, îl putem considera unul din mesagerii cei mai
                  fideli între lumea operei și personalitatea autorului. În
                  legătură cu el, fraza, chiar și atunci când exprimă o
                  realitate dură, se încarcă de o abia perceptibilă notă de
                  participare afectivă. Un portret al lui Niculae, în culori
                  reci și linii severe, apăsate, are menirea să sublinieze
                  contrastul dintre sărăcia lucie și resursele complexe ale
                  premiantului care, chiar în ziua serbării, purta pe cap
                  pălăria tatălui: ,Așa cum se oprise, cu obrajii negri-galbeni
                  de friguri, cu capul mare, peste care pusese pălăria destul de
                  veche a tatălui, în cămașă și cu picioarele desculțe și pline
                  de zgârieturi, Niculae parcă era o sperietoare". În altă parte
                  se ivesc aceleași sumbre amănunte: ,Pe poarta grădinii intră
                  un băiat de vreo doisprezece ani. Avea capul gol și cămașa de
                  pe el ferfelița. Picioarele goale erau pline de zgârieturi
                  vechi cu urme de sânge închegat cu praf".
                </p>
                <p>
                  Dorința lui arzătoare este să fie dat mai departe la școală.
                  Tatăl nu o prea înțelege, după cum nu o înțeleg nici frații
                  sau surorile lui. Ar fi fost o cheltuială în plus pe capul
                  familiei. Copilul cunoaște de mic viața aspră a satului, e
                  trimis cu oile, iar Bisisica, oaia, îi face zile amare.
                  Băieții îl terorizează („Vrând-nevrând, învățase să se lupte
                  și să se bată cu ciomagul"). Ascultă încântat cum povestește
                  tatăl și, la un moment dat, are un gest tipic moromețian:
                  lucrau la încropirea unei porți iar el îi întinde lui
                  Paraschiv, care nu era atent, cuie cu partea ascuțită înainte.
                  Nu își bătuse joc tatăl de atâtea ori de prostia lui
                  Paraschiv, Nilă sau Achim? Mamă, Catrina, îi înțelege dorința
                  de a merge mai departe la școală, tatăl e mai reticent
                  (această împovără familia, îi încurca socotelile), până când,
                  în final, după fuga celor trei frați vitregi, se va hotărî
                  să-i plătească școlarizarea, îl admiră pe Ilie Moromete și
                  pentru că îi îngăduie să persevereze în rugămintea lui: „Tatăl
                  său era un om care gândea și gândirea lui era limpede, n-avea
                  nevoie să se înghesuie în ea. Nu cu rugăminți putea să-l facă
                  să-l dea la școală, ci cu argumente".Și le va găsi.
                  Impresionează prin maturitatea gândirii și fermitatea unei
                  timpurii opțiuni: între pământ și carte, renunțând la pământ
                  în favoarea surorilor. „Fuga" lui de acasă se realizează prin
                  acest intermediu, pentru că drumul spre lumea lui nu poate fi
                  stăpânit prin contemplare numai, ci și prin
                  acțiune.Înstrăinarea de tatăl lui are mai multe explicații,
                  dintre care cea mai evidentă ar fi ca ,Moromete îi spuse că
                  s-a terminat cu istoria lui cu studiile, să stea acasă și să
                  pună mâna pe sapă'.
                </p>
                <p>
                  Ilinca îi povestește lui Niculae ca tatăl regretă că nu l-a
                  dat la școală, ca „să nu umble el pe urmă de gât cu toți ăștia
                  ca de-alde Isoșica. Vezi, zicea, aici am greșit". Înstrăinarea
                  evoluează spre o confruntare și nu puține sunt episoadele în
                  care se prezintă înfruntarea dintre două concepții de viață:
                  cea a țăranului care vede în rostul lui temelia existenței și
                  nu concepe viața în afara sistemului tradițional de valori; pe
                  de altă parte, o concepție nouă, hrănită și din multă iluzie,
                  despre construcția noii societăți. Devenind activist, sub
                  influența noului notar, Niculae crede cu fermitate că „omul
                  are nevoie de o nouă religie a binelui și răului" și declară
                  că va fi, primul apostol" al noii religii, dacă va fi convins.
                  După o perioadă de activitate agitată prin comunele din
                  apropiere, se întoarce în sat având sarcina să supravegheze
                  campania agricolă de vară. Descoperă o realitate
                  deconcertantă, în care evoluează, mânați de diferite interese,
                  Isosică, Flotoagă, Bilă, Mantăroșie, Gae, Zdroncan. Când au
                  loc tulburările de la arie, se dovedește un spirit lucid,
                  operativ.
                </p>
                <p>
                  Ceea ce nu poate schimba el este mentalitatea oamenilor. Va fi
                  victima acestei mentalități, a violenței pe care o generează.
                  Un oarecare Gheorghe, hăituit de cei care strângeau cotele, se
                  aruncă în rău și se îneacă, iar moartea lui va constitui
                  motivul să fie dat afară din Comitetul raional de partid și
                  chiar să fie exclus. În ultimă instanță, însă, vechiul prieten
                  - noul notar -, după ce-i explică situația în care se află
                  partidul, îl sfătuiește să-și urmeze studiile până vor veni
                  vremuri mai bune. Marele singuratic continuă povestea lui
                  Niculae după ce el, într-adevăr, s-a făcut inginer
                  horticultor. Volumul II al Moromeților este cartea unei lumi
                  care se stinge pentru a apărea o alta, nouă, ai cărei profeți
                  nu sunt întotdeauna de bună-credință. În vremurile încurcate
                  de atunci, Niculae gândește clar, animat de o mare încredere
                  în ceea ce vine.
                </p>
                <p>
                  Prins în țesătură foarte ciudată de interese colective și
                  individuale, de deformări, Niculae pare un erou
                  camilpetrescian, preocupat să înțeleagă ceea ce se întâmplă.
                  Istoria nu-l iartă nici pe el. Spiritul independent al tatălui
                  s-a strecurat pe nesimțite în conduita fiului. Pentru cei
                  specializați în demascări, nu era o notă tocmai fericită să
                  gândești cu capul propriu. Romanul se încheie cu pagini amare
                  despre moartea tatălui, în urma unei lungi decrepitudini
                  fizice, revenindu-se la raporturile dintre tată și fiu.
                  Niculae are remușcări că nu l-a căutat în ultimii ani de viață
                  și este obsedat că, în imaginație, în lumina de sub pleoape,
                  „nu vrea să se uite la el". Credința lui Niculae că „binele
                  n-a dispărut din omenire" domină singurătatea acestui personaj
                  complex, a cărui problematică o va relua Mărin Preda în Marele
                  singuratic.
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
