import React, { Component } from "react";
import { Layout } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class PatulLuiProcust extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Patul lui Procust</h1>
              <h4>Camil Petrescu</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section">
                <span className="title">Definiția romanului:</span>
                <p>
                  Specie a genului epic, în proza de mare întindere având o
                  acțiune complexă desfășurată pe mai multe planuri narative și
                  cu un număr mare de personaje.
                </p>
              </div>
              <div class="section">
                <span className="title">Trăsături:</span>
                <p>
                  Opera lui Petrescu este că specie roman psihologic deoarece
                  nararea se face la persoana a I-a, exista conflicte interioare
                  puternice iar particularea izbitoare este structura
                  compozițională care conține un număr de scrisori, confesiuni
                  și documente.
                </p>
              </div>
              <div class="section">
                <span className="title">Apariția:</span>
                <p>
                  Romanul a apărut în anul 1933, la trei ani după "Ultima noapte
                  de dragoste, întâia noapte de război". Romanul a avut un mare
                  succes la public, impunând un prozator de primă mărime analist
                  excepțional și creator al unei formule narative deosebite.
                </p>
              </div>
              <div class="section">
                <span className="title">Titlul:</span>
                <p>
                  Titlul cărții amintește de celebrul tâlhar "Procust" din
                  Atica, în patul căruia erau scurtați sau lungiți călătorii.
                </p>

                <p>
                  <strong>I.</strong>In sens restrâns, interpretând simbolul din
                  titlu drept spațiu al nepotrivirii, se poate considera că
                  scriitorul face o analiză de mare subtilitate psihologică a
                  mutilărilor pe care sentimentul neîmpărtășit al iubirii le
                  poate provoca sufletului omenesc.
                </p>

                <p>
                  Ladima trebuie să-și amputeze spiritul și să cunoască
                  suferința, umilința, deznădejdea. Doamna T, trăiește dezgustul
                  și suferința neînțelegerii unui refuz. Fred Vasilescu își
                  reprima cu brutaliltate o mare pasiune din cauza unei
                  inexplicabile stări de inferioritate pe care i-o dă Doamna T.
                </p>
                <p>
                  Emilia nu pare atinsă de nimic, deoarece ea nu trăiește
                  sentimentul, ci îl mimează. Din perspectiva acestei
                  interpretări, romanul este unul de dragoste și încă din cele
                  mai profunde din întreaga literatură.
                </p>

                <p>
                  <strong>II.</strong>Dar cartea se pretează și la alte
                  interpretări. Dacă considerăm patul lui Procust ca spațiu pe
                  care îl ocupă drama lui Ladima se poate vorbi de: un roman al
                  condiției intelectualului într-o societate nedreaptă care
                  încearcă să-i îngrădească aspirațiile, să niveleze talentul și
                  inteligența, promovând mediocritatea și compromisurile de
                  orice fel.
                </p>

                <p>
                  <strong>III.</strong>Și totuși sensul cel mai cuprinzător al
                  simbolului trebuie căutat în poezia lui Ladima intitulată
                  "Patul lui Procust" publicată în subsolul paginilor și în care
                  se relevă caracterul contradictoriu al esenței umane,
                  antinomia ei structurală.
                </p>
              </div>
              <div class="section">
                <span className="title">Temele romanului:</span>
                <p>
                  Iubirea și contidia geniului într-o societate care nu îl
                  înțelege și îi îngrădește aspirațiile.
                </p>
              </div>
              <div class="section">
                <span className="title">
                  Perspectiva narativă și viziunea despre lume a autorului:
                </span>
                <p>
                  În acest roman nararea se face la persoana a I-a, naratorul
                  fiind în același timp și personaj.
                </p>
                <p>
                  În roman avem pe rând mai mult naratori, care relatează
                  întâmplări din punctul lor de vedere, așa cum simt și observă
                  lucrurile, deci subiectiv. În cazul narării la persoană I,
                  narătorul de obicei diferă de personaj.
                </p>
              </div>

              <div class="section">
                <span className="title">Conflictele:</span>
                <p>
                  Fiind un roman de analiză psihologică, subiectivă, nu există
                  conflicte puternice exterioare, ci majoritatea lor sunt
                  conflicte interioare (Fred, Doamna T., Ladima)
                </p>
              </div>

              <div class="section">
                <span className="title">Relațiile spațio-temporale:</span>
                <p>
                  Acțiunea se petrece în decursul câtorva ore într-o după-amiază
                  caniculară de august în apartamentul Emiliei. Acțiunea
                  osciliază între planul prezentului și cel al trecutului în
                  funcție de reactile și memoria lui Fred.
                </p>
              </div>

              <div class="section">
                <span className="title">Povestirea:</span>
                <p>
                  Incipitul este realizat printr-o adresare directă, constând în
                  mustrările pe care doamna T. le face autorului și pe care
                  acesta le explică în subsolul primei pagini.
                </p>
                <p>
                  Romanul începe cu cele trei scrisori ale doamnei T. și cu
                  notele explicative - din subsolul paginilor - ale autorului,
                  care o îndeamnă să facă publică experiența nefericită a
                  sentimentului de iubire neîmplinită, cu scopul de a crea "un
                  dosar de existence".
                </p>
                <p>
                  Ea refuză și scriitorul o convinge să se confeseze în scris,
                  recomandându-i un stil precis și concis, "Fără ortografie,
                  fără compozifie, fără stil și chiarfară caligrafie", concepție
                  ce susține, de altfel, anticalofaismul lui Camil Petrescu.
                </p>
                <p>
                  Scrisorile doamnei T. narează suferința și dezamăgirea produse
                  de faptul că amantul ei, X, nu o mai iubește și nu-i dă nici o
                  explicate privind această atitudine, deși ea suferise "din
                  cauza lui aproape mortal" și se întreabă cum poate el s-o
                  prefere pe alta, să aibă metresa și să trăiască "o viață
                  completă fără mine", ori ce-i pot oferi lui celelalte femei și
                  ea nu.
                </p>
                <p>
                  Suferința doamnei T se amplifică din cauza umilirii, a
                  mândriei ranite care "îmi macina corpul", mai ales că îl
                  întâlnește în tren pe fostul iubit care se ducea cu amantă la
                  munte, așa cum altă dată mergeau împreună ei doi.
                </p>
                <p>
                  Camil Petrescu apelează la formula romanului epistolar pentru
                  că impresia de autenticitate, ce se desprinde din scrisori, să
                  se constitute într-o confesiune cuprinzătoare a adevărului, ca
                  experiența trăită, ca valoare esențială a vieții
                  (substanțialitatea), așa cum a fost percepută de către fiecare
                  personaj în parte, autorul realizând o creație literară după
                  formula romanului în roman.
                </p>
                <p>
                  Jurnalul lui Fred Vasilescu este cea mai întinsă și complexă
                  parte a romanului și poartă titlul "într-o după-amiază de
                  august", cuprinzând confesiunea tânărului privind iubirea
                  pentru doamna T., scrisorile de dragoste ale lui George
                  Demetru Ladima către Emilia, precizările lămuritoare ale
                  acesteia și notele de subsol ale autorului. În acest capitol,
                  Camil Petrescu ilustrează și o imagine complexă a vieții
                  economice, politice și financiare din România, între anii 1926
                  - 1928.
                </p>
                <p>
                  La rugămintea autorului, Fred Vasilescu consemnează în
                  jurnalul său iubirea pentru doamna T. și scrisorile lui George
                  Demetru Ladima, din ambele confesiuni reieșind două puncte de
                  vedere diferite asupra aceleiași pasiuni, adică "două romane
                  subiective despre aceeași poveste de dragoste", la care se
                  adaugă și "comentariile autorului, care reușește să creeze
                  niște veritabile dosare de existență" (Gheorghe Glodeanu
                  -"Poetica romanului românesc interbelic"), toate acestea
                  concretizând nouă formulă estetică a relativismului.
                </p>
                <p>
                  În primele note de subsol, prozatorul îl prezintă pe Fred
                  Vasilescu, "acel enigmatic X., care era fiul lui Tănase
                  Vasilescu, personaj întâlnit și în romanul precedent, "Ultima
                  noapte de dragoste, întâia noapte de război".
                </p>
                <p>
                  Tânărul era chinuit de o poveste de iubire, despre care
                  autorul afiase întâmplător și-l roagă să scrie un jurnal în
                  care să se confeseze asupra tuturor stărilor și sentimentelor
                  trăite. Ca timp, întâmplările relatate de jurnalul lui Fred
                  Vasilescu încep într-o după-amiază călduroasă de august, iar
                  spațiul real este patul Emiliei Răchitaru, o semiprostituată
                  cu pretenții de artistă, căreia tânărul îi face o vizită.
                </p>
                <p>
                  Fred are 25 de ani, este spirit modern, cu o educație aleasă,
                  diplomat și aviator, fiul unui foarte bogat om de afaceri,
                  Tănase Vasilescu Lumânăraru. Încercând să-l impresioneze,
                  Emilia îi povestește despre unul, Ladima, "care m-a iubit
                  îngrozitor, săracul" și care murise acum trei luni, dându-i să
                  citească scrisorile de dragoste pe care acesta i le trimisese
                  în ultimii doi ani.
                </p>
                <p>
                  Fred Vasilescu citește pe rând și în ordine fiecare scrisoare
                  din teancul legat cu fundă roz. Ladima fusese gazetar și poet
                  talentat, iar Fred este uimit cum un astfel de om nu se
                  gândise că se compromite scriindu-i unei astfel de femei, o
                  cocotă de lux pe care o vizitau și alți bărbați.
                </p>
                <p>
                  Scrisorile lui Ladima creionează un îndrăgostit sensibil și
                  sentimental, preocupat ca iubita lui să obțină un rol într-o
                  piesă, apoi fericit că Emilia debutase, în sfiirșit, într-un
                  spectacol la teatrul Național, fiind convins de talentul
                  actoricesc al femeii.
                </p>
                <p>
                  Emilia îi relatează lui Fred despre relațiile ei cu alți
                  bărbați -printre care deputatul Nae Gheorghidiu- și
                  strădaniile ei de a evita orice întâlnire a acestora cu
                  Ladima.
                </p>
                <p>
                  Cu toate acestea, el aflase întâmpiator că Emilia avea un
                  amant, pe Traian Justiniu și-i scrie, cu o tristețe
                  sfâșietoare, rupând orice legătură cu ea: "S-a făcut în mine
                  ca o lumină de moarte și înțeleg acum, neașteptat, toate
                  întâmpiarile și toate faptele omenești ale acestui an de
                  delir, de când te cunosc...".
                </p>
                <p>
                  Aflând că Ladima este foarte rău bolnav, "merge spre
                  moarte...", Emilia nu se putuse duce la el, deoarece însoțea,
                  ca să-l distreze, un alt bărbat, dar Valeria îl găsise zăcând
                  într-o mizerie cruntă, neras și foarte slăbit. Vestea
                  căsătoriei Emiliei cu Arghiropol îl aruncă pe Ladima într-o
                  disper are fără margini, dar când logodna se anulează, el îi
                  trimite o scrisoare tulburătoare în care descrie chinurile
                  îngrozitoare care-l mistuiau și "o suferință care întrece
                  puterea de rezistență a nervilor mei", rugând-o să-l
                  reprimească.
                </p>
                <p>
                  De atunci se întâlneau din nou în fiecare seară, ieșeau
                  împreună, dar nu a fost niciodată a lui pentru că Ladima
                  "altminteri era idealist. [...] Îi piacea, așa, să stea aci",
                  spune Emilia "tot senină, mare și goală". Dintr-o altă
                  scrisoare, adresată "Emy scumpă", reiese mândria lui Ladimă
                  pentru "logodnica" lui, așa cum o prezintă pe Emilia celor mai
                  buni prieteni ai săi, Bulgaran, Cibănoiu și Penciulescu,
                  invitându-i pe toți într-o seară la masă, dar Emilia pleacă
                  devreme pentru că se plictisise îngrozitor, deși unul era mare
                  savant, altul teozof și ceiaialt un om de mare cultură.
                </p>
                <p>
                  Dintr-o altă perspectivă narativă, Fred rememorează toate
                  întâmpiarile legate de Ladima, în care se implicase în mod
                  direct și își amintește faptul că îl cunoscuse în iulie 1926,
                  într-o împrejurare foarte personală, deoarece el venise la
                  Techirghiol ca să se afle - neobservat și nebănuit de nimeni -
                  în apropierea doamnei T, pe care o iubea în taină.
                </p>
                <p>
                  Ladima este prima persoană căreia Fred îi destăinuiește
                  chinurile prin care trece iubind-o atât de total pe doamna T.,
                  simțind o pornire inexplicabilă pentru confesiune: "Era
                  singurul pe lume căruia i-aș fi încredințat taina, pe care n-o
                  știu nici părinții mei, faptul cumplit care e cancerul vieții
                  mele, care mă face să fug de-o femeie iubită".
                </p>
                <p>
                  Fred îl recomandă pe Demetru Ladima tatălui său, Tănase
                  Vasilescu, pentru a-l lua director la "Veacul", ziar înființat
                  împreună cu Nae Gheorghidiu, pentru a stăvili o campanie de
                  presă pornită împotriva deputatului, prin care se dezvăluiau
                  afacerile necinstite privind fabrica de muniții din Ardeal și
                  recepția unor avioane, din care cauza fusese remaniat din
                  funcția de ministru.
                </p>
                <p>
                  Tânărul director s-a ocupat cu pasiune și înfrigurare de
                  organizarea gazetei, pe care o scria aproape singur și timp de
                  două săptămâni, "Veacul" a deziântuit o campanie furibundă,
                  "aproape sălbatică" împotriva celui care-l atacase pe
                  Gheorghidiu pentru, "afaceri veroase", iăcându-l să apară în
                  fața opiniei publice "un imbecil ridicol".
                </p>
                <p>
                  Atacurile virulente ale noii gazete, "Veacul", se îndreptau
                  acum împotriva întregii clase politice, împotriva conducerii
                  tineretului liberal, astfel că Nae Gheorghidiu ajunsese să fie
                  "temut și ascultat", iar gazeta "semăna teroare, mai ales
                  [...] în partidul liberal". Ziarul are și un succes financiar,
                  atingând în două luni tirajul "excepțional de 14 mii de
                  exemplare", izbânda fiind posibilă și pentru că Ladima era
                  "absolut invulnerabil — și se făcuseră investigații la
                  tribunalul din orașul iui de naștere, detectivii îi urmăriseră
                  tot firul trecutului, îi scrutaseră viața" dar, negăsind nimic
                  de care să se agate, nu se putea riposta în nici un fel
                  campaniilor lui de presă, care deveniseră extrem de aprige.
                </p>
                <p>
                  Notele de subsol ale autorului sunt o altă perspective
                  narativă și se constituie într-o satiră vehementă privind
                  statutul omului în societatea vremii, unde toate șansele sunt
                  de partea afaceriștilor veroși, pe când cei cinstiți, ca
                  Ladima, trăiesc mizerabil și în umbra celorlalți.
                </p>
                <p>
                  Astfel că talentul, cinstea și demnitatea gazetarului Ladima
                  nu pot învinge păienjenișul afacerilor frauduloase ale lui
                  Gheorghidiu și Lumânăraru. El descoperă întâmplător neregulile
                  dintr-o afacere a industriei miniere și C.F.R., care păgubea
                  statul român cu un miliard de lei anual, iară să bănuiască
                  implicarea totală a patronilor săi în această fraudă.
                </p>
                <p>
                  Ceea ce a dus la ruperea definitivă a acestei colaborări, a
                  fost apariția unui articol de o violență deosebită, prin care
                  Ladima acuză puterea politică de matrapazlăcuri în domeniul
                  financiar-bancar.
                </p>
                <p>
                  În notele de subsol, autorul relatează antipatia profundă pe
                  care Ladima o avea pentru economiști, pe care-i numește
                  "corbii matematici ai mizeriei" ori "improvizați medici
                  financiari", considerându-i vinovați direct de criza
                  financiară și economică în care se află țara, demonstrând că
                  nimeni dintre cei bogați nu-și plătește impozitele, ci numai
                  cei săraci și funcționarii le achită.
                </p>
                <p>
                  Citind scriosarea în care Ladima o anunță pe Emilia că își dă
                  definitiv demisia de la gazetă, pentru că nu poate scrie ceea
                  ce i se comandă, ci numai ceea ce gândește el
                  (autenticitatea), mai ales că este și prost plătit, Fred își
                  amintește că îl rugase pe Ladima să renunțe la demisie, dar
                  acesta refuzase categoric: "Nu sunt în stare să scriu două
                  rânduri care să nu vie dintr-o convingere adâncă".
                </p>
                <p>
                  Privind la "această Emilie cu carnea de gutuie moale, la
                  povestea aceasta pe care puteam să n-o cunosc", Fred Vasilescu
                  este uluit de imaginea pe care o are îndrăgostitul, cum vede
                  el în femeia iubită perfecțiunea fizică și integritatea
                  morală.
                </p>
                <p>
                  Plecat de la gazetă, Ladima o duce din ce în ce mai greu,
                  încearcă să publice în gazete poezii și foiletoane, nu mai
                  putea ieși nicăieri, iar când își dă seama că Emilia are
                  relații cu un grec, Micropolu, îi scrie pentru ultima dată și,
                  plin de amărăciune, îi cere înapoi scrisorile pe care îi le
                  trimisese.
                </p>
                <p>
                  Ea refuză, socotind că nu este un om bine crescut și-l minte
                  că le-a pierdut. Peste două săptămâni Ladima s-a sinucis și
                  Emilia spune că a recurs la acest gest "din mizerie", pentru
                  că "nu mai mânca poate nici o dată pe zi".
                </p>
                <p>
                  Fred Vasilescu își dă seama că scrisorile nu trebuie să mai
                  rămână "în mâna Emiliei", întrucât acestea puteau compromite
                  amintirea lui Ladima, statutul său de poet talentat și de
                  gazetar intransigent, așa că se hotărăște să le fure.
                </p>
                <p>
                  <strong>Epilogul I</strong> relateaza infrigurarea cu care
                  Fred Vasilesci cerceteaza, chiar de a doua zi de dupa vizita
                  la Emilia, imprejurarile sinuciderii lui George Demetru
                  Ladima. Necrologurile erau surprinzator de elogioase, Ladima
                  fiind considerat "un stralucit talent", "o mare pierdere
                  pentru literatura romaneasca", dar unul singur il
                  impresioneaza profund "Unul dintre cei mai mari poeti ai
                  timpului... hranit cu otet, fiere si dezgust de contemporani,
                  a dus tacut teava rece pe inima calda si stupida si a
                  domolit-o, sfaramand-o. Noapte buna, poet smintit si
                  cumsecade" Desi pare absurd ca un om ca Ladima sa se sinucida
                  din cauza "unei femei atat de vulgare ca Emilia", Fred stie ca
                  acest soi de cocote provoaca adevarate drame de amor,
                  sinucideri sau crime, pe cand cele superioare, inteligente si
                  distinse provoaca suferinte mult mai adanci, dar pe un alt
                  plan spiritual: "Toate dramele din toti anii, de cand sunt
                  atent la lume, sunt din mahalaua sufleteasca".
                </p>
                <p>
                  Ladima isi trasese un glont in inima, inabusind zgomotul cu o
                  patura, iar in buzunarul hainei s-a gasit o mie de lei si o
                  scrisoare adresata unei doamne, Maria Manescu, in care Ladima
                  isi destainuie profunda suferinta provocata de "iubirea
                  aceasta eterna si otravitoare".
                </p>
                <p>
                  De aceea procurorul respinge cauza sinuciderii ca fiind
                  mizeria în care ar fi trăit poetul și considera că el s-a
                  împușcat din pricina acelei femei, căreia îi lăsase
                  scrisoarea.
                </p>
                <p>
                  Fred își dă seama că orgoliul lui Ladima a determinat această
                  punere în scenă a împrejurărilor. Fire hipersensibilă, Ladima
                  se simțea rușinat de iubirea înjositoare pentru vulgară Emilia
                  și împrumutase cei 1.000 de lei pe care-i pusese în buzunar,
                  astfel că demnitatea lui de om să rămână neștirbită, fiind
                  unica lui mândrie în viață, singurul ideal: "nu trebuie să se
                  știe că am iubit asemenea femeie și că în viață am suferit de
                  foame. [...] Am trăit patruzeci de ani, inutili".
                </p>
                <p>
                  <strong>Epilogul II</strong>, subintitulat "Povestit de autor"
                  naratorul relatează la persoană I despre ciudatul accident de
                  avion suferit de Fred Vasilescu, a doua zi după ce îi predase
                  caietele. Finalul romanului cuprinde discuția doamnei T cu
                  naratorul, din care reiese că ea fusese tot timpul frământată
                  de incertitudinea iubirii, "uneori și mie mi se părea că mă
                  iubește...[...] o iubire pătimașă ascunsă" și mărturisirea ei
                  că l-a iubit pe Fred cu pasiune, cu voluptate și nu a înțeles
                  niciodată de ce el întrerupsese această relație.
                </p>
                <p>
                  Naratorul renunță definitiv la aflarea unor eventuale
                  răspunsuri lămuritoare privind iubirea ciudată dintre cei doi,
                  deoarece adevărurile nu pot fi limitate: "Taina lui Fred
                  Vasilescu merge poate în cea universaia, fără nici un moment
                  de sprijin adevărat, așa cum singur a spus-o parcă, un afluent
                  urmează legea fluviului".
                </p>
                <p>
                  Romanul "Patul lui Procust" este construit pe axa fundamentală
                  a autenticității, Camil Petrescu ilustrând substanța vieții,
                  fie prin valori existențiale că iubirea și demnitatea, fie
                  prin prezentarea realităților sociale, culese din banalitatea
                  cotidiană, conturând un tablou convingător al lumii politice,
                  economice și financiare din România anilor 1926-1928.
                </p>

                <p>
                  Destinele celor doua personaje masculine - Fred si Ladima -
                  sunt, in conceptia lui Nicolae Manolescu, doua enigme care
                  stau fata in fata, una fiind imaginea rasturnata a celeilalte:
                  "daca Ladima isi pierde cu desavarsire capul pentru o femeie
                  vadit inferioara si se sinucide probabil din cauza ei, lacand
                  insa totul spre a masca acest lucru, Fred se dovedeste capabil
                  sa paraseasca o femeie care este superioara, desi nu e deloc
                  exclus sa se sinucida din cauza ei, mascand motivul la fel de
                  grijuliu ca si Ladima. [...] Dragostea (si cu atat mai mult
                  dragostea-pasiune) transforma pe cel ce iubeste in sclavul
                  celui iubit: iar Fred n-a vrut sa devina sclavul erotic al
                  doamnei T." Fred sacrifica iubirea din vanitate, iar Ladima
                  din demnitate.
                </p>

                <div class="section">
                  <span className="title">Caracterizarea personajelor:</span>
                  <p>
                    <strong>Doamna T.:</strong> Doamna T. este numele enigmatic
                    pe care Camil Petrescu îl dă eroinei Măria T. Mănescu,
                    cititorul făcând cunoștință cu acest detaliu din notele de
                    subsol ale autorului, care explică inițiala cu care este
                    semnată prima scrisoare, dintre cele patru, ce compun
                    începutul romanului: "Acest T. nu este o inițială cum s-ar
                    părea, căci a devenit un adevărat nume, și nu știu dacă n-ar
                    fi trebuit să fie scris Te".
                  </p>
                  <p>
                    O altă menționare a numelui întreg al eroinei este amintită
                    în trecere de Fred Vasilescu, atunci când își scrie
                    testamentul și, mai accentuat, în scrisoarea de adio pe care
                    George Demetru Ladima i-o adresează femeii înainte de
                    sinucidere.
                  </p>
                  <p>
                    Doamna T reprezintă tipul de feminitate modernă, mai întâi
                    pentru că se distinge printr-un farmec aparte, prin misterul
                    fascinant pe care-l emană, apoi pentru că ea întruchipează,
                    pentru prima oară într-o viziune romanescă, un spirit
                    feminin intelectualizat și, totodată, femeia independentă
                    din punct de vedere material, ea fiind proprietara unui
                    magazin de mobilă stil. Prima secvență narativă din roman
                    este reprezentată de cele trei scrisori ale eroinei,
                    numerotate cu cifre romane.
                  </p>
                  <p>
                    Autorul o rugase pe doamna T. să primească un rol într-o
                    piesă a lui, "să apară pe scenă", deoarece ea ar fi putut să
                    exprime convingător "cum se, gândește, cum se iubește, cum
                    se suferă", sentimente pe care nu le poți învăța "în orele
                    de curs", așa cum procedează actorii profesioniști și nici
                    "atestă prin certificat de absolvire".
                  </p>
                  <p>
                    Refuzul doamnei T. reliefează, indirect, discreția și
                    distincția acesteia: "-N-am nimic în mine de arătat, pe
                    scenă, lumii".
                  </p>
                  <p>
                    După discuții prelungite, din care reiese ideea că numai
                    scriitorul este acela care are capacitatea de a exprima, cu
                    sinceritate, "ceea ce a simțit, ceea ce a gândit, ceea ce i
                    s-a întâmplat în viață, lui și Celor pe care i-a cunoscut",
                    ea îi trimite trei scrisori în care-și dezvăluie
                    sentimentele, concepțiile despre iubire, despre relațiile cu
                    oamenii, despre viață, în general.
                  </p>
                  <p>
                    Tot în notele de subsol autorul îi face, în mod direct,
                    portret fizic detaliat: "Nu înalță și insulator de slabă,
                    palidă și cu un păr bogat de culoarea castanei [...]" era
                    poate prea personală ca să fie frumoasă în sensul obicinuit
                    al cuvântului.
                  </p>
                  <p>
                    Avea orbitel puțin neregulate, ușor apropiate, pronunțate,
                    cu ochii albaștri ca platina, lucind, fremătând de viață,
                    care, când se fixau asupra unui obiect îl creau parcă.
                    Bărbia feminină, delicată, dar prelungirea ei, întinsă
                    frumos până sub ureche, cam aparență, căci era lipsită de
                    orice grăsime.
                  </p>
                  <p>
                    Gura, foarte mobilă, vie ca o floare plină. Gâtul lung,
                    robust, cu tendoane lămurite la orice întoarcere a capului
                    [...] un soi de frumusețe, incertă, am spune «pe muche de
                    cuțit»".
                  </p>
                  <p>
                    Inteligenta, rasata si cu o profunda cultura, ea isi pune pe
                    picioare o afacere proprie, deschizand un magazin cochet si
                    discret de mobila stil, in care piesele rare, de valoare
                    autentica ii satisfac nevoia de frumos, de sensibilitate
                    pentru arta adevarata, fata de care are o atractie si o
                    pasiune de critic de arta.
                  </p>
                  <p>
                    Camil Petrescu creeaza un model de feminitate, de delicatete
                    si sensibilitate, calitatile spirituale si intelectuale ale
                    doamnei T. constituind un adevarat si inalt omagiu al ideii
                    de femeie ideala: "extrem de emotiva, alternand o
                    sprinteneala nervoasa, cu lungi taceri melancolice [...] De
                    o tulburatoare feminitate uneori, avea ades o voce scazuta,
                    seaca, dar alteori cu mangaieri de violoncel [...] o voce cu
                    inflexiuni sexuale, care dau unui barbat ameteli calde si
                    reci".
                  </p>
                  <p>
                    Relativismul, principiul estetic românesc pe care Camil
                    Petrescu l-a introdus în proza modernă, se reflectă și în
                    construirea doamnei T, a "cărei personalitate se răsfrânge
                    diferit în opinia celorlalte personaje.
                  </p>
                  <p>
                    Unul dintre prietenii lui Fred, mirat de fascinația pe care
                    doamna T. o exercită asupra tânărului monden, cu succese
                    legendare la femei -"N-oi fi pus gând rău urâței aceleia
                    care stă de vorbă cu Ladima? Șefule, te știam om de gust"-
                    stârnește în tânărul îndrăgostit contradicțiile personajului
                    hipersensibil, înclinat spre analize amănunțite, care-i
                    provoacă în conștiința percepții profunde ale unei
                    feminități tulburătoare, căreia "nu-i plăcea nicio
                    perversiiine, nicio «rafinerie» așa-zisă de femeie modernă
                    [...] mă măgulea gândul de a ști că e frumoasă numai cu mine
                    și mai ales numai prin mine".
                  </p>
                  <p>
                    Pentru Fred este femeia unică, iubirea vieții lui,
                    sentimentele fiind la aceleași înalte cote și din partea
                    doamnei T. Dragostea lor este devorantă, îi duce pe amândoi
                    la o uitare totală de sine, bărbatul întrebându-se uneori
                    dacă mai sunt "în toate mințile".
                  </p>
                  <p>
                    Iubirea lor tainică, ascunsă de privirile lumii "atât de
                    rea... neînțelegătoare", păstrând cu strășnicie trăirea
                    intensă numai pentru ei, conturează femeia enigmatică, care
                    atrage prin eternul mister feminin. Pentru P., doamna T.
                    este fascinantă, farmecul ei supunându-l total pe bietul
                    ratat, care o iubea necontenit de cincisprezece ani și
                    căruia, convins că este iubită "de toți bărbații", i se pare
                    imposibil ca cineva să rămână insensibil la frumusețea
                    distinsă, delicată și elegantă a femeii.
                  </p>
                  <p>
                    Doamna T. emana sensibilitate și mister, este înzestrată cu
                    o spiritualitate superioară, înclinată spre meditație, ca
                    urmare a preocupărilor ei pentru artă și literatură, având
                    "acea permanentă tensiune intelectuală", fiind adesea
                    "absența și gânditoare, înregistrând interior și lin cele
                    mai mici nuanțe ale clipei".
                  </p>
                  <p>
                    George Călinescu afirmă că nu există în viața reală decât
                    femei ca Emilia,"femeia de toate zilele" ce se poate
                    caracteriza, pe când "Doamna T. este fantoma romanului,
                    aspirațiunea lui Fred, obscură și enigmatică, tocmai prin
                    aceasta, și dacă autorul n-a știut să-i dea tonuri de ulei,
                    este pentru că n-a putut s-o scoată din mediul ei aerian".
                  </p>
                  <p>
                    <strong>Fred Vasilescu: </strong>
                    Fred Vasilescu este fiul lui Tănase Lumânăraru, industriașul
                    multimilionar asociat în diferite afaceri cu politicianul
                    liberal Nae Gheorghidiu (personaje conturate în romanul
                    "Ultima noapte de dragoste, întâia noapte de război") și
                    definit de Pompiliu Constantinescu ca fiind "un fel de
                    intersecție a tuturor celorlalte personaje". Ca personaj
                    realist-modern, Fred recompune - prin memorie afectivă -
                    viața celorlalte personaje, a Emiliei, a doamnei T.,
                    destinul lui Ladima, precum și imaginea socială, politică și
                    economică a României interbelice, prin prezentarea
                    manifestărilor politicianului Nae Gheorghidiu și a omului de
                    afaceri Tănase Vasilescu, zis Lumânăraru (porecla ce provine
                    de la prima sa afacere, o prăvălie de lumânări).
                  </p>
                  <p>
                    Întâmplările relatate în jurnalul lui Fred Vasilescu încep
                    într-o după-amiază călduroasă de august (perspectiva
                    temporală), iar spațiul real este patul Emiliei Răchitaru, o
                    semiprostituată cu pretenții de artistă, căreia tânărul îi
                    face o vizită. Fred are 25 de ani, este spirit modern, cu o
                    educație aleasă, diplomat și aviator, ceea ce-l motivează ca
                    personaj "rotund" ("care nu poate fi caracterizat succint și
                    exact", E.M.Forster).
                  </p>
                  <p>
                    Emilia încearcă să-l impresioneze povestindu-i despre unul,
                    Ladima, "care m-a iubit îngrozitor, săracul" și care murise
                    acum trei luni. Ea îi dă să citească scrisorile de dragoste
                    pe care ziaristul i le trimisese în ultimii doi ani. Fred
                    Vasilescu parcurge, pe rând și în ordine, fiecare scrisoare
                    din teancul legat cu fundă roz.
                  </p>
                  <p>
                    Autorul romanului insistă, în notele de subsol, ca Fred
                    să-și exprime în scris impresiile care 1-au marcat profund
                    în urma citirii scrisorilor de dragoste ale lui George
                    Demetru Ladima adresate Emiliei, precum și propriile
                    mărturii în legătură cu iubirea pentru doamna TV, acesta
                    intuind în tânărul monden "un soi de loialitate și
                    delicatețe, de sinceritate a vieții".
                  </p>
                  <p>
                    Portretul fizic este conturat în mod direct abia în epilogul
                    al II-lea din finalul romanului, cu prilejul morții
                    impresionante a pilotului, când nu mai rămăsese nimic din
                    "tânărul blond, cu obrazul limpede, cu trăsături regulate și
                    evidente ca un cap de statuie grecească, doar cu fruntea
                    puțin cam boltită deasupra ochilor verzi adânci", al cărui
                    "corp vânjos" avusese "mișcări mlădioase de haiduc tânăr,
                    afemeiat și gânditor".
                  </p>
                  <p>
                    Portretul moral se fundamentează, indirect, pe îmbinarea
                    subtilă dintre esență și aparență, dintre profunzimea
                    spirituală și o mediocritate afișată, fiul milionarului
                    părând să nu aibă alte preocupări decât sportul, moda,
                    chefurile și femeile, dar fund, în fondul său, un tânăr
                    sensibil, inteligent și serios.
                  </p>
                  <p>
                    Sub aparența unui tânăr superficial, pe care "banda" îl
                    consideră valoros pentru că era plin de bani, boem, snob și
                    prost, Fred este un intelectual lucid, introvertit,
                    inteligent, ale cărui sondări interioare dezvăluie "acea
                    vibrație melodioasă, calmă, pe care o au toți oamenii,
                    fruntași adevărați în activitatea lor, orișicare ar fi ea:
                    artă, politică, militărie, acrobație, dans modern, destin de
                    Don Juan sau box".
                  </p>
                  <p>
                    Cu o bogată experiență de viață, fost diplomat, pilot
                    ambițios, sportiv talentat, cu succese răsunătoare la femei,
                    Fred consideră că trebuie să trăiască în mod direct tot ceea
                    ce i-a comandat amorul propriu, experiența trăită
                    constituind un adevărat "memoriu de carieră": "Toate le-am
                    făcut în viață și tot ce mi-a stârnit amorul-propriu e
                    trecut în activitatea mea ca într-un memoriu de carieră".
                  </p>
                  <p>
                    El își ascunde adevărata natură spirituals, întrucât, în
                    esență, contrastează flagrant cu mediul înconjurător, cu
                    lumea superficială al cărei stil l-a adoptat, prefăcându-se
                    că aparține societății mondene, meschine și dominate de
                    parvenitism.
                  </p>
                  <p>
                    Încadrându-se perfect în tipul personajului camilpetrescian
                    și modern, Fred Vasilescu este intelectualul lucid,
                    autoanalitic, hipersensibil, asemenea lui Ladima și
                    Gheorghidiu.
                  </p>
                  <p>
                    Cu toate că este îndrăgostit total și definitiv de doamna
                    T., Fred o evită, fiind capabil să părăsească o femeie
                    care-i este superioară, probabil din cauza aceluiași orgoliu
                    ce individualizează personajele lui Camil Petrescu:
                    "Sacrifică definitiv pasiunea pe altarul vanitapi: în loc să
                    se piardă pe sine, preferă s-o piardă pe ea. [...] Fred n-a
                    vrut să devină sclavul erotic al doamnei T." (Nicolae
                    Manolescu, "Area lui Noe").Dragostea pentru doamna T. rămâne
                    o enigmă, iar moartea lui în accidentul de avion poate fi și
                    o sinucidere, motivul fiind ascuns cu grijă, ca și în cazul
                    lui Ladimă.
                  </p>
                  <p>
                    Ambele destine, al lui Ladima și al lui Fred, sunt dramatice
                    prin intensitatea iubirii, deși una reprezintă imaginea
                    răsturnată a celeilalte: Ladima trebuie să-și diminueze
                    capacitatea spirituală, să coboare la nivelul vulgaritalii
                    Emiliei, Fred, dimpotrivă, simte că este sub iubirea
                    superioard a doamnei T., temându-se că n-ar putea da atât de
                    multă dragoste câtă i se cere.
                  </p>
                  <p>
                    Legătura celor două destine este întâmplătoare, prin
                    scrisorile lui Ladima, pe care Fred le citește cutremurat,
                    înțelegând dincolo de cuvinte tragedia prietenului său, cu
                    care, de altminteri, comunicase printr-tin flux spiritual,
                    dincolo de rațiune: "Era singurul pe lume căruia i-aș fi
                    încredințat taina, pe care n-o știu nici părinții mei,
                    faptul cumplit care e cancerul vieții mele, care mă face să
                    fug de-o femeie iubită.
                  </p>
                  <p>
                    Posibilitatea de a povesti cuiva cât sufăr m-ar fi ușurat
                    poate că filtrarea sângelui". Reacția lui de a fura
                    scrisorile lui Ladima, care, chiar după moartea acestuia,
                    l-ar fi putut compromite, este determinată de simțul onoarei
                    de a păstra secretul rușinos al omului pe care-l cunoscuse
                    demn și incapabil de compromisuri, inflexibil.
                  </p>

                  <p>
                    Personalitatea lui Fred Vasilescu se menține enigmatică,
                    sfârșitul său pendulând între accident de avion și
                    sinucidere. El moare brusc, a doua zi după predarea
                    caietelor în care confesiunile lui sunt o permanentă
                    autoîntrospecție, ca și când scrisul l-ar fi ajutat să se
                    izbăvească de suferință prin analiza lucidă a "cancerului"
                    vieții lui, iubirea.
                  </p>

                  <p>
                    Taina iubirii lui reintră, prin moartea eroului, în
                    circuitul marilor taine ale Universului: "Taina lui Fred
                    Vasilescu merge poate în cea universală fără niciun moment
                    de sprijin adevărat, așa cum, singur a spus-o parcă, un
                    afluent urmează legea fluviului", fraza care încheie
                    romanul, că o concluzie ideatică sugestivă pentru concepția
                    estetică a lui Camil Petrescu.
                  </p>

                  <p>
                    Modernismul romanului e definit de subtilitatea și
                    profunzimea analitică a conștiințelor, de dramele interioare
                    suferite din iubire și demnitate, de identificarea deplinaa
                    timpului subiectiv cu eel obiectiv, de faptul că personajele
                    sunt în același timp și naratori.
                  </p>

                  <p>
                    Camil Petrescu, romancier prin excelență citadin modern,
                    realizează o literatură intelectuală, situându-se pe linia
                    ideii că actul scrisului este, în primul rând, "o experiență
                    spirituală, o formă de confesiune destinată să atingă
                    suprema sinceritate și să ducă la o revelație existențială"
                    (Ov.S.Crohmălniceanu - "Literatura română între cele două
                    războaie mondiale").
                  </p>
                </div>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
