import React, { Component } from "react";
import { Layout } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class Ion extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Ion</h1>
              <h4>Liviu Rebreanu</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section">
                <span className="title">Definitia romanului: </span>
                <p>
                  Specie a genului epic, in proza, de mare intindere, avand o
                  actiune complexa desfasurata pe mai multe planuri narative cu
                  un numar mare de personaje.
                </p>
                <div class="section">
                  <span className="title">Trasaturi: </span>
                  <p>
                    Opera este ca specie un roman deoarece este o structura
                    narativa de mare intindere,subiectul cuprinde doua planuri
                    narative paralele ale actiunii si doua nuclee.Actiunea este
                    ampla si implica un numar mare de personaje cu ponderi
                    diferite in ansamblul epic si diversificate
                    psihologic.Cunoaste o mare varietate de forme si formule
                    epice si tehnici narative.
                  </p>
                </div>
                <div class="section">
                  <span className="title">Aparitia: </span>
                  <p>
                    A aparut in anul 1920 si este considerat roman modern,
                    obiectiv si realist.
                  </p>
                </div>
                <div class="section">
                  <span className="title">Titlul: </span>
                  <p>Omonim cu personajul principal.</p>
                </div>
                <div class="section">
                  <span className="title">Tema: </span>
                  <p>
                    Este una ruarala,a pamantului,iar cea de a doua este una
                    universala,a iubirii adaptata la lumea satului.Ideea
                    romanului se desprinde din final,si anume ca "iubirea" este
                    cea care invinge in cele din urma glasul pamantului care
                    este patima,exces,legare de lucrurile materiale,sacrificiu
                    al umanului, diabolicul. Drama lui Ion este fireasca atata
                    timp cat a sacrificat pentru obtinerea averii iubiri (a lui
                    pentru Florica,a Anei pentru el)si destine (Ana care se
                    spanzura,Florica care ramane fara sot,a parintiilor care
                    ajunsi la batranete raman fara sprijin si a copilului care
                    moare).
                  </p>
                </div>

                <div class="section">
                  <span className="title">
                    Viziunea despre lume a autorului:{" "}
                  </span>
                  <p>
                    Pentru Rebreanu,literatura este creatie de oameni si
                    viata,iar romanul este discursul care fixeaza curgerea
                    vietii.Scriitorul opteaza pentru un realism al
                    esentelor,aceasta proiectie moderna permite reprezentarea
                    lumii cu o privire impartiala.
                  </p>
                </div>
                <div class="section">
                  <span className="title">Perspectiva narativa: </span>
                  <p>
                    Nararea se face la persoana a treia, naratorul fiind
                    omniscient,omnipotent si omniprezent.
                  </p>
                </div>
                <div class="section">
                  <span className="title">Compozitia: </span>
                  <p>
                    Romanul are 2 parti intitulate sugestiv:{" "}
                    <strong>Glasul Pamantului</strong> si{" "}
                    <strong>Glasul Iubirii</strong>,sugerand cele doua forte
                    antagonice din sufletul lui Ion. La fel de sugestive sunt si
                    titlurile capitolelor, care condenseaza intr-un singur
                    cuvant esenta narativa a capitolului.
                  </p>
                  <p>
                    <strong>Glasul Pamantului: </strong> Inceputul, Zvarcolirea,
                    Iubirea, Noaptea, Rusinea, Nunta.
                  </p>
                  <p>
                    <strong>Glasul Iubirii: </strong>Vasile, Copilul, Sarutare,
                    Streangul, Blestemul, George, Sfarsit.
                  </p>
                  <p>
                    In total sunt 13 capitole, cifra a nenorocului in consecinta
                    cu destinul lui Ion, care nu a dobandit fericirea pentru ca
                    a ascultat "glasul pamantului" si nu pe cel al "iubirii".
                  </p>
                  <p>
                    Romanul are o structura de sferoid. Se observa acest lucru
                    din titlurile primului si ultimului capitol, dar si din
                    imaginea simetrica a drumului Pripasului de la inceputul si
                    finalul romanului (la inceput drumul intra in sat vesel, ca
                    la sfarsit acelasi drum sa iasa obosit, vazand dramele
                    lasate in urma). Fiecare capitol este structurat in
                    episoade, secvente, numerotate riguros, totul bine cantarit
                    si echilibrat. Avem planuri paralele, simetri, gradari de
                    tensiuni bine armonizate. Structural are 3 planuri:
                  </p>
                  <p>
                    <strong> 1. </strong> Drama lui Ion.
                  </p>
                  <p>
                    <strong>2. </strong> Drama familiei Herdelea.
                  </p>
                  <p>
                    <strong>3. </strong> Monologul satului ardelean de la
                    inceputul secolului XX (obiceiuri, traditii, evenimente,
                    componenta sociala).
                  </p>
                </div>
                <div class="section">
                  <span className="title">Conflictele: </span>
                  <p>
                    Varietatea si complexitatea conflictelor reprezinta o
                    caracteristica fundamentala a romanului. Conflictul central
                    al romanului il reprezinta lupta pentru pamant in satul
                    traditional ardelenesc in mod special. Drama lui Ion, este
                    accea de a nu semnifica nimic pentru societatea in care
                    traieste din lipsa averii. Conflictul exterior, social se
                    manifesta intre Ion si Vasile Baciu. Insa adevarata
                    dimensiune a dramei personajului o reda conflictul interior
                    care se da in sufletul lui Ion care trebuie sa aleaga intre
                    pamant si dragostea pentru Florica. Conflicte secundare:
                    Ion-George, Ion-Vasile Baciu Ion-Simion, romani-maghiari,
                    Herdelea-preotul Belciug.
                  </p>
                </div>
                <div class="section">
                  <span className="title">Relatii spatiale si temporale: </span>
                  <p>
                    Timpul este destul de vag conturat si indica plasarea
                    actiunii pe durata cateorva ani de la inceputul secolului al
                    XX-lea. Din punct de vedere spatial, universul fictiunii se
                    plaseaza in imediata vecinatate a lumii reale din zona
                    Bistritei, satul Pripas, Armadia, Jidovita, Lechinta, Cluj
                    si Sibiu, curtea vaduvei lui Maxim Oprea, acasa la Vasile
                    Baciu, la Ion la George.
                  </p>
                </div>
                <div class="section">
                  <span className="title">Povestirea: </span>
                  <p>
                    Actiunea se desfasoara pe doua planuri paralele, al
                    taranilor si intelectualitatii rurale, care confera impresia
                    de monografie a lumii infatisate. Personaje apartinand celor
                    doua planuri sunt prezentate in scena simbolica de la
                    inceputul romanul o hora a soartei (Nicolae Manolescu).
                    Actiunea romanului incepe intr-o zi de duminica, care
                    locuitorii satului Pripas se afla la hora, in curtea
                    Todosiei, vaduva lui Maxim Oprea.
                  </p>
                  <p>
                    In centrul actiunii sta personajul Ion, taran sarac care
                    vrea sa aiba repede mult pamant. Hotararea lui Ion de a o
                    lua la joc pe Ana cea bogata, desi o place pe Florica cea
                    saraca, marcheaza inceputul conflictului. Venirea lui Vasile
                    Baciu, tatal Anei, si confruntarea verbala cu Ion, pentru ca
                    sarantocul umbla sa-i ia fata promisa altui taran bogat
                    George Bulbuc, constituie intriga romanului. Rusinea pe care
                    Vasile i-o face la hora, in fata satului, va starni dorinta
                    de razbunare a flacaului, care la randul sau il va face pe
                    chiabur de rusinea satului, lasand-o pe Ana insarcinata
                    pentru a obtine pamantul ca zestre.
                  </p>
                  <p>
                    Ion il forteaza astfel pe Vasile Baciu sa accepte casatoria.
                    La nunta cu Ana, Ion isi da seama ca tot pe Florica o
                    iubeste. In plus, el nu-i cere acte socrului pentru pamantul
                    - zestre, si simtindu-se inselat, incep bataile si drumurile
                    Anei de la Ion la Vasile. Preotul Belciug mediaza conflictul
                    dintre Ion si Vasile, in care „biata Ana nu este decat o
                    victima tragica". Sinuciderea sotiei nu-i trezeste regrete
                    sau constiinta vinovatiei, pentru ca in Ana, iar apoi in
                    Petrisor, fiul lor, nu vede decat garantia proprietatii
                    pamanturilor. Nici moartea copilului nu-l opreste din
                    drumurile lui dupa Florica, maritata acum cu George, astfel
                    ca deznodamantul este previzibil. George il omoara pe Ion si
                    este arestat, Florica ramane singura, iar averea lui Ion
                    revine bisericii.
                  </p>
                  <p>
                    In celalalt plan, rivalitatea dintre preot si invatator
                    pentru autoritate in sat este defavorabila celui din urma.
                    El are familie: sotie, un baiat, poetul visator Titu, si
                    doua fete de maritat, dar fara zestre, Laura si Ghighi. In
                    plus, casa si-o zidise pe lotul bisericii, cu invoirea
                    preotului. Cum relatiile dintre ei se degradeaza, pornind de
                    la atitudinea lor fata de faptele lui Ion, invatatorul se
                    simte amenintat. Marturisirea lui Ion ca invatatorul i-a
                    scris jalba determina conflictul celui din urma cu
                    autoritatile austro-ungare si problemele sale de constiinta
                    nationala. Accepta inutil compromisul, votandu-l pe
                    candidatul maghiar la alegeri. Preotul Belciug este un
                    caracter tare. Ramas vaduv inca de tanar, se dedica total
                    comunitatii. Visul sau de a construi o biserica noua in sat
                    este urmarit cu tenacitate, iar romanul se incheie cu
                    petrecerea poporului prilejuita de sfintirea bisericii.
                  </p>
                </div>
                <div class="section">
                  <span className="title">Caracterizarea personajelor: </span>
                  <p>
                    <strong>Ion: </strong>Ion este un personaj eponim, realizat
                    prin tehnica basoreliefului. El domina intreaga lume, ce
                    graviteaza in jurul sau. Aceasta lume contribuie la
                    evidentierea trasaturilor sale, a caracterizarii complexe cu
                    insusiri contradictorii.Monografia satului ardelean se
                    realizeaza treptat prin prezentarea obiceiurilor,
                    traditiilor, problemelor taranilor, problema nationala. Nu
                    intamplator romanul incepe cu descrierea jocului popular,
                    scena antologica prin care autorul realizeaza mai multe
                    obiective: introduce treptat personajele, descrie
                    obiceiurile si sugereaza viitoarele conflicte.
                  </p>
                  <p>
                    Ion este caracterizat in mod direct de catre narator
                    beneficiind de un portret marcat de calitati: "Iute si
                    harnic ca ma-sa", "munca ii era draga oricat ar fi fost de
                    aspra". Elementul fundamental al conflictului, pamantul ii
                    subordoneaza toate trasaturile "pamantul ii era drag ca
                    ochii din cap", iar lipsa acestuia apare ca o nedreptate
                    ceea ce justifica dorinta patimoasa de-al avea: "toata
                    istetimea lui nu plateste o ceapa degerata daca n-are si el
                    pamant mult...". Celelalte personaje ii evidentiaza
                    caracterul cu lumini si umbre in functie de conflictele in
                    care sunt implicati. Astfel invatatorul Herdelea il aprecia
                    ca "unul dintre cei mai iubiti elevi, iar doamna Herdelea il
                    considera un baiat cumsecade, muncitor, harnic, saritor si
                    istet". Optica lor se va schimba cand Ion va trece in
                    conflictul dintre invatator si preot, Ion fiind de partea
                    celui din urma. Autocaracterizarea evidentieaza framantarile
                    sufletesti prin intermediul monologului interior "Ma
                    molesesc ca o baba". Definitoru pentru personajul principal
                    este conflictul interior intre glasul pamantului si glasul
                    iubirii.(desi o iubeste pe Florica, fata frumoasa dar
                    saraca, dorinta de posesie a pamantului il determina sa o
                    aleaga pe Ana). Ana cea "uratica" dar bogata. Cele mai multe
                    trasaturi reies din caracterizarea indirecta din faptele si
                    atitudinea personajelor, din relatiile sale cu celelalte
                    personaje.
                  </p>
                  <p>
                    Astfel, Ion este impulsiv, chiar violent uneori, ceea ce
                    atrage teama celorlalti flacai ai satului. Viclenia sa este
                    evidentiata in relatia cu Ana pe care o seduce. Inteligenta
                    dura, egoismul, cruzimea ii subordoneaza toate actiunile. In
                    relatia cu Vasile Baciu este naiv, crezand ca nunta ii va
                    aduce si pamanturile. De fapt destinul acestuia nu este
                    marcat de conflictele exterioare, cat mai ales de cele
                    interioare, generate de relatia sa cu pamanturile: "Il
                    cuprinsese o pofta salbatica sa imbratiseze bruma...".
                    Personajul traieste o deplina voluptate a simturilor, a
                    puterii si a trupului atunci cand vorbeste despre pamant.
                    Din aceasta perspectiva, deznodamantul este previzibil. Ion
                    va murii, ucis cu ajutorul unei unelte a pamantului, sapa.
                    Ion este un personaj memorabil, ipostaza a omului simplu dar
                    supus destinului tragc de a fi strivit de forte mai presus
                    de vointa lui: pamantul, stihie.
                  </p>
                  <p>
                    <strong>Ana: </strong>Ana, fata instaritului Vasile Baciu,
                    pare nascuta sub semnul nefericirii, fiind predestinata unei
                    existente tragice. Autorul o surprinde in trei ipostaze
                    succesive care ii contureaza treptat profilul moral prin
                    analiza sufletului ei chinuit: cea de tanara femeie,
                    indragostita profund de Ion, caruia ii incredinteaza cu
                    generozitate viata, aceea de sotie, indurand cu umilinta
                    vorbele grele si loviturile barbatului si aceea de mama,
                    ipostazata care in circumstante normale ar fi putut deveni o
                    supapa salvatoare pentru femeia nefericita. Intreaga
                    existenta a Anei este guvernata de iubire si blandete,
                    virtuti care intregesc un portret moral superior. Ea este
                    harnica, supusa, rusinoasa, prototipul femeii de la tara.
                  </p>
                  <p>
                    Din punct de vedre fizic, Ana este insignifianta; pentru Ion
                    ea este o fata “slabuta” si “uritica”, mai ales in
                    comparatie cu Florica, ai carei “obraji fragezi ca piersica”
                    si “ochii albastri ca cerul de primavara“ ii tulburase
                    sufletul flacaului. Lui George Bulbuc, flacaul bogat pe care
                    Baciu il voia ginere “Ana ii placea”, lui nu i se parea
                    urata, insa nu ii zicea ca-i “cine stie ce frumoasa”. Firava
                    si fara personalitate, asa cum pare la inceput, covarsita de
                    vointa lui Ion, imbatate de cuvintele si gesturile lui
                    dragostoase, Ana va deveni o victima usoara a flacaului
                    interesat numai de zestrea sa. In ciuda acestei firi slabe
                    care se anunta din primul capitol, Ana va dovedi pe
                    parcursul actiunii o vointa si o putere de a rabda
                    uluitoare. Nu numai Ion e un revoltat, incalcand normele
                    colectivitatii; Ana insasi traieste aceeasi conditie,
                    intrucat nesocoteste obiceiul tipic lumii rurale de a
                    accepta casatoria planuita de parinti, in care latura
                    sentimentala nu are importanta. Autorul comenteaza ”Ana lui
                    Vasile Baciu ii era fagaduita lui George Bulbuc de nevasta.
                    Ea, fata cu stare, el fecior de botocon, se potriveau.”
                  </p>
                  <p>
                    Din dragoste pentru Ion, Ana accepta relatia cu acesta si,
                    chiar si atunci insarcinata, ajunge de rasul satului si e
                    crunt batuta de tatal ei, nu-i reproseaza nimic lui Ion.
                    Pentru ca a “crescut singura, lipsita de o dragoste
                    parinteasca mangaietoare … sufletul ei trist cauta o
                    dragoste sfioasa si adanca“. Din dragoste isi infrunta
                    tatal, acceptand orice umilinta din partea lui si a
                    colectivitatii. Ceea ce o distruge insa este totala lipsa de
                    afectiune al lui Ion, pentru care sacrificase totul si fara
                    de care viata ei nu-si aflase rostul: ”isi zicea mereu, ca
                    fara el ar trebui sa moara“. Framantarile fetei, nesigura de
                    dragostea lui Ion, complexata de frumusetea Floricai, sunt
                    surprinse cu fina intuitie psihologica, autorul insistand
                    mai ales pe deznadejdea ei care ii da adesea “ganduri de
                    moarte”.
                  </p>
                  <p>
                    Nunta Floricai cu George e un moment de cumpana in existenta
                    Anei, care intrevede acum moartea ca pe unica scapare din
                    acest univers cuprins parca de niste “ape tulburi”. Femeia
                    simte acum “o sila grea pentru tot ceeea ce o inconjoara”,
                    iar copilul I se pare o povara. Obsesiv ii apare imaginea
                    lui Avrum care se spanzurase. Sinuciderea ei este descrisa
                    minutios intr-un capitol de mare forta analitica, streangul.
                    Moartea ei, fara sa fie in intentia asta, devine, prin
                    urmarile ei, o cumplita pedeapsa aplicata aceluia care I-a
                    distrus viata... Destinul ei este tipic lumii rurale, unde
                    “femeia reprezinta doua brate de lucru, o zestre si o
                    producatoare de copii”.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
