import React, { Component } from "react";
import { Layout } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class FloriDeMucigai extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Flori de mucigai</h1>
              <h4>Tudor Arghezii</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section poezie">
                Le-am scris cu unghia pe tencuială <br />
                Pe un părete de firidă goală, <br />
                Pe întuneric, în singurătate, <br />
                Cu puterile neajutate <br />
                Nici de taurul, nici de leul, nici de vulturul <br />
                Care au lucrat împrejurul <br />
                Lui Luca, lui Marcu şi lui Ioan. <br />
                Sunt stihuri fără an, <br />
                Stihuri de groapă, <br />
                De sete de apă Şi de foame de scrum, <br />
                Stihurile de acum. <br />
                Când mi s-a tocit unghia <br />
                îngerească Am lăsat-o să crească <br />
                Şi nu mi-a crescut - <br />
                Sau nu o mai am cunoscut. <br />
                Era întuneric. <br />
                Ploaia bătea departe, afară. <br />
                Şi mă durea mână ca o ghiară <br />
                Neputincioasă să se strângă <br />
                Şi m-am silit să scriu cu unghiile de la mâna stângă. <br />
              </div>
              <div class="section">
                <span className="title">Poezia:</span>
                <p>
                  Poezia "Flori de mucigai" se afia în deschiderea volumului
                  omonim și constituie arta poetică a lui Arghezi, concepția lui
                  despre efortul artistului și implicațiile acestuia în actul
                  creației, constituind -așadar - poezia programatică a acestui
                  volum, așa cum "Testament" este arta poetică din volumul
                  "Cuvinte potrivite".
                </p>
              </div>
              <div class="section">
                <span className="title">Tema poeziei:</span>
                <p>
                  Tema poeziei exprimă efortul creator al artistului pentru un
                  produs spiritual și consecințelepe care le are acesta asupra
                  stărilor interioare ale eului poetic, chinuit de frământări și
                  de tulburări interioare.
                </p>

                <p>
                  Versurile nu mai sunt produsul unei revelații, al harului
                  divin, ci al unei neliniști artistice și al setei creatoare.
                </p>
              </div>
              <div class="section">
                <span className="title">Titlul:</span>

                <p>
                  Titlul poeziei "Flori de mucigai" este un oximoron, în care
                  florile sugerează frumusețea, puritatea, lumina, iar mucigaiul
                  semnifică urâtul, răul, descompunerea și întunericul.
                </p>
                <p>
                  Oximoronul creează o imagine contradictorie a lumii, în care
                  valorile umane sunt degradate, alterate, lumea închisorilor,
                  în care viața oamenilor este supusă reprimărilor,
                  restricțiilor rigide.
                </p>

                <p>
                  Titlul este, în același timp, reprezentativ pentru inovația
                  limbajului arghezian numită estetica urâtului, o modalitate
                  artistică întâlnită în lirica europeană la Baudelaire, care
                  scrisese "Florile râului".
                </p>
                <p>
                  Asocierea celor două categorii estetice contradictorii,
                  frumosul - reprezentat de floare - și urâtul - sugerat de
                  mucigai - oferă titlului o expresivitate șocantă și fascinanta
                  totodată prin efectele estetice.
                </p>
                <p>
                  Urâtul are rolul de a evidenția imperfecțiunile vieții,
                  senzațiile de aversiune și oroare care capătă valori noi, ele
                  făcând parte din existența umană.
                </p>
              </div>
              <div class="section">
                <span className="title">Structura:</span>
                <p>
                  Poezia "Flori de mucigai" este structurată în două secvențe
                  lirice inegale, prima ilustrând crezul artistic arghezian, iar
                  cealaltă neputință artistului de a crea în condiții de
                  claustrare.
                </p>

                <p>
                  <strong>Primă secvență</strong> sugerează dorința devoratoare
                  a artistului de a se exprima în versuri, fiind dominat de
                  setea de comunicare cu lumea. Poetul, într-o solitudine impusă
                  și lipsindu-i uneltele scrisului, încearcă să zgârie
                  <span className="citat">
                    "cu unghia pe tencuiaia / Pe un părete de firidă goală, / Pe
                    întuneric" /
                  </span>
                  versurile născute din nevoia comunicării.
                </p>
                <p>
                  Condițiile vitrege de viață îi seacă forță creatoare,
                  <span className="citat">
                    "Cu puterile neajutate / Nici de taurul, nici de leul, nici
                    de vulturul / Care au lucrat împrejurul / Lui Luca, lui
                    Marcu și lui loan."
                  </span>
                  Enumerarea prin negație a elementelor fabuloase ale
                  evangheliștilor, "taurul", "leul", "vulturul", creează o
                  imagine de mare forță sugestivă privind starea de deprimare a
                  poetului nefericit în absența creației, în raport direct cu
                  scrierile religioase a căror esență este Absolutul.
                </p>
                <p>
                  Versurile sunt săpate în sufletul poetului, sunt "stihuri fără
                  an" ce nu pot fi exprimate în viața reală, dar sunt profund
                  simțite de sensibilitatea artistului:
                  <span className="citat">
                    "Stihuri de groapă / De sete de apă / Și de foame de scrum".
                  </span>
                </p>
                <p>
                  Harul poetic, "unghia îngerească", este tocit de efort, nu-i
                  mai permite poetului revelația, deoarece ea "nu a mai crescut"
                  sau, altfel spus, artistul nu se mai poate regăsi în sine, nu
                  se mai percepe ca pe un creator de valori spirituale: "Sau nu
                  o mai am cunoscut".
                </p>

                <p>
                  <strong>Ultima secvență</strong> amplifică deznădejdea lui
                  Arghezi, care este simbolizată de atmosfera sumbră, e
                  "întuneric", iar ploaia se aude "departe afară", ceea ce
                  provoacă poetului o durere simțită profundă, "ca o ghiară",
                  din cauza neputinței totale de a se exprima. Nevoia de
                  comunicare a poetului cu lumea, setea de a-și dezvălui
                  trăirile îl silesc să scrie "cu unghiile de la mâna stângă".
                </p>
                <p>
                  O simbolistică străveche asociază mâna stângă cu forțele
                  demonice, în opoziție totală cu puterea divină a creației.
                  Închisoarea este pentru Arghezi un fel de bolgie a Infernului
                  lui Dante, însă, în acest cadru al ororilor, frumosul nu este
                  absent.
                </p>
                <p>
                  Râul, urâtul sunt numai conjuncturi ale destinului, cărora
                  omul le opune aspirația spre frumos, care poate fi regăsit în
                  sine, în vis sau în speranța împlinirii. În "Flori de mucigai"
                  predomină registrele stilistice ale esteticii urâtuiui, pe de
                  o parte ca inovație lingvistică, pe de altă parte ca substanță
                  a ideilor exprimate.
                </p>
              </div>
              <div class="section">
                <span className="title">Limbajul:</span>
                <p>
                  Limbajul este caracterizat prin folosirea cuvintelor care
                  șochează prin expresivitatea fascinantă, cuvinte "urâte", al
                  căror sens capătă noi valori. De pildă, cuvântul "mucigai"
                  este un regionalism cu aspect arhaic, dar are aici sensul
                  profund al degradării morale, al descompunerii spirituale, cu
                  trimitere sugestivă către om, deoarece el însoțește cuvântul
                  "flori", care poate semnifica viața, lumea.
                </p>

                <p>
                  Arghezi utilizează cuvinte din limbajul popular ori arhaisme,
                  ca "firidă", "stihuri", din vocabularul religios, cum sunt
                  numele celor trei evangheliști - Luca, Marcu, loan -pentru a
                  sugera atemporalitatea stărilor sufletești de tristețe,
                  dezamăgire și deprimare ale poetului.
                </p>
              </div>
              <div class="section">
                <span className="title">Oximoronul:</span>
                <p>
                  Oximoronul "flori de mucigai" transmite ideea complexă a
                  imperfecțiunilor vieții, a condițiilor vitrege la care este
                  supusă ființă umană, fapt care îi provoacă poetului aversiune,
                  repulsie. Metaforele argheziene potențează starea de disperare
                  a omului claustrat, a artistului care nu poate crea liber,
                  fiind constrâns să-și reprime setea de comunicare.
                </p>

                <p>
                  Neputința creatoare a artistului este sugestiv relevată de
                  metaforă "cu puterile neajutate", deși poetul avea resurse
                  spirituale profunde. Lumea închisorii poartă în ea stigmatele
                  răului care acționează negativ asupra naturii angelice a
                  ființei umane, tocește s"unghia îngerească", împiedicând-o
                  astfel să e exprime.
                </p>

                <p>
                  La noutatea limbajului arghezian s-a referit și Tudor Vianu,
                  care afirmă: "Renovarea liricii românești, smulgerea ei de pe
                  căile unde o fixase marea influență a poetului "Luceafărului",
                  este consecința cea mai importantă produsă de afirmarea lui
                  Arghezi încă din al doilea deceniu al secolului nostru
                  (secolul al XX-lea - n.n.)".
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
