import React, { Component } from "react";
import { Layout } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class MoaraCuNoroc extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Moara cu noroc</h1>
              <h4>Ioan Slavici</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section">
                <span className="title">Definiția nuvelei psihologice:</span>
                <p>
                  Specie a genului epic, in proza cu dimensiuni mai mari in
                  comparatie cu cele ale schitei si povestirii, dar mai reduse
                  in comparatie cu cele ale romanului, avand un singur fi epic,
                  conflict puternic iar personajele sunt puternic reliefate.
                  Nuvela este psihologica prin tematica, modalitati de
                  caracterizare a personajelor, investigare psihologica si
                  natura conglictului si anume interior.
                </p>

                <div class="section">
                  <span className="title">Trăsături:</span>
                  <p>
                    Opera lui Slavici este ca specie o nuvela psihologica
                    deoarece conflictul major este cel interior (Lupta lui Ghita
                    intre ratiune si patima pentru bani). Prezenta personajelor
                    puternic reliefate (Ghita, Lica, Ana), nararea se face la
                    persoana a treia. Este urmarit un singur fir narativ
                    (imbogatirea lui Ghita si instrainarea lui fata de sine si
                    de familie). Timpul relatat este indelungat (peparcursul
                    unui an-de la sfantul Gheorghe pana in anul urmator la
                    Pasti). Este impartita pe Capitole.
                  </p>
                </div>

                <div class="section">
                  <span className="title">Aparitia:</span>
                  <p>
                    Nuvela "Moara cu noroc" a fost publicata in volumul de debut
                    "Novele din popor" in anul 1881.
                  </p>
                </div>
                <div class="section">
                  <span className="title">Titlul:</span>
                  <p>
                    Titlul reprezinta toposul unde se petrece actiunea. In
                    structura de adancime, titlul este o metafora ce include o
                    ironie si rezuma destinele personajelor. Ideea de "moara"
                    dezvaluie ideea unei miscari continue, prin scopul ei de a
                    macina, moara sugereaza macinarea sufletelor si degradarea
                    oamenilor iar norocul de la inceput are sa se transforme in
                    ghinion.
                  </p>
                </div>
                <div class="section">
                  <span className="title">Tema:</span>
                  <p>
                    Temele nuvelei le reprezinta setea de imbogatire, familia,
                    iubirea si consecintele nefaste pe care setea de imbogatire
                    le are asupra individului.
                  </p>
                </div>
                <div class="section">
                  <span className="title">
                    Viziunea despre lume a autorului:
                  </span>
                  <p>
                    Este exprimata prin cuvintele batranei din incipit potrivit
                    carora omul trebuie sa fie multumit cu ceea ce are, caci nu
                    exista bogatie mai mare decat cumpatarea, adevarul si
                    omenia. In aceeasi viziune se inscrie si finalul
                    moralizator, care pare a transmite invatatura ca cei care
                    incalca normele etice sunt aspru pedepsiti de soarta.
                    Moralistul din spatele batranei vrea sa scoata in evidenta
                    ca binele coexista in lume cu raul dar omul matur si trecut
                    prin viata trebuie sa discearna intre bine si rau.
                  </p>
                </div>
                <div class="section">
                  <span className="title">Perspectiva narativa:</span>
                  <p>
                    Este obiectiva iar naratorul este omniscient, omniprezent si
                    omnipotent.
                  </p>
                </div>
                <div class="section">
                  <span className="title">Compozitia:</span>
                  <p>
                    Nuvela are 17 capitole numerotate cu cifre romane fara
                    titlu, este clasica, echilibrata, ciclica, rotunjita iar
                    evenimentele se desfasoara in ordine cronologica.
                  </p>
                </div>
                <div class="section">
                  <span className="title">
                    Relatiile spatiale si temporale:
                  </span>
                  <p>
                    Ca si spatiu, aciunea se desfasoara in casa parinteasca a
                    Anei, la carciuma Moara cu noroc, pe drumul spre Ineu, in
                    padure si la judecatoria Ineu. Iar ca si timp nuvela face
                    referire la secolul XIX, cand in satul romanesc traditional
                    patrund relatiile de tip capitalist.
                  </p>
                </div>

                <div class="section">
                  <span className="title">Povestirea:</span>
                  <p>
                    <strong>I. </strong> Soacra lui Ghita, mama sotiei lui, Ana,
                    isi spune parerea in legatura cu intentia ginerelui sau de a
                    lua in arenda carciuma numita Moara cu noroc, de langa Ineu:
                    "-Omul sa fie multumit cu saracia sa, caci daca e vorba, nu
                    bogatia, ci linistea colibei tale te face fericit. Dar voi
                    sa faceti dupa cum va trage inima". Ghita ii raspunde: "sa
                    ramanem aici, sa carpesc si mai departe cizmele oamenilor,
                    care umblatoata saptamana in opinci ori desculti, iara daca
                    dumineca e noroi, isi duc cizmele in mana pana la biserica,
                    si sa ne punem pe prispa casei in soare, privind eu la Ana,
                    Ana la mine, amandoi la copilasi, iara d-ta la tustrei. Iaca
                    linistea colibei". Si se decide sa arendeze carciuma.
                  </p>
                  <p>
                    <strong>II. </strong> Moara cu noroc este un loc unde se
                    castiga bine caci pe drumetul "venind despre locurile rele,
                    ea il vesteste ca a scapat norocos, iara mergand spre ele,
                    la moara poate sa gaseasca ori sa astepte alti drumeti, ca
                    sa nu plece singur mai departe." In plus fata de situarea
                    favorabila, clientii erau multumiti deoarece noul carciumar
                    si familia sa "nu il primeau pe drumet ca pe un strain venit
                    din lume ci ca pe un prieten asteptat de multa vreme in casa
                    lor".
                  </p>
                  <p>
                    <strong>III. </strong> Soseste la Moara cu noroc Lica
                    Samadaul, "un om ca de treizeci si sase de ani, inalt,
                    uscativ si supt la fata, cu mustata lunga, cu ochii mici si
                    verzi si cu sprancenele dese si impreunate la mijloc".
                    Numele de "Samadaul" este de fapt o porecla, insemnand cel
                    care raspunde de turmele de porci date porcarilor la pascut.
                    De obicei este un "om aspru si neindurat, care umbla mereu
                    calare de la turma la turma, care stie toate infundaturile,
                    cunoaste pe toti oamenii buni si mai ales pe cei rai, de
                    care tremura toata lumea si care stie sa afle urechea
                    grasunului pripasit chiar si din oala cu varza". Pe Ana,
                    Lica o fascineaza ("ramase privind ca un copil uimit la
                    calaretul ce statea ca un stalp de piatra inaintea ei.") iar
                    lui Ghita oaspetele ii cere sa il informeze despre ce se
                    intampla la Moara.
                  </p>
                  <p>
                    <strong>IV. </strong> In urma vizitei lui Lica, Ghita isi
                    cumpara doua pistoale, ia o a doua sluga si doi caini, pe
                    care ii invata sa fie rai. Dar isi schimba si atitudinea
                    fata de sotia sa: "Ca om harnic si sarguitor, Ghita era
                    mereu asezat si pus pe ganduri, dar el se bucura cand o
                    vedea pe dansa vesela: acum el se facuse mai de tot ursuz,
                    se aprindea pentru orice lucru de nimic, nu mai zambea ca
                    mai nainte, ci radea cu hohot, incat iti venea sa te sperii
                    de el, iar cand se mai harjonea cateodata cu dansa, isi
                    pierdea lesne cumpatul si-i lasa urme vinete pe brate". Lica
                    ii cere carciumarului, printr-un porcar sosit cu turma la
                    Moara, sa dea de mancare si de baut porcarilor sai si sa isi
                    opreasca drept plata cinci porci din acea turma. Ghita
                    refuza la inceput, apoi, la trecerea ultimelor turme, cand
                    vrea sa isi opreasca porci, pazitorii lor ii spun ca nu au
                    primit de la Lica ordin sa ii dea. Carciumarul interpreteaza
                    situatia ca semn al dependentei sale de bunavoia Samadaului.
                  </p>
                  <p>
                    <strong>V. </strong> Lica Samadaul, impreuna cu doi
                    tovarasi, se apropie de carciuma pe un drum laturalnic, dar
                    este latrat de cainii cei rai ai lui Ghita pe care ii
                    imblanzeste. Vazandu-l pe Lica sosit astfel, Ana il trimite
                    pe Laie, sluga lor, sa astepte ascuns in spatele unor
                    rachiti de langa Moara, pentru a putea da de stire daca se
                    intampla ceva rau. Samadaul ii da lui Ghita insemnele
                    turmelor de porci, bucati de piele pe care le prinde la
                    urechile porcilor, deosebite ca forma, ii cere sa observe
                    deplasarile turmelor si sa il informeze despre aceste
                    deplasari. Dupa care ia de la carciumar, sub amenintarea ca
                    nu il va mai lasa sa stea la Moara, o parte din banii
                    acestuia, ca imprumut. Iritat de niste cuvinte josnice
                    rostite de Lica dupa ce a luat banii ("Asa-i ca te-ai facut
                    bland ca un mielusel?"), Ghita il avertizeaza pe
                    Samadau:"Lica, tu trebuie sa intelegi ca oamenii ca mine
                    sunt slugi primejdioase, dar prieteni nepretuiti". Afland de
                    la Ghita despre trimiterea lui Laie, Lica accepta sa il
                    trateze pe carciumar ca pe un prieten.
                  </p>
                  <p>
                    <strong>VI. </strong> "Ana se simtea tot mai parasita. De
                    cand se imprietenise cu Lica, Ghita parca fugea de dansa,
                    parca-i ascundea ceva si se ferea sa nu ramaie singur cu
                    dansa". Ghita primeste de la Lica sase porci, dar nici unul
                    dintre ei nu avea insemnele turmelor Samadaului. Acesta il
                    intreaba pe carciumar, in timpul unei vizite la Moara,
                    prefacandu-se a vorbi in soapta, dar de fapt vorbind in asa
                    fel incat sa il auda cei din preajma, despre ziua cand vine
                    arendasul evreu pentru a-si lua banii de arenda de la Ghita.
                    Dupa care, profitand de sosirea unor lautari tigani la
                    carciuma, Lica ii pune sa cante si joaca cu Ana. "Incetul cu
                    incetul, ea prinse voie buna; se cam tulbura cand Lica se
                    apropia de dansa; sangele ii navalea in obraji cand el o
                    apuca de brau ca s-o invarteasca; dar asa era acum, si
                    altfel nu putea sa fie, si ea se dete din ce in ce dupa
                    par"; atitudinea ei trezeste gelozia sotului.
                  </p>
                  <p>
                    <strong>VII. </strong>Ghita se imprieteneste cu unul dintre
                    jandarmii de la Ineu, "Pintea caprarul, om scurt si indesat,
                    cu ochii mari, cu umerii obrajilor iesiti si cu falcile
                    late, cu mustata tunsa si cu o taietura pe frunte, dar mai
                    presus de toate om asezat si tacut la fire". Situatia
                    economica a carciumarului si a familiei sale este
                    infloritoare, caci "avea porci la ingrasare, doua vaci cu
                    lapte, caruta pe razoare, doi cai buni, avea bani in lada,
                    nu prea multi, dara destui, ca sa poata trai un an, doi
                    dintrinsii". Lica ramane peste noapte la Moara cu noroc, iar
                    Ghita, trezit de latratul cainilor priveste afara si are
                    impresia ca vede sosind, pentru a-l vizita pe Lica, un
                    barbat insotit de o femeie cu umeri largi. Din nou trezit de
                    caini si privind iarasi afara, lui Ghita i se pare ca vede
                    plecand un alt barbat, ce parea a fi Lica, insotit de
                    aceeasi femeie. Sosit la carciuma, Pintea il informeaza pe
                    Ghita despre faptul ca arendasul evreu fusese "batut de
                    anevoie se mai poate pune pe picioare" si jefuit acasa la
                    el. Totodata ii marturiseste ca a fost tovaras de talharii
                    cu Lica, a fost prins impreuna cu el, inainte de a deveni
                    jandarm, dupa care precizeaza: "Ghita, sunt de treizeci si
                    opt de ani: ma spanzur daca implinesc patruzeci fara ca sa-i
                    arat ca mai sunt si altii ca dansul. Mi-a facut una, pe care
                    n-am sa i-o uit toata viata". Dupa care merge cu Ghita si cu
                    cele doua slugi ale acestuia, Laie si Marti, la Ineu, pentru
                    cercetari asupra jafului de la arendas.
                  </p>
                  <p>
                    <strong>VIII. </strong> In absenta lui Ghita soseste la
                    carciuma o trasura cu o femeie tanara in doliu, cu un copil
                    de cinci ani, insotita de vizitiu si de feciorul din casa.
                    Discutand cu feciorul, Ana afla ca sotul femeii s-a sinucis
                    cu trei saptamani in urma si ca femeia, se zice, are turme
                    de porci in padurile din preajma Morii cu noroc. Discutand
                    intre ei, feciorul si vizitiul, primul ii spune celui de-al
                    doilea ca banuieste o intovarasire intre Lica si femeie, ca
                    unul fura bijuterii si celalalt le vinde. Femeia plateste
                    Anei cu o hartie careia ii lipseste un colt, Ana cere sa i-o
                    schimbe ceea ce vizitatoarea si face.
                  </p>
                  <p>
                    <strong>IX. </strong> La Ineu, Ghita si slugile sale sunt
                    anchetati de catre comisar, care afla de la Marti ca "Ghita
                    are o veriga de sarma, pe care sunt insirate semnele
                    turmelor lui Lica, de la care a primit in mai multe randuri
                    porci, si ca luni, ca ieri, Lica a stat cu Raut, cu
                    Buza-Rupta, cu Saila Boarul si cu Ghita la carciuma, ca ei
                    au vorbit cam in taina despre arandasul si ca in amurgul
                    serii Buza-Rupta si cu Saila au plecat spre Ineu, Raut a
                    luat-o pe vale in sus la padure, iara Lica a ramas pana
                    dimineata la carciuma. Tarziu apoi, cam pe la miezul noptii,
                    s-au miscat cainii si asa el, Marti, a iesit afara si,
                    vazand un om si o muiere, a intrebat cine-i. Raut a raspuns
                    atunci: Oameni buni!. De cu zori insa el iar s-a desteptat,
                    fiindca iar se miscau cainii: a iesit si a vazut ca Raut se
                    duce cu muierea aceea". Marturia lui Ghita difera de cea a
                    lui Marti, el afirmand ca nu Raut fusese cel care a venit cu
                    "o muiere" si ca cei doi, "omul" si "muierea" au plecat la
                    scurt timp dupa ce sosisera, imediat dupa miezul noptii.
                    Dupa depunerea marturiilor, Ghita este eliberat pe garantie,
                    o garantie scrisa si semnata de un var preot.
                  </p>
                  <p>
                    Pintea ii aduce lui Lica o servitoare, pe Uta, om de-al sau
                    menit sa intre in legatura cu Lica si sa il spioneze.
                    Revenind la Moara cu noroc impreuna cu Pintea, doi jandarmi
                    si noua servitoare, pe drum gasesc o trasura fara cal, iar
                    langa trasura un copil omorat de o lovitura primita in cap.
                    Cu toata impotrivirea lui Pintea, care vroia sa il retina
                    pentru a-i face apoi o perchezitie acasa, Ghita pleaca la
                    Moara cu noroc, impreuna cu Uta, pentru a vedea daca s-a
                    intamplat ceva rau acolo. Prezenta servitoarei in caruta
                    alaturi de Ghita o face pe Ana sa aiba o violenta reactie de
                    gelozie:"nu mai simti in ea decat o singura pornire
                    patimasa: sa mearge la femeia aceea si sa-i traga cu
                    ghearele pelea de pe obraji". Apoi, pe acelasi fond de
                    enervare, isi acuza sotul de colaborare cu Lica in jefuirea
                    arendasului, atitudine ce starneste mania lui Ghita("-Mi-e
                    scarba, cand ma gandesc ca am o nevasta care poate sa mai
                    traiasca cu un om, precum tu ma socotesti pe mine, zise el,
                    si iesi din casa cu amandoua mainile in cap.").
                  </p>
                  <p>
                    <strong> X. </strong> Plecat in cercetare, Pintea gaseste
                    cadavrul unei tinere femei, imbracata in negru, deja
                    intepenit. Ea murise sufocata de carpa cu care fusese legata
                    la gura, iar mainile ii erau prinse la spate cu un bici, al
                    lui Lica. Dupa aceea Pintea il gaseste pe unul dintre
                    jandarmi, Hantl, impuscat si injunghiat cu un cutit ramas in
                    rana, dar inca in viata, pentru scurt timp. Cutitul, spun
                    jandarmii care umblasera sa-i prinda pe Saila si pe
                    Buza-Rupta, ar fi fost al lui Saila, iar la casa lui
                    Buza-Rupta au gasit ascunsa o parte din argintaria furata de
                    la arendas. Pintea il aresteaza pe Lica, in casa din Ineu a
                    unui prieten al acestuia; in fata comisarului, Samadaul
                    declara ca biciul era al sau si l-a uitat probabil la Moara,
                    iar cutitul era al lui Saila. Lica este eliberat la
                    interventia unui proprietar de turme de porci.
                  </p>
                  <p>
                    <strong>XI. </strong> Raut, Buza-Rupta si Saila sunt prinsi,
                    iar procesul are loc la Oradea. In timpul audierilor, Lica
                    neaga sosirea lui Raut cu o femeie la el si spune ca Raut a
                    venit, dar singur, sa il anunte despre disparitia unei parti
                    din turma de porci din padurea de la Fundureni. Dupa cum
                    spune ca nu isi aduce aminte sa fi vorbit la Moara cu noroc
                    despre arendas, ori ca Saila si Buza-Rupta sa fi plecat in
                    noaptea jafului spre Ineu. Cum exista declaratia lui Marti
                    facuta anterior, judecatorii inclina sa creada ca Lica neaga
                    sosirea femeii din cavalerism, si ca neaga plecarea lui
                    Saila si Buza-Rupta pentru ca acestia ii erau tovarasi. Dupa
                    cum inclina sa creada ca Saila si Buza-Rupta sunt autorii
                    jafului de la arendas si ai uciderii copilului, femeii si
                    jandarmului.
                  </p>
                  <p>
                    Pintea insa afirma in fata judecatorilor "ca nu-i crede pe
                    Buza-Rupta si pe Saila vinovati, ca argintaria arandasului a
                    fost ascunsa de altii la casa lui Buza-Rupta, ca cutitul lui
                    Saila a fost lasat dinadins in trupul lui Hantl". Buza-Rupta
                    declara ca nu a fost in perioada jafului si a crimelor la
                    Ineu, ci cu Raut si Saila la Salonta, iar Saila spune ca s-a
                    inteles cu Raut sa fure o parte din turma de la Fundureni,
                    iar apoi s-a dus la Salonta ca sa caute cumparator. Ghita
                    afirma si el, ca si Pintea, ca nu ii crede vinovati pe Saila
                    si pe Buza-Rupta. Prin verdictul judecatorilor, Lica si
                    Ghita sunt socotiti nevinovati, iar Buza-Rupta si Saila
                    Boarul condamnati la inchisoare pe viata.
                  </p>
                  <p>
                    <strong>XII. </strong> Lica ii restituie lui Ghita banii pe
                    care ii luase cu imprumut, specificand, in urma intrebarii
                    carciumarului, ca sunt din cei furati de la arandas si de la
                    femeia ucisa. Despre femeie marturiseste, intrebat de Ghita,
                    ca a ucis-o pentru ca si-a pastrat pentru ea un lant de aur
                    pe care el i l-a lasat pentru a-l vinde in conditii sigure,
                    putand astfel sa-l dea de gol. Isi expune totodata starea
                    psihica ce il cprinde atunci cand devine violent:"sangele
                    cald e un fel de boala, care ma apuca din cand in cand".
                  </p>
                  <p>
                    <strong>XIII. </strong>Participand la numararea banilor,
                    alaturi de Ghita, Ana recunoaste intre bancnote hartia rupta
                    la un colt pe care i-o schimbase cu o alta intreaga femeia
                    ce sosise cu un copil. Dupa cum recunoaste si banii noi,
                    dintre care femeia a luat hartia buna spre a o schimba pe
                    cea data inapoi de Ana. La intrebarea ei asupra provenientei
                    banilor, Ghita raspunde ca intr-o zi ea va afla de unde sunt
                    banii, si pleaca la Ineu, unde ii vorbeste lui Pintea despre
                    bani. Acesta ii cere sa ia de la Lica, ce va veni la el
                    pentru a-i propune sa-i schimbe banii furati, orice suma, si
                    sa faca schimbul la arendasul evreu jefuit, pana cand va
                    reusi sa anunte jandarmilor prezenta lui Lica la carciuma cu
                    bani la el.
                  </p>
                  <p>
                    <strong>XIV. </strong> Lica ii face lui Petrisor, copilul
                    lui Ghita si al Anei, un bici, "iara Ana statea in dosul lui
                    si privea peste umarul lui cum lucreaza, si cum stetea asa,
                    ea se rezema cu dreapta de masa, iara stanga o tinea pe
                    celalalt umar al lui". Vazand scena, Ghita are un mic soc,
                    dupa care se linisteste:"Nu! isi zise el, de asta nu m-am
                    temut niciodata si nu ma tem nici acum". Lica merge din ce
                    in ce mai des pe la Moara cu noroc, dar Ghita "cand vedea pe
                    Lica invartindu-se pe langa Ana ori pa Ana tragandu-se la
                    Lica, el se ducea pe ici incolo, ca sa nu vaza, fiindca ii
                    era greu sa vaza si-l durea inima cand simtea cum Ana
                    scapata din ce in ce in gandul sau". La rugamintea soacrei,
                    Ghita ii trimite pe ai sai de Pasti la sora Anei din Ineu,
                    dar Ana refuza sa plece fara sotul ei, ramas pentru o
                    intalnire cu Lica.
                  </p>
                  <p>
                    Inainte de a pleca cu copiii la Ineu, batrana ii avertizeaza
                    pe soti:"Ii bine sa-i crezi pe oameni buni, fiindca asa te
                    bucuri mai mult de dansii; Lica e insa om rau din fire."
                    Profitand de sosirea unor tigani lautari, Lica organizeaza o
                    petrecere in timpul liturghiei, iar Ana joaca cu placere cu
                    el; apoi Lica ii cere lui Ghita sa-l lase singur cu ea.
                    Carciumarul accepta pentru a chema jandarmii de la Ineu spre
                    a-l surprinde pe Lica cu banii din jafuri asupra sa, caci
                    adusese o parte din ei pentru a-i schimba Ghita. Lica isi
                    motiveaza cererea prin faptul ca doreste sa il lecuiasca pe
                    carciumar de dependenta sa de femeie:"-Are sa-ti fie greu
                    acu o data, urma Lica, de aici inainte esti lecuit pe
                    vecie". Dupa purcederea lui Ghita, Ana se da Samadaului dar
                    se motiveaza:"Tu esti om, Lica, iara Ghita nu e decat o
                    muiere imbracata in haine barabatesti, ba chiar mai rau
                    decat asa".
                  </p>
                  <p>
                    <strong>XV. </strong> Lica pleaca de la Moara, iar Ana ii
                    cere sa o ia cu el, ceea ce Samadaul refuza. Pentru ca
                    incepuse sa ploua pe drum Lica se adaposteste in biserica de
                    la marginea satului Fundureni. In bisetrica are revelatia
                    atotputerniciei lui Dumnezeu, a faptului ca "este o putere
                    tainica ce lucreaza prin oameni si le lumineaza mintile, ca
                    toate vin de la aceasta putere, pe care nimic nu o
                    covarseste"; dupa aceasta observa ca si-a uitat chimirul cu
                    bani la carciuma si rosteste:"unul dupa altul, om cu om toti
                    trebuie sa moara, toti care ma pot vinde, viata cu viata
                    trebuie sa se stinga, caci daca nu-i omor eu pe ei, ma duc
                    ei pe mine la moarte".
                  </p>
                  <p>
                    <strong>XVI. </strong>Ghita, Pintea, Marti si alti doi
                    jandami sosesc la Moara cu noroc, si se opresc pe un deal
                    inaintea ei; vazandu-l pe Lica plecand, pornesc toti in
                    urmarirea lui, cu exceptia lui Ghita, care merge la Moara,
                    cu intentia de a-si ucide sotia. Inainte de a o injunghia,
                    Ghita isi precizeaza vina:"Acu vad ca am facut rau si (...)
                    ca eu te-am aruncat ca un ticalos in bratele lui, pentru ca
                    sa-mi astampar setea de razbunare". Sosesc Lica si Raut, iar
                    acesta din urma il impusca pe Ghita, care era aplecat asupra
                    Anei. Cu ultimele forte, "Cand Lica se apleaca asupra ei ea
                    tipa dezmierdat, ii musca mana si isi infipse ghearele in
                    obrajii lui, apoi cazu moarta langa sotul ei"; apoi Lica
                    pleaca spre Fundureni, cerandu-le tovarasilor sai, Raut si
                    Paun sa dea foc Morii in urma lui. Pe drum insa calul sau se
                    prabuseste de oboseala. Lica il abandoneaza dar, inconjurat
                    de jandarmi, se sinucide izbindu-se, din fuga, cu capul de
                    un copac. Mama Anei, sosita la carciuma dupa incendiu, isi
                    spune ca totul se datoreaza unui fulger ce a aprins casa.
                  </p>
                </div>

                <div class="section">
                  <span className="title">​Limbajul artistic al nuvelei:</span>
                  <p>
                    Se caracterizeaza prin apropierea de graiul vorbit, prin
                    sobrietatea stilului si prin concizia clasica a exprimarii.
                    Regionalismele prezente in textul operei si in replicile
                    personajelor dau autenticitate faptelor si accentueaza
                    specificul romanesc al operei. Sadismul lui Lica este
                    realizat magistral prin procedeele artistice ale
                    introspectiei psihologice, prin comentariile pe care le face
                    naratorul, prin interventiile sale in desfasurarea actiunii,
                    Slavici construind unul dintre primele personaje realiste
                    puternic individualizate din literatura romana, prin cruzime
                    si satanism.
                  </p>
                </div>

                <div class="section">
                  <span className="title">​Caracterizarea personajelor:</span>
                  <p>
                    <strong>Lica Samadau:</strong> Lica Samadaul, personaj
                    principal negativ, este, totodata, un personaj realist si
                    "rotund" ("care nu poate fi caracterizat succint si exact",
                    E.M.Forster), un spirit satanic ce exercita asupra
                    celorlalte personaje o dominatie fascinanta. Personalitatea
                    lui se contureaza din lumini si umbre ca intr-un joc al
                    oglinzilor paralele si este foarte minutios si atent
                    construita de narator. Slavici sugereaza inca de la inceput
                    trasaturile dominante ale caracterului lui Lica Samadaul,
                    "porcar si el, dar om cu stare care poate sa plateasca
                    grasunii pierduti ori furati [...] e mai ales om aspru si
                    neindurat [...] care stie toate infundaturile, cunoaste pe
                    toti oamenii buni si mai ales pe cei rai", de teama caruia
                    tremura toata lumea si care "stie sa afle urechea grasunului
                    pripasit chiar si din oala de varza". [Cuvantul "samadau"
                    numeste o persoana care tine socotelile cuiva, contabil;
                    supraveghetor peste porcari. Portretul fizic al personajului
                    este detaliat, Lica era "un om de 36 de ani, inalt, uscativ
                    si supt la fata, cu mustata Iunga, cu ochii mici si verzi si
                    cu sprancenele dese si impreunate la mijloc". Desi tot
                    porcar, Samadaul purta "camasa subtire si alba".
                  </p>
                  <p>
                    De la prima aparitie, Lica se comporta ca un stapan absolut
                    peste oameni si locuri, autoritar, apoi se prezinta cu
                    cinism si brutalitate: "Eu sunt Lica Samadaul, multe se zic
                    despre mine si multe vor fi adevarate si multe scornite
                    [...] de la mine nimeni nu cuteaza sa fure, ba sa-l fereasca
                    Dumnezeu pe acela care as crede sa-l pot banui. Eu voiesc sa
                    stiu totdeauna cine umbla pe drum, cine trece pe-aici, cine
                    ce zice si cine ce face si voiesc ca nimeni afara de mine sa
                    nu stie.
                  </p>
                  <p>
                    Cred ca ne-am inteles!". Construit ca simbol al degradarii
                    morale, insusirile personajului se desprind in mod indirect
                    din atitudinile si faptele acestuia. Lica se conduce dupa
                    legi proprii, altele decat cele ale statului si profita cu
                    inteligenta de protectia oferita de stapanii turmelor de
                    porci, care intervin in favoarea lui, chiar daca este
                    suspectat de jaf si crima: "Daca Lica e vinovat ori nu, de
                    asta nu prea era vorba: la urma urmelor, parca toti ucigasii
                    se pedepsesc!? Mai ales cand oameni prea cumsecade au
                    trebuinta de dansii". Bun cunoscator de oameni, Lica mizeaza
                    pe patima lui Ghita pentru bani, exercita asupra celorlalti
                    si mai ales asupra Anei o fascinatic diabolica, desi aceasta
                    il caracterizeaza in mod direct "om rau si primejdios",
                    avertizandu-si sotul ca este periculos, insusire desprinsa
                    indirect din ceea ce "se vede din ochii lui, din ranjetul
                    lui si mai ales din cautatura ce are, cand isi roade mustata
                    cu dintii." Crima, inselaciunea, furtul sunt cateva dintre
                    faptele savarsite de Lica si au efecte devastatoare asupra
                    celorlalte personaje, pe care le domina cu cinism si
                    satanism. El ii dezvaluie lui Ghita cum a ucis primul om,
                    apoi pe al doilea, dupa care acum simte chiar "placerea de a
                    lovi pe omul care te supara, de al lovi tare ca sa-l sfarami
                    cand te-a atins cu o vorba ori cu o privire, de a raspiati
                    insutit si inmiit."
                  </p>
                  <p>
                    Dupa uciderea lui Ghita, Lica porunceste sa dea foc hanului
                    Moara cu noroc si, ascunzandu-se de jandarmul Pintea,
                    alearga cu disperare prin padurea din apropierea carciumii,
                    priveste "imprejurul sau, isi tinti ochii la un stejar uscat
                    ce stetea la departare de vreo 50 de pasi, scrasni din
                    dinti, apoi isi incorda toate puterile si se repezi inainte.
                    Pintea il gasi cu capul sfaramat la tulpina stejarului."
                    Sinuciderea lui Lica este semnificativa pentru forta lui
                    interioara si nemasuratul lui orgoliu, care-i dau taria
                    extraordinara de a-si zdrobi capul de tulpina stejarului,
                    preferand acest sfarsit violent decat sa cada viu in mainile
                    lui Pintea. El moare la fel cum a trait, dovedind aceeasi
                    cruzime extraordinara si aceeasi hotarare de neclintit.
                  </p>
                  <p>
                    Bun cunoscator al psihologiei umane, al randuielilor rurale,
                    al datinilor, obiceiurilor si superstitiilor, Ioan Slavici
                    este neindurator cu cei care se abat de la principiile
                    fundamentale ale moralei si-si pedepseste personajele
                    proportional cu greselile savarsite de acestea. Focul din
                    finalul nuvelei este sugestiv pentru nevoia de a fi
                    purificat locul acela de toate relele ce se infaptuisera la
                    carciuma Moara cu noroc.
                  </p>
                  <p>
                    <strong>Ana: </strong>Ana este un personaj principal toate
                    actiunile ei fiind de fapt reactii la faptele lui Ghita. Ea
                    este descrisa de-a lungul operei epice atat in mod direct de
                    catre narator cat si de alte personaje, precum mama sa, cat
                    si in mod indirect prin intermediul faptelor si a vorbelor
                    sale.
                  </p>
                  <p>
                    Portretul ei fizic este realizat in mod direct de catre
                    narator, care pe langa trasaturi fizice ii dezvaluie si
                    cateva insusiri morale (Ghita priveste in jursi inima ii
                    radea cand Ana cea inteleapta si asezata deodata-si pierde
                    cumpatul si se arunca rasfatata asupra lui, caci Ana era
                    tanara si frumoasa, Ana era frageda si subtirica, Ana era
                    sprintena si mladioasa).Cu toate ca ea era inteleapta, era
                    si naiva, inocenta ca un copil si timida, de aceea prima sa
                    intalnire cu Lica Samadaul o impresioneaza profund (Ana
                    ramase privin ca un copil uimit la calaretul ce statea in
                    fata ei Ana nu putea sa-l vaza in ochi). Posedand intuitia
                    specifica oricarei femei, ea isi da seama destul de repede
                    de firea ticaloasa a lui Lica, pe care o percepea in
                    ranjetul,in cautatura Samadaului, ori in felul in care
                    acesta isi rodea mustata cu dintii si il previne pe Ghita sa
                    se fereasca de acest om rau.
                  </p>
                  <p>
                    Pe masura ce Ghita se indeparteaza de ea, temerile ce se
                    desteptara peste noaptein sufletul ei nu mai slabeau si,desi
                    ingrijorata pentru sotul ei, fiind o femeie supusa ce nu
                    dorea sa-si supere sotul,tacea (Ana simtea ca barbatul ei
                    s-a schimbat tine mai putin la nevasta si la copii:. dar el
                    tacea si asa ca nu indraznea sa-l supere ci se intreba mereu
                    ce-o fi avand sotul ei, Ana ar fi vrut sal intrebe insa ea
                    nu mai indraznea sa-i vorbeasca dezghetat ca mai nainte,
                    cacise temea ca nu cumva el sa se supere pe dansa) Actiunile
                    lui Ghita ajung sa o ingrozeasca pe Ana, ba chiar sa o
                    raneasca profound sis a o aduca pe aceasta la capatul
                    disperarii (Ana jalindu-se!, striga Ana speriata, Ana se
                    necajea, temandu-se ca nu cumva copilul sa cada ori cainele
                    sa-l muste, Ana tacu, dar ochii I se umplura de lacrami,
                    fiindca vorbele I se pareau grele). Cu cat Ghita devena mai
                    distant fata de Ana si cand pleaca de acasa fara sa o anunte
                    motivul pentru care a plecat, aceasta isi pierde stapanirea
                    de sine si chiar demnitatea, rugandu-l pe Lica s-o ia cu el
                    (Daca te duci si te duci, ia-ma si pe mine: nu vreau sa-l
                    mai vad).
                  </p>
                  <p>
                    Motivul pentru care Ana a ramas cu Lica de Paste a fost
                    razbunarea, ea crezand ca Ghita n-o mai iubeste si
                    simtindu-se jicnita de nepasarea lui. Pe moarte, aflata in
                    bratele celui vinovat de destramarea casniciei si a vietii
                    sale, intr-o incercare disperata de a lasa dovada unei
                    intamplari violente pe chipul Samadaului, ea da dovada de un
                    ultimo moment de luciditate. Prin aceasta actiune, ea isi
                    dovedeste inca o data dispretul pe care l-a simtit fata de
                    Lica de la bun inceput. Ca si Ghita, Ana isi pierde treptat
                    inocenta initiala fiind orbita de incertitudinea fata de
                    sentimentele sotului ei si dorinta de a se razbuna pe
                    acesta.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
