import React, { Component } from "react";
import { Layout } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class AlexandruLapusneanu extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Alexandru Lăpusneanu</h1>
              <h4>Costache Negruzzi</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section">
                <span className="title">Definiția nuvelei istorice:</span>
                <p>
                  Specie a genului epic în proză cu dimensiuni mai mari în
                  comparație cu cele ale schiței și povestirii,dar mai reduse
                  decât ale romanului,avend un singur fir epic,conflict puternic
                  și personaje reliefate.Nuvela este istorică doarece se evocă o
                  imagine a trecutului(cea a lui Lăpușneanu), autorul cercetează
                  istoria Moldovei în vederea realizării nuvelei.
                </p>
              </div>
              <div class="section">
                <span className="title">Trăsături:</span>
                <p>
                  Opera lui Negreuzzi este că specie o nuvelă istorică deoarece
                  narațiunea are un caracter obiectiv fără intervenții
                  subiective din partea naratorului, nararea se face la persoana
                  a treia naratorul fiind sobru,omniscient, omniprezent, detașat
                  și predominant obiectiv.
                </p>

                <p>
                  Faptele prezentate sunt verosimile: autorul povestește în fond
                  istoria Moldovei făcând anumite schimbări și adaugant elemente
                  imaginative. Are un număr restrâns de personaje: Alexandru
                  Lăpușneanu, Ruxandra, Motoc, Spancioc, Stroici etc.
                </p>
              </div>
              <div class="section">
                <span className="title">Apariție:</span>
                <p>
                  Nuvela istorică "Alexandru Lăpușneanu" a apărut în anul 1840
                  și a fost publicată la Iași în revista "Dacia literară".
                </p>
              </div>
              <div class="section">
                <span className="title">Titlul:</span>
                <p>Titlul este omonim cu personajul principal al nuvelei.</p>
              </div>
              <div class="section">
                <span className="title">Tema:</span>
                <p>
                  Tema nuvelei este dată de evocarea unei domnii zbuciumate din
                  medievalitatea românească și anume întoarcerea lui Lăpușneanu
                  pentru cea de-a doua domnie (1564-1569).
                </p>
              </div>
              <div class="section">
                <span className="title">Viziunea despre lume a autorului:</span>
                <p>
                  În ceea ce privește viziunea despre lume a autorului, nuvela
                  de inspirație istorică aduce momentul apariției nu un model de
                  patriotism,ci un antimodel de conducător, ca un avertisment
                  adresat contemporanilor într-o perioadă de efervescență
                  revoluționară.
                </p>
              </div>

              <div class="section">
                <span className="title">Perspectiva narativă:</span>
                <p>
                  Nararea se face la persoana a treia,naratorul fiind
                  omniscient,omniprezent.
                </p>
              </div>

              <div class="section">
                <span className="title">Compoziția operei:</span>
                <p>
                  Nuvela este clasică, concisă și sobră având 4 capitole fiecare
                  purtant câte un moto reprezentativ, care sintetizează
                  conținutul.
                </p>
                <ol type="I">
                  <li>
                    <strong>"Dacă voi nu mă vrei,eu vă vreu!"</strong>{" "}
                    reprezintă răspunsul dat de lăpușneanu soției.
                  </li>

                  <li>
                    <strong>"Ai să dai samă,doamna!"</strong> reprezintă
                    cuvintele jupânesei ai cărei soț fusese ucis de porunca
                    domnitorului. Cuvintele vor deveni obsesia doamnei Ruxandra
                    care îi va cere soțului să înceteze cu omorurile.
                  </li>

                  <li>
                    <strong>"Capul lui Moțoc vrem!" </strong>sunt cuvintele
                    mulțimii adunate în grabă în curțile palatului domnesc.
                  </li>

                  <li>
                    <strong>
                      "De mă voi scula,pre mulți am să popesc și eu!"
                    </strong>{" "}
                    sunt cuvintele lui Lăpușneanu aflat într-un moment de
                    luciditate,trezit din agonie îi amenință pe călugării care
                    l-au popit.
                  </li>
                </ol>
              </div>

              <div class="section">
                <span className="title">Conflictele:</span>

                <p>
                  Conflictele sunt complexe și pun în lumină personalitatea
                  puternică a personajului principal.
                </p>
                <p>
                  Conflictul principal este de natură exterioară, de ordin
                  politic și prezintă lupta pentru putere dintre domnitor și
                  boieri.Cel secundar se dă între domnitor și Motoc,
                  particularizând dorința de răzbunare a domnitorului.
                </p>
                <p>
                  Conflictul social dintre boieri și popor, limitat însă la
                  revolta mulțimii din capitolul 3.
                </p>
              </div>

              <div class="section">
                <span className="title">Relații spațiale și temporale:</span>
                <p>
                  Timpul și spațiul acțiunii sunt precizate și conferă
                  verosimilitate narațiunii,întoarcerea lui Lăpușneanu pentru
                  cea de-a doua domnie (1564-1569).
                </p>

                <p>
                  În primele trei capitole evenimentele se desfășoară îndată
                  după revenirea acestuia la tron,iar în ultimul capitol se
                  trece direct la moarea lui, 4 ani mai târziu. Evenimentele se
                  desfășoară în cetatea hotinului, în curtea domnească și în
                  palatul domnitorului.
                </p>
              </div>

              <div class="section">
                <span className="title">Povestirea:</span>
                <p>
                  <strong>I "Dacă voi nu mă vreți, eu vă vreau"</strong> După ce
                  Iacob Euclid, poreclit Despotul, murise de mâna lui Ștefan
                  Tomșa, iar după ce Alexandru Lăpușneanu fusese învins de două
                  ori, se retrăsese la Constantinopol.
                </p>
                <p>
                  El pleacă spre Moldavia cu gândul de a-și recăpăta tronul cu
                  ajutorul celor 7 mii de spahii și celor 2 voinici Bogdan și
                  Moțoc, postelnicului Veveriță, și spătarului Stroici. Ajuns în
                  Moldova, Lăpușneanu află că nu este dorit nici de popor nici
                  de boieri. Printre acești boieri se află și Moțoc cel care-l
                  vânduse o dată pe Lăpușneanu, cum l-a vândut și pe Iacov
                  Eraclid, după ce aflase de la Alexandru Lăpușneanu că va domni
                  peste Moldova cu sau fără acordul boierilor sau al poporului.
                </p>
                <p>
                  Moțoc încearcă să-l facă să aibă din nou încredere în el, dar
                  subtil Lăpușneanu tăindu-i vorbele mieroase cu zicătoarea:
                  "Lupul părul schimbă dar năravul ba" Lăpușnuanu recunoaște că
                  nu-și va păta sabia cu sângele lui Moțoc, care sub orice formă
                  încearcă să-l ademenească pe Lăpușneanu să aibă încredere în
                  el.
                </p>
                <p>
                  <strong>II "Ai să dai samă doamna"</strong> Tomșa nefiind
                  pregătit să se înfrunte cu Alexandru fuge în Valahia astfel
                  încât Lăpușneanu își recăpătă ușor tronul. Devenind domnitor,
                  poporul spera într-o viață mai bună cu el în timp ce boierii
                  sunt îngroziți. Dar acesta dezamăgește poporul dând foc la
                  toate cetățile Moldovei înafară de Hotin.
                </p>
                <p>
                  Domnitorul luă toate averile boierilor sub tot felul de
                  pretexte, fără ca aceștia să se împotrivească sau să măcar să
                  comploteze împotriva lui căci știau că vor fi omorâți așa cum
                  au fost omorâți și alți boieri Alexandru cunoscan-do pe fiica
                  lui Petru Rareș, Ruxandra se căsătorește cu ea, căsătoria
                  fiind făcută cu ajutorul intereselor comune a celor doi soți
                  Zilele treceau astfel încățintr-o zi pe Ruxandra o opri în
                  stradă o femeie care îi spuse că e văduvă că are 5 copii și că
                  soțul ei ucide fără milă, și pentru faptele rele făcute de
                  soțul ei Ruxandra va plăti.
                </p>
                <p>
                  Aceasta se sperie și îl roagă pe Lăpușneanu să nu mai ucidă pe
                  nimeni. Acesta că drept leac pentru tristețea ei dă un mare
                  ospăț la palat la care erau invitați toți boierii.
                </p>
                <p>
                  <strong>III "Capul lui Motoc vrem"</strong> Dupa ce fusesera
                  anuntati toti boierii de marele ospat ce va fii dat, ei se
                  dusera la mitropolie unde veni si Lăpusneanu pentru a asculta
                  slujba dupa care, Lapusneanu cobora si pupa moastele sfantului
                  Ioan In mitropolie el cuvanta in fata boierilor si a poporului
                  spunand ca este timpul să termine cu toate crimele si că de
                  acum inainte să se aiba cu boierii ca fratii.
                </p>
                <p>
                  Acesta este iertat de boieri si de popor, mai putin de boieri
                  : Spancioc si Stroici acestia nefiind luati in seama. După
                  acel discurs boierii incalecara pe cai si pornira spre palat.
                  In interiorul palatului o masa ca-n povesti astepta Dupa ce se
                  ospatară Lăpusneanu il lovi in cap cu Buzduganul pe boierul
                  Veverita in timp ce acesta se inchină in fata lui.
                </p>
                <p>
                  Dupa aceasta incepe un adevarat razboi intre osteni si boieri
                  ramanand dupa batalie o baie de sange Poporul, stand la poarta
                  palatului striga in gura mare "Capul lui motoc vrem",
                  facandu-l pe acesta vinovat pentru cele intamplate.
                </p>
                <p>
                  Domnitorul dadu norodului ceea ce cereau sub forma de cadou de
                  la domnitor asezand capetele boierilor sub forma de piramida
                  dupa rangul fiecaruia Lapusneanu o chema pe Ruxandra care
                  ingrozita lesina. IV"De ma voi scula, pe multi am sa popesc si
                  eu" Dupa 4 ani de la acel macel, pe tiran il apuca dorul de a
                  ucide asa ca incepu a ucide din oamenii norodului, peste care
                  pusese stapanire.
                </p>
                <p>
                  Dar il chinuia gandul ca nu a petut sa-i omoare pe cei doi
                  boieri:Stroici si Spancioc, pentru asta trebuia sa-i
                  supravegheze, astfel incat se muta in cetatea Hotinului unde
                  se inbolnaveste. Fiind pe patul mortii acesta ceru sa fie
                  popit inainte sa moară, dupa care lesina. Dupa un timp
                  indelungat isi reveni si ceru ca Ruxandra si fiul ei sa fie
                  omorat, cu toate ca avea mustrari de constiinta.
                </p>
                <p>
                  Moartea lui Lăpusneanu pusa la cale de cei 2 boieri care-l
                  otravesc. Trupul sau a fost dus la manastirea Slatina.
                </p>

                <p>
                  <strong>
                    IV"De mă voi scula, pe mulți am să popesc și eu"
                  </strong>
                  După 4 ani de la acel măcel, pe tiran îl apucă dorul de a
                  ucide așa că începu a ucide din oamenii norodului, peste care
                  pusese stăpânire. Dar îl chinuia gândul că nu a petut să-i
                  omoare pe cei doi boieri:Stroici și Spancioc, pentru asta
                  trebuia să-i supravegheze, astfel încât se mută în cetatea
                  Hotinului unde se înbolnăvește.
                </p>

                <p>
                  Fiind pe patul morții acesta ceru să fie popit înainte să
                  moară, după care leșină. După un timp îndelungat își reveni și
                  ceru ca Ruxandra și fiul ei să fie omorât, cu toate că avea
                  mustrări de conștiință. Moartea lui Lăpușneanu pusă la cale de
                  cei 2 boieri care-l otrăvesc. Trupul său a fost dus la
                  mănăstirea Slatina.
                </p>
              </div>

              <div class="section">
                <span className="title">Caracterizarea personajelor:</span>
                <p>
                  <strong> Alexandru Lăpușneanu:</strong> este personajul
                  principal al nuvelei cu același nume de Costache Negruzzi.
                  Nuvela apare pentru prima dată în primul număr al revistei
                  ieșene „Dacia literară” în 1840 sub titlul de „Scene istorice
                  din cronicele Moldaviei”. Ca sursă de inspirație, autorul
                  folosește „Letopisețul țării Moldovei” de Grigore Ureche.
                </p>
                <p>
                  Alexandru Lăpușneanu este un personaj cu rădăcină atestată
                  istoric, individualitate proprie, complex, personaj pozitiv și
                  totodată negativ. După criteriul social și uman, e tipul
                  domnitorului tiran.
                </p>
                <p>
                  După structura afectiv-intelectuală și de caracter are un
                  temperament mobil, sensibilitate nestăpânită. Viziunea asupra
                  realității e pragmatică și realistă. În capitolul I, Negruzzi
                  utilizează dialogul ca procedeu de caracterizare.
                </p>
                <p>
                  Dialogul se bazează pe replici și are scopul de a înviora
                  acțiunea, dar și de a-i da un dramatism uneori. Astfel,
                  personajul își relevă propriile trăsături prin intermediul
                  gesturilor și al gândurilor. Lăpușneanu vorbește în pilde,
                  folosindu-se de exclamații și întrebări retorice.
                </p>
                <p>
                  El știe cum să îndrepte ascuțitul discuției în orice situație.
                  Sintagma „Dacă voi nu mă vreți, eu vă vreu, și dacă voi nu mă
                  iubiþi, eu vă iubesc pre voi, și voi merge ori cu voia, ori
                  fără voia voastră” denotă dorința și voința de a domni,
                  încăpătânare, ambiție, sigurantă și hoțărârea domnitorului.
                </p>
                <p>
                  Dacă în primul capitol caracterizarea și portretul lui
                  Lăpușneanu este scos în evidentă prin intermediul antitezei
                  dintre boieri și Lăpușneanu, în al doilea capitol,
                  caracterizarea lui reiese din antiteză romantică și totodată
                  conflictuală dintre domnița Ruxanda și el.
                </p>
                <p>
                  Domnița Ruxanda e plină de înțelepciune, gingăsie, pe când
                  Lăpușneanu e crud, nemilos ºi fătarnic. Cerința domniței de a
                  înceta omorurile îl înfurie, punând mâna pe jugher. Este o
                  reacție necontrolată a lui, nepotrivită pentru un domnitor.
                </p>
                <p>
                  Prin această acțiune, Lăpușneanu dovedește a fi un om cu o
                  fire impulsivă, obișnuit să judece și să facă dreptate singur,
                  după propriile reguli. Auzind cererea domniței, Lăpușneanu o
                  mustră „pentru vorbele nebune”, dar promite în final că va
                  înceta cu omorurile, însă nu înainte de a-i da leac de frică.
                  În momentul de față, Lăpușneanu încearcă să fie calm și se
                  poartă autoritar fată de soția sa, însă și cinic pentru că în
                  sine leacul promis o va îngrozi.
                </p>
                <p>
                  Faptele lui sunt impresionante prin cruzime și răzbunare:
                  ospătul unde sunt masacrați 47 de boieri. Moțoc e dat pe
                  mâinile mulțimii, ce-l sfâșie de viu pentru ca în final să fie
                  asasinat. Lăpușneanu surprinde caracteristicile boierilor.
                </p>
                <p>
                  Capacitatea de a-și stăpâni impulsurile violente îl ajută să
                  câștige în orice situație. Este expert în manipulare, însă
                  poate fi și tandru
                  <span className="citat">
                    „ o ridică ca pe o până și o pune pe genunchii săi”
                  </span>
                  Reiese că e vorba de un conflict interior.
                </p>
                <p>
                  Alexandru Lăpușneanu nu concepe să trăiască fără putere
                  <span className="citat">
                    "Eu nu sunt călugăr. Sunt domn! Sunt Alexandru-vodă".
                  </span>
                  Începe să-i amenințe pe cei din jur, în momentul în care se
                  trezește în letargie și observă că este călugăr
                  <span className="citat">
                    „M-ați popit voi, dar de mă voi scula pe mulți am să popesc
                    și eu”
                  </span>
                  , și ajunge să-ți amenințe propriul fiul când află că acesta
                  este domn.
                </p>
                <p>
                  Alexandru Lăpușneanu rămâne un personaj bine individualizat,
                  cu lumini și umbre, cu gesturi și cuvinte memorabile conturând
                  personalitatea domnului tiran.
                </p>
                <p>
                  <strong>Doamna Ruxanda:</strong> Fiind o nuvelă, operă
                  literară “Alexandru Lăpușneanul “ are un număr relativ redus
                  de personaje față de roman, dar mai multe decât schiță, iar
                  accentual cade pe caracterizarea personajului principal.
                </p>
                <p>
                  Cu toate acestea o prezență remarcabilă o face și Doamna
                  Ruxanda, soția personajului principal. Ea apare ca personaj
                  secundar și este fiica, soră și soție de domn, simbolizând
                  frumusețea, bunătatea, iertarea și înțelegerea.
                </p>
                <p>
                  Direct, prin descriere, autorul îi evidențiază mai întâi
                  vestimentația aleasă și frumusețea ei vestită, realizându-i
                  portretul fizic :“ Peste zobonul de stofă aurită, purta un
                  benișel de felendras albastru, blănit cu samur, a cărei mânece
                  atârnau dinapoi ; era închisă cu un colan de aur, ce se
                  încheia cu mari paftalme de matostat, împregiurate cu petre
                  scumpe, iar pe gurmanjii ei atârnă o salbă cu multe șiruri de
                  mărgăritar.
                </p>
                <p>
                  Silicul de samur, pus cam într-o parte, era împdobit cu un
                  surguci alb și sprijinit cu o floare mare de smaragde. Părul
                  ei, după moda de atuncea se împărțeaaă despletit pr umerii și
                  pe spatele sale.
                </p>
                <p>
                  Figura ei avea acea frumusețe, care făcea odinioară vestite pe
                  femeile din România. ”Îmbrăcăminta și ținuta ei degajă
                  somptuozitate, fiind la înălțimea rangului său domnesc.
                  Iubitoare și supusă își apreciază soțul căruia îi adresează
                  cuvinte pline de afecțiune și respect : “ bunul meu domn “, “
                  viteazul meu soț “, “ maria ta este puternic “.Fire sensibilă,
                  nu suportă violentele și este hotărâtă să împiedice pe
                  Lăpușneanul de la noi crime, aducându-i diverse argumente : “
                  Ce-ți lipsește măriei tale ? N-ai cu nime război ; țara e
                  liniștită și supusă.
                </p>
                <p>
                  Judeca că după viață este și moarte, și că măria-ta ești
                  muritorși ai să dai sama ! “ Îndârjită în hotărârea ei, nu
                  cedează nici atunci când voievodul se mânie și devine
                  impulsive, și insistă cu orice risc, exprimându-și energic
                  dorința.
                </p>
                <p>
                  Principalul procedeu de caracterizare a ei, folosit de autor,
                  este antiteza cu Lăpușneanul. Dacă acesta este dur, titanic,
                  crud, ipocrit ,impulsive, doamna Ruxanda este o fire angelică,
                  suavă, delicată, sensibilă, sinceră și supusă. Antitezei i se
                  adaugă caracterizarea directă prin descriere, cu ajutorul
                  căreia autorul îi realizează portretul fizic, insistând asupra
                  vestimentației și frumuseții.
                </p>

                <p>
                  Domnița Ruxanda este raportată, totodată, și la celelalte
                  personaje, în afară de Lăpușneanul. Ea reacționează, astfel,
                  în funcție de amenințarea jupânesei cu cinci copii și a
                  îndemnurilor lui Spancioc și ale mitropolitului Teofan.
                </p>

                <p>
                  Tot indirect sunt evidențiate însușirilesale prin intermediul
                  dialogului, care-i relevă și stările sufletești și atitudinea
                  în funcție de împrejurări și de interlocutor.
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
