import React, { Component } from "react";
import { Layout } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class LeoaicaTanara extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Leoaica tânără, iubirea</h1>
              <h4>Nichita Stanescu</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section poezie">
                <p>
                  Leoaica tânără, iubirea <br />
                  mi-ai sărit în faţă. <br />
                  Mă pândise-n încordare <br />
                  mai demult. <br />
                  Colţii albi mi i-a înfipt în faţă, <br />
                  m-a muşcat leoaica, azi, de faţă. <br />
                </p>

                <p>
                  Şi deodata-n jurul meu, natura <br />
                  se făcu un cerc, de-a-dura, <br />
                  când mai larg, când mai aproape, <br />
                  ca o strângere de ape. <br />
                  Şi privirea-n sus ţişni, <br />
                  curcubeu tăiat în două, <br />
                  şi auzul o-ntâlni <br />
                  tocmai lângă ciocârlii. <br />
                </p>

                <p>
                  Mi-am dus mâna la sprânceană, <br />
                  la tâmplă şi la bărbie, <br />
                  dar mâna nu le mai ştie. <br />
                  Şi alunecă-n neştire <br />
                  pe-un deşert în strălucire, <br />
                  peste care trece-alene <br />
                  o leoaică arămie <br />
                  cu mişcările viclene, <br />
                  încă-o vreme, <br />
                  şi-ncă-o vreme... <br />
                </p>
              </div>
              <div class="section">
                <span className="title">Definiția curentului:</span>
                <p>
                  Denuminrea de neomodernism se datorează viziunii moderne a
                  artiștilor abordată mai târziu,în deceniul al 6-lea în secolul
                  XX,când modernismul interbelicse încheiase cu câteva decenii.
                </p>

                <p>
                  Poeții acestei perioade se întorc,după perioada întunecată a
                  realismului socialist,la metafore subtile,la ironie și la
                  temele filosofice ale poeziei interbelice,la
                  mit,intelectualism.
                </p>

                <p>
                  Ei vor revigora poezia,determinând-o să revină la discursul
                  liric interbelic,laformulelede expresie metaforice,la imagini
                  artistice,a reflecției filosofice,dovada că realismul social
                  este părăsit și poezia se liricizează, deschizându-se
                  perspectiva unui nou modernism adică neomodernismul.
                </p>
              </div>
              <div class="section">
                <span className="title">Trăsături:</span>
                <p>
                  Trăsături ale neomodernismului prezente în opera lui Stănescu
                  sunt împărțirea inegală a strofelor : poezia prezintă trei
                  strofe cu câte 6,8 și 10 versuri,textul este scris în vers
                  alb,rima este întâmplătoare și ritmul inegal.Poezia abundă în
                  mărci ale subiectivității : "m-a mușcat".
                </p>

                <p>
                  Modernismul stanescian rezidă în originalitatea
                  comparației-metaforă a iubirii materializată într-o "leoaică
                  tânără",care pândește cu încordare până își atacă victima
                  direct "în față",mușcând aprig cu "colții albi",precum
                  felinele nemiloase,înfricoșătoare.
                </p>
              </div>
              <div class="section">
                <span className="title">Apariție:</span>
                <p>
                  "Leoaică tânără,iubirea" aparține primei etape de creație al
                  celui de-al doilea volum al poetului intitulat "O viziune a
                  sentimentelor" apărut în anul 1964, volum ce abordează
                  sentimentul dragostei văzut ca o stare de grație.
                </p>
              </div>
              <div class="section">
                <span className="title">Tema:</span>
                <p>
                  Tema: pozeiei o constituie "iubirea",privită ca "regina" a
                  sentimentelor și înțeleasă ca stare de grație dar și că forță
                  demiurgică,ce năvălește asemenea unui animal de pradă în
                  spațiul sensibilității poetice,capabil să îl determine pe
                  îndrăgostit să reordoneze universul și să schimbe raportul
                  eului poetic cu lumea exterioară și cu șinele sau totodată.
                </p>
              </div>
              <div class="section">
                <span className="title">Titlul:</span>
                <p>
                  Titlul poeziei este metaforic și simbolic sugerând tema poeiei
                  și anume iubirea.Structural titlul este alcătuit dintr-o
                  apozitie
                </p>

                <p>
                  Titlul este exprimat pintr-o metaforă,care sugerează extazul
                  poetic la apariția neașteptată a iubirii,percepută ca un
                  animal agresiv construcția apozițională "leoaică tânără" este
                  antepusa substantivului "iubirea",care devine simbolul central
                  al textului și despre care prin epitetul "tânăra" se sugerează
                  forța realizării individului prin nașterea sentimentului etern
                  al erosului.
                </p>

                <p>
                  Prin alcătuirea celor trei cuvinte,încercarea poetului este
                  accea de a face cunoscut încă din titlu trăsăturile iubirii:
                  este tânără,instinctuală,sălbatică,feroce,spontană și plină de
                  frumusețe.Se înțelege că "leoaică"/iubirea atacă surprinzător
                  pe neașteptate.
                </p>
              </div>
              <div class="section">
                <span className="title">Compoziția exterioară:</span>
                <p>
                  Compozițional poezia este alcătuită din 3 strofe cu un număr
                  diferit de versuri (6,8,10).Versurile sunt inegale că măsură
                  și rima este accidentală.Structura creează impresia de
                  spontaneitate în dezvăluirea sentimentelor,discursul liric
                  fiind eliberat de toate opreliștile,în afara unui foarte
                  firesc ritm care cadențează mișcarea afectivă.
                </p>
              </div>

              <div class="section">
                <span className="title">Structura de adâncime:</span>
                <p>
                  Discursul liric asemenea strofelor este structurat în 3
                  secvențe :
                </p>
                <p>
                  <strong>Prima secvență</strong> corespunde primei strife.Aici
                  prin intermediul metaforelor,al personificărilor și
                  comparațiilor ne este prezentat modul în care vânătorul își
                  prinde prada.
                </p>
                <p>
                  Sentimentul de iubire i-a forma unei tinere leoaice
                  agresive,care năvălește în sufletul poetului cu o forță
                  devastatoare,sfâșiindu-i cu colții ei albi ființa și
                  provocându-i durere: "Leoaică tânără,iubirea/Mi-ai sărit în
                  fără/Mă pândise-n încordare/Mai demult/Colții albi mi i-a
                  înfipt în față/M-a mușcat leoaica azi de fața". Această
                  apariție feroce îi lasă urme adânci pe fața
                  individului,metafora semnelor semnificând că cel atins de
                  sentiment se diferențiază de ceilalti.
                </p>
                <p>
                  Personificarea iubirii creează sugestia începutului tulburător
                  al dragostei adolescentine,care sperie prinspontaneitatea
                  ei,aventura iubirii nedepinzând de voita celui atacat.Dacă în
                  prima secvență dragostea este văzută din exterior.
                </p>
                <p>
                  <strong>Cea de-a doua secvență</strong> prezentată-n
                  următoarea strofă,dezvăluie iubirea văzută din interior și
                  accentuează efectul psihologic al neașteptatei întâlniri.
                </p>
                <p>
                  De data aceasta individul trece într-o altă dimensiune a
                  existenței,iubirea poziționându-l în centrul Universului.Forța
                  agresivă a iubirii pare să reordoneze lumea după propriile
                  legi,într-un joc al cercurilor concentrice,ca simbol al
                  perfecțiunii,eul liric simțându-se în aceast nou univers un
                  adevărat "Axis Mundi".
                </p>

                <p>
                  <strong>Secvența a treia</strong> (corespunde ultimeu strofe)
                  revine a ideea centrală a poeziei,iubirea,văzută acum ca o
                  forță care pustiește ființa poetului,transformată într-un
                  "deșert în strălucire" peste care "trece alene" puternică
                  "leoaică arămie/cu mișcările viclene".
                </p>

                <p>
                  Mișcările unduioase,lente și inconștiente ale fiarei(ce
                  "alunecă-n neștire")îi fac pe cei doi să se contopească,ființa
                  iubită părând a se transforma într-un deșert infinit peste
                  care trece sentimentul iubirii în ipostaza de "leoaică
                  arămie".
                </p>

                <p>
                  Finalul dă de înțeles faptul că animalul se află iarăși la
                  panda.Dincolo de metafora acestei relaționari se află un
                  adevăr general valabil: indiferent de momentul istoric în care
                  trăiește,individul se va întâlni inevitabil cu sentimentul de
                  iubire,care va acționa asupra lui spontan și impulsiv.
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
