import React, { Component } from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import Home from "./pages/Home";

// Components
import Navbar from "./Navbar";

import { Layout, Input, Icon } from "antd";
import Romana from "./romana/";
import Page404 from "./pages/Page404";

class App extends Component {
  state = {
    userLogged: true
  };

  handleSubmit = e => {
    e.preventDefault();
    if (
      document.getElementById("password").value === "rEVOLUTIE" ||
      document.getElementById("password").value === "invitat47" ||
      document.getElementById("password").value === "MixRadu" ||
      document.getElementById("password").value === "Ciocaniu"
    ) {
      this.setState({ userLogged: false });
    }
  };
  render() {
    if (this.state.userLogged) {
      return (
        <div>
          <form onSubmit={this.handleSubmit}>
            <input id="password" type="password" />
            <button>Intra</button>
          </form>
        </div>
      );
    } else {
      return (
        <div className="App">
          <Layout>
            <Router>
              <Navbar />
              <Route exact path="/" component={Home} />
              <Route path="/romana" component={Romana} />
              <Route path="/istorie" component={Page404} />
              <Route path="/matematica" component={Page404} />
            </Router>
          </Layout>
        </div>
      );
    }
  }
}

export default App;
