import React, { Component } from "react";
import { Layout, Menu, Breadcrumb, Icon } from "antd";
// Ant design
import { Row, Col } from "antd";
export default class Home extends Component {
  render() {
    return (
      <div className="Home">
        <Row className="header">
          <Col className="left" xs={24} sm={24} md={12} lg={12} xl={12}>
            <h1>Noope, asta nu-i pagina oficiala iBac</h1>
            <h4>
              Aceasta pagina este doar pentru cei care ajuta la crearea
              proiectului <strong>iBac</strong>
            </h4>
            <h4>
              Proiectul o sa fie disponibil pe <strong>Web</strong> |{" "}
              <strong>Android </strong>| <strong>iOS</strong>
              <br />
              Continut verificat pentru materile: <strong>Romana</strong> |{" "}
              <strong>Istorie</strong> | <strong>Matematica</strong>
            </h4>
          </Col>
          <Col className="right" xs={24} sm={24} md={12} lg={12} xl={12}>
            <img
              className="responsiveImage"
              src="https://cdn.dribbble.com/users/472667/screenshots/4037893/ui-ux-design-banner.png"
              alt=""
            />
          </Col>
        </Row>
        <Row>
          <Col className="botton" xs={24} sm={24} md={24} lg={24} xl={24}>
            <h1>100.000+</h1>
          </Col>
        </Row>
      </div>
    );
  }
}
