import React, { Component } from "react";
import { Icon } from "antd";
export default class ExtraContent extends Component {
  render() {
    return (
      <div className="extra">
        <button>
          <Icon type="sound" />
          <span>Audio</span>
        </button>

        <button>
          <Icon type="video-camera" />
          <span>Video</span>
        </button>
      </div>
    );
  }
}
