import React, { Component } from "react";
import { Row, Col } from "antd";
export default class Page404 extends Component {
  render() {
    return (
      <div className="page404">
        <Row className="header">
          <Col
            className="left"
            xs={{ span: 24, offset: 0 }}
            sm={{ span: 24, offset: 0 }}
            md={{ span: 12, offset: 6 }}
            lg={{ span: 12, offset: 6 }}
            xl={{ span: 12, offset: 6 }}
          >
            <img
              src="https://maxcdn.icons8.com/app/uploads/2018/11/graphic-design-trends-dribbble.png"
              alt=""
            />
          </Col>
        </Row>
      </div>
    );
  }
}
