import React, { Component } from "react";
import { Layout } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class EnigmaOtiliei extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Enigma Otiliei</h1>
              <h4>George Călinescu</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section">
                <span className="title">Definiția romanului: </span>
                <p>
                  Specie a genului epic, în proză, de mare întindere, având o
                  acțiune complexă desfășurată pe mai multe planuri narative cu
                  un număr mare de personaje.
                </p>
              </div>
              <div class="section">
                <span className="title">Trăsături: </span>
                <p>
                  Opera este că specie un roman deoarece este o structură
                  narativa de mare întindere, subiectul cuprinde două planuri
                  narative paralele ale acțiunii și două nuclee. Acțiunea este
                  ampla și implică un număr mare de personaje cu ponderi
                  diferite în ansamblul epic și diversificate psihologic.
                  Cunoaște o mare varietate de forme și formule epice și tehnici
                  narative.
                </p>
              </div>
              <div class="section">
                <span className="title">Apariția: </span>
                <p>Romanul apare in anul 1938.</p>
              </div>
              <div class="section">
                <span className="title">Titlul: </span>
                <p>
                  Inițial titlul ar fi trebui să fie după Călinescu "Părinții
                  Otiliei", însă editorul l-a numit "Enigma Otiliei" deoarece i
                  s-a părut a fi un nume mai sonor. Acest titlu sugerând
                  imprezivibilul Otiliei, comportamentul ei derutant, uneori
                  absurd, care îl uimește pe adolescentul Felix "nu Otilia are
                  vreo enigma, ci Felix crede că o are."
                </p>
              </div>
              <div class="section">
                <span className="title">Temele: </span>
                <p>
                  Una din temele operei o reprezintă monografia unei familii
                  bucureștene, alte teme fiind viața burgheziei bucureștene de
                  la sfârșitul secolului al XX-lea, moștenirea, relațiile de
                  familie, paternitatea, condiția femeii și căsătoria.
                </p>
              </div>
              <div class="section">
                <span className="title">Perspectiva narativă: </span>
                <p>
                  Evenimentele sunt relatate din perspectiva unui narator
                  heterodiegetic, omniscient, omnipotent și omniprezent.
                  Perspectiva narativă este completată de cea a personajului
                  martor, Felix, prin intermediul căruia sunt introduse în scenă
                  celelalte personaje. Naratorul relatează la persoana treia și
                  pare a nu se implica în narațiune; el știe totul despre
                  personaje, prezintă evenimente pe care nici un martor nu le-ar
                  fi putut vedea, stabilește relații între situații foarte
                  îndepărtate în timp și spațiu, comentează permanent
                  evenimentele, controland traiectoriile existenței personajelor
                  sale. Întâlnim de asemenea, tehnică pluriperspectivismului
                  pentru că fiecare personaj, în momentul în care intră în
                  scenă, este privit din mai multe perspective.
                </p>
              </div>
              <div class="section">
                <span className="title">Compoziția: </span>
                <p>
                  Romanul este alcătuit din 20 de capitole, numerotate cu cifre
                  romane și fără titluri. Romanul este contruit pe două planuri
                  care fuzionează:
                </p>

                <ol>
                  <li>
                    Primul plan prezintă destinul tânărului Felix Sima, care
                    înainte de a-și face o carieră, parcurge o experiența
                    erotică.
                  </li>

                  <li>
                    Alt plan expune istoria moștenirii lui Costache
                    Giurgiuveanu. Acest plan conține două conflicte succesorale.
                    Primul, esențial înscat în jurul averii lui Costache și
                    celălalt destrămarea familia Tulea.
                  </li>
                </ol>
              </div>
              <div class="section">
                <span className="title">Conflictele: </span>
                <p>
                  Conflictele romanului se bazează pe relațiile a două familii
                  înrudite, care sugerează universul social. O familie o
                  formează Costache Giurgiuveanu și Otilia Mărculescu, fiica
                  celei de-a două soții decedate. A două familie, vecină și
                  înrudită o formează Aglae, sora lui Costache, Simion Tulea și
                  și copiii lor: Olimpia, Aurică și Titi. În universul acestor
                  familii intră trei personaje aduse de diferite motive. Primul
                  este Felix Sima, orfan, care după ce termină liceul la Iași
                  vine la București, la unchiul și tutorele sau legal Costache
                  Giurgiuveanu, cu dorința fermă de a urma medicina. STĂNICĂ
                  RAȚIU, avocat fără procese intră în familia Tulea ca soț al
                  Olimpiei, dar gravitează pe rând în jurul averii lui Costache
                  Giurgiuveanu și al zestrei datorată de socrii în vederea
                  realizării unei bune situații materiale. Pe moșierul LEONIDA
                  PASCALOPOL în aduce în familia Giurgiuveanu pe de-o parte
                  afecțiunea pentru Otilia, nedefinit amestec între instincte
                  paterne și suava pasiune, pe de altă parte dorința de a avea o
                  familie care să îi umple singurătatea.
                </p>
              </div>
              <div class="section">
                <span className="title">Relații spațiale și temporale: </span>
                <p>
                  Acțiunea începe într-o seară de la începutul lunii iulie în
                  anul 1909. Că spațiu acțiunea se petrece în strada Antim, pe
                  Calea Victoriei, în casa lui Costache, în casa Aglaei, la
                  Paris, la moșia lui Pascalopol și în apartamentul acestuia
                  etc.
                </p>
              </div>
              <div class="section">
                <span className="title">Povestirea: </span>
                <p>
                  <strong>Capitolul I: </strong>
                  Primul capitol prezintă sosirea tânărului Felix Sima în casă
                  tutorelui său, Costache Giurgiuveanu, unchiul său. Într-o
                  seara la începutul lui iulie 1909, Felix ajunge în casă
                  unchiului său și este întâmpinat cel puțin ciudat de acesta.
                  Verișoara lui, Otilia, o tânără de aceeași vârstă cu el îl
                  primește cu multă căldură și îl conduce pe băiat într-o cameră
                  unde restul familiei juca table. Astfel, Felix îi cunoaște pe
                  Pascalopol, un moșier bogat, prieten de familie (cu care
                  Otilia își permite multe familiarități, spre nemulțumirea
                  băiatului), pe Aglae Tulea, sora lui Costache, Simon Tulea,
                  soțul acesteia și Aurica Tulea, fiica de 30 ani a Aglaei. În
                  timp ce ceilalți jucau table și mai apoi cărți, tânărul Felix,
                  viitor medicinist își aduce aminte de felul în care a ajuns
                  orfan, cum murise mama sa și cum tatăl lui fusese distant de
                  copil, trimițându-l mai mult prin internate. După un timp,
                  Otilia își aduce aminte de Felix și îi aduce două prăjituri
                  (considerând că îi este foame) și îl trimite să doarmă chiar
                  în camera ei, unde Felix observă o dezordine generală.
                </p>
                <p>
                  <strong>Capitolul II: </strong>Când se trezește, Felix o aude
                  pe Otilia cântând la pian și începe să studieze unele părți
                  ale casei. Pe holuri o întâlnește pe Marina, servitoarea casei
                  și Costache îi cere cu împrumut niște bani, dar intervine
                  Otilia care nu admite ca tatăl ei să ceară bani de la “vărul
                  ei”. Fata îi cânta lui Felix și cuprinsă de ceea ce Marina
                  numea “nebunie”, începe să alerge prin grădină. Otilia își
                  mărturisește dorința ca Felix să ajungă un mare doctor și
                  începe să îi prezinte familia, ilustrând calitățile și
                  defectele membrilor familiei Tulea. Totodată, încep să
                  răscolească amintirile, uitându-se prin fotografiile vechi.
                  După amiază, Pascalopol îi scoate pe Felix și pe Otilia la
                  plimbare, spre invidia Aureliei și a Aglaei. Acestea insistă
                  pe lângă Felix să îl mediteze pe Titi, băiatul mătușei sale.
                </p>

                <p>
                  <strong>Capitolul III: </strong>Capitolul începe printr-o
                  scurtă prezentare a lui Simion Tulea, Otilia avertizându-l
                  discret asupra manierelor celor din familia Tulea. Aurelia îl
                  invită pe Felix la ei acasă și în timpul vizitei, o ponegrește
                  pe Otilia, cu invidie ascunsă. Îl cunoaște pe Simion, care îi
                  prezintă tablourile sale, Felix observând că și Titi și tatăl
                  lui au un oarecare talent în ale picturii, deși află că toate
                  tablourile făcute de cei doi sunt simple copii după ilustrate
                  sau cărți. Titi nu se dovește a fi foarte interesat de
                  meditații, plictisindu-se foarte repede. Capitolul prezintă
                  aspirațiile familiei Tulea și dorința arzătoare a Aureliei de
                  a se mărita. Aurica începe să se familiarizeze din ce în ce
                  mai mult cu Felix cu dorința ascunsă de a-l face pe acesta să
                  o ia de nevastă; speranțele pe care și le face Aurica
                  speriindu-l pe tânăr, acesta care începe să o e evite.
                </p>
                <p>
                  <strong>Capitolul IV: </strong>Cu timpul, Felix se obișnui în
                  casa lui Costache, acomodându-se cu ciudățeniile locului.
                  Având nevoie de bani, îi cere bătrânului său tutore însă
                  acesta își dovedește avariția fără să îi dea vreun ban, cea
                  care îi dă bani fiind Otilia. Capitolul prezintă vizita celor
                  doi la casa lui Pascalopol, unde Felix observă un rafinat gust
                  în decorațiuni interioare. Deși la început Felix nu îl
                  agreează pe Pascalopol, considerând că Otilia nu ar trebui să
                  se poarte așa familiar cu un bărbat mai în vârstă ca ea,
                  moșierul îi devine treptat simpatic.
                </p>
                <p>
                  <strong>Capitolul V: </strong>În august, este anunțată sosirea
                  lui Stănică și a Olimpiei, cealaltă față a Aglaei. Otilia ii
                  explică scandalul produs de relația dintre cei doi, care nu
                  erau căsătoriți, fiindcă Stănică nu dorea să facă nunta dacă
                  Olimpia nu primea zestre, aceasta fiindu-i refuzată de Simion.
                  Ajunși la momentul cinei, cei doi încearcă să îl înduplece pe
                  Simion să le dea casa, acesta însă refuza motivând că Olimpia
                  nu e fața lui, deși Felix observă o asemănare izbitoare între
                  tată și fiica. După plecare, Stănică îi cere Otiliei împrumut
                  20 franci. De asemenea, Pascalopol primește un bilet de la
                  Stănică Rațiu prin care acesta îi cere 100 franci. De dragul
                  Otiliei, moșierul ii da avocatului banii ceruți. Capitolul
                  prezintă modul în care Stănică încearcă să îl convingă pe
                  Simion și modalitatea de ai păcăli pe ceilalți, simulând o
                  boală de inimă.
                </p>
                <p>
                  <strong>Capitolul VI:</strong> In acest capitol este
                  prezentata vizita lui Felix si a Otiliei la mosia lui
                  Pascalopol, mosie situata la jumatatea distantei intre
                  Calarasi si Fetesti. La mosie, Felix observa acelasi
                  rafinament, de aceeasta data mai rustic si Pascalopol ii
                  devine din ce in ce mai familiar, ajungand sa il considere un
                  prieten. In timpul vizitei, Otilia si Felix invata sa
                  calareasca, se urca pe capitele de fan, petrecand doua
                  saptamani de vis. Totodata, din ziar, cei trei afla de cununia
                  dintre Olimpia si Stanica dar si de moartea copilului lor de 2
                  luni (moarte cauzata de neglijenta parintilor).
                </p>
                <p>
                  <strong>Capitolul VII: </strong>Acum Felix era student în anul
                  I la Medicină, spre nemulțumirea Aglaei care îl îndemna să
                  “își găsească un rost” în acest fel încercînd să îl jignească
                  pe tânăr. Stănică încearcă să creeze intrigi între Otilia și
                  Pascalopol, dovedind un caracter infect. Otilia îi propune
                  tatălui ei să se mute într-o altă casă din Știrbey-Vodă, însă
                  Aglae se opune, arătând felul în care Costache era manipulat
                  de sora sa. Totodată, Titi începe să se simtă atras de Otilia,
                  fața de care începe să aibă un comportament necuviincios,
                  Otilia îndepărtându-l de ea, făcând-o pe Aglae să se plângă
                  lui Costache, rostind cuvinte grele la adresa feței. Otilia îl
                  pune pe Costache să alegă între ea și Aglae, și astfel mătușa
                  să nu mai calcă în casa fratelui său. Stănică aduce un fals
                  doctor la Costache cu scopul ascuns de a-l face pe acesta să
                  creadă că este bolnav. Pascalopol, Otilia și Felix hotărăsc să
                  fie mai atenți la comportamentul avocatului.
                </p>
                <p>
                  <strong>Capitolul VIII: </strong>Felix își dă seama că o
                  iubește pe Otilia și încearcă să fie din ce în ce mai aproape
                  de față, considerând chiar și unele gesturi mărunte (cum ar fi
                  coaserea unui nasture) un adevărat deliciu. Îi scrie o
                  scrisoare de dragoste, pe care i-o lăsă pe pat, însă față pare
                  să nu o fi citit. După un timp îi mărturisește dragostea și
                  Otilia îi spune ca și ea îl iubește, însă trebuie să aștepte.
                  La rugămintea lui Felix, Otilia nu îl mai primi un timp pe
                  Pascalopol, acesta purtând o discuție cu studentul. În timpul
                  acestei discuții, Felix își dă seama de caracterul minunat al
                  moșierului, rugând-o pe Otilia sa îl primească din nou pe
                  Pascalopol. În același timp, Titi face cunoștință cu Ana, sora
                  unui coleg și se căsătorește cu aceasta, căsnicia eșuând în
                  mod lamentabil.
                </p>
                <p>
                  <strong>Capitolul IX: </strong>In martie, Felix incepe sa-si
                  faca planuri mari, dorind sa aiba cat mai multi bani, pentru
                  a-i putea oferi Otiliei placeri ca plimbatul cu trasura. Avand
                  nevoie de bani, completeaza batranului o polita cu scadenta in
                  alb de 1000 franci. Pascalopol incearca sa il convinga pe
                  Costache sa o adopte legal pe Otilia, oferindu-se sa plateasca
                  el cheltuielile, batranul acceptand. Dar Stanica afla de acest
                  lucru, si dupa cateva saptamani de discretie, lanseaza stirea
                  si Aglae jura ca Otilia nu va fii adoptata cat timp ea
                  traieste. Stanica ii face avansuri Otiliei care il da afara,
                  manioasa. Otilia ii povesteste cum Aglae si Aurica au incercat
                  mereu sa o umileasca, spunandu-i cuvinte grele la o varsta
                  frageda. Afland de vorbele care s-au zis despre ea cand Aglae
                  a aflat de intentia lui Costache de a o adopta, Otilia
                  incuviinteaza.
                </p>
                <p>
                  <strong>Capitolul X: </strong>După câteva zile de la acest
                  incident, Costache primește un bilet anonim, prin care este
                  sfătuit să nu o adopteze pe Otilia, folosind imaginea Aglaei
                  (care avea o înrâurire asupra bătrânului), anonimă reușind să
                  îl sperie pe bătrân. Otilia, aflând de bilet și citindu-l, îl
                  liniștește pe Costache și arată că ea nu dorește neapărat să
                  fife adoptată, iubirea ei pentru bătrân fiind la fel de mare
                  cu sau fără acte. Felix de întâlnește pe stradă cu Aurica,
                  aceasta începând să-l elogieze pe Titi, spre disperarea
                  medicinistului. Ajuns acasă, află că Otilia plecase cu
                  Pascalopol la moșie. Stănică i-o prezintă pe Georgeta, o
                  prostituată de lux, care ii atrage atenția tânărului, el
                  primind invitația de a o vizita pe față oricând.
                </p>
                <p>
                  <strong>Capitolul XI: </strong>Starea de sănătate a lui Simion
                  se înrăutățește, bătrânul acuzând tulburări psihice, însă
                  doctorul adus de Stănică nu îi găsește nimic, familia crezând
                  că are doar o criză de glicozurie. Stănică atrage atenția
                  asupta pericolului pe care îl reprezintă Otilia pentru
                  moștenirea lui Costache și le împinge pe cele două femei și
                  mai mult împotrivă tinerei. Felix începe să se remarce la
                  Universitate, și datorită interesului său, primește
                  permisiunea de a intra în orice secție a spitalului, pentru a
                  face observații. Deși unii interni încearcă să îl descurajeze,
                  Felix realizează o lucrare pe care profesorul său i-o publică
                  într-o revistă de mare interes în Franța, reușind să îi
                  mărească ambiția lui Felix. Stănică reușește să îl manipuleze
                  pe Costache, manevrând lucrurile în așa fel încât Costache să
                  nu facă testament. Felix se duce să o viziteze pe Georgeta,
                  care îi povestește drama vieții sale. Felix simte pentru față
                  o afecțiune de frate și între cei doi începe să se înfiripe o
                  relație, deși Felix o iubea foarte mult pe Otilia.
                </p>
                <p>
                  <strong>Capitolul XII: </strong>Felix primește o carte poștală
                  de la Otilia, tocmai din Paris, dându-i tânărului o stare de
                  tristețe. Georgeta îi roagă să treacă pe la Iorgu, chiriașul
                  lui Costache. Îi scrie înainte de această vizită Otiliei, unde
                  își exprimă dezamăgirea. Felix află de la Iorgu de manevrele
                  lui Stănică, acesta plănuind să-și strice afacerile lui
                  Costache pentru ca acesta să nu vândă imobilele. Costache și
                  Felix pornesc spre restaurantul lui Iorgu unde cei doi încheie
                  afacerea, spre ciuda lui Stănică. Simion este din ce în ce mai
                  bolnav, însă Aglae și Olimpia sunt cât se poate de
                  nepăsătoare, stârnind chiar repulsia lui Stănică. Felix devine
                  din ce în ce mai convins de tulburările psihice ale lui
                  Simion.
                </p>
                <p>
                  <strong>Capitolul XIII: </strong>La începutul capitolului este
                  prezentată o scenă din viața maritală a cuplului
                  Stănică-Olimpia, ilustrându-se lipsa de suflet a celor din
                  familia Tulea. Felix află de la Georgeta că Titi se
                  îndrăgostise de ea, dorind să o ia de soție, aceasta având o
                  obsesie maritală. Când pleacă de la Georgeta, se întâlnesc cu
                  generalul, scena care i se pare lui Felix, foarte penibilă.
                  Costache vrea să își vândă proprietățile pentru a-i da Otiliei
                  ce i se cuvine. Alarmați de așa o posibilitate, Stănică are
                  ideea să-i trimită Otiliei o carte poștală și să se prefacă
                  a-i fi prieteni.
                </p>
                <p>
                  <strong>Capitolul XIV: </strong>Felix, un pic supărat din
                  cauza indiferenței colegilor de la Universitate, își aduce
                  aminte de discuțiile pe care le avea cu colegii de la internat
                  în Iași. Se întâlnește cu Weissmann, un prieten și coleg care
                  îi ține o prelegere despre poezie și dragoste, ațâțându-i
                  curiozitatea pentru un roman de Weininger. Costache îl roagă
                  pe Felix să nu iasă din camera sa, fiindcă avea de făcut niște
                  presupuse reparații, însă Felix își dă seama că bătrânul avea
                  de fapt ascuns în podeaua casei o adevărată comoară, desigur
                  nu acordă multă atenție acestui amănunt. Costache,
                  Felix,Weissmann, Stănică, Olimpia, Georgeta și generalul,
                  Iorgu și soția acestuia iau cina împreună la restaurantul
                  celor din urmă, unde Stănică încearcă să manevreze pe bătrân,
                  povestindu-i o întâmplare cu un avar care își îngropase toți
                  banii în subsol și care la un incendiu, i-a pierdut pe toți.
                  Felix este condus acasă de general și Georgeta, simțindu-se
                  din nou foarte penibil.
                </p>
                <p>
                  <strong>Capitolul XV: </strong>Aglae decide să îl interneze pe
                  Simion, deși consideră că acesta nu are nici o avere, cu toate
                  că bătrânul fusese privat de pensia sa și de orice alte
                  plăceri. Weissmann vine și îl face pe simion să vină cu el,
                  spunându-i că oamenii așteaptă învățăturile sale (Simion se
                  credea de un timp Iisus Hristos). Stănică încearcă să îl facă
                  pe Costache să o primească din nou pe Aglae, însă acesta este
                  împotriva ideii, Felix găsindu-l cotrobăind prin casă.
                  Avocatul îi aduce acasă niște studenți pe care nu îi cunoaște,
                  purtându-se foarte familiar, stârnind indignarea tânărului și
                  a unchiului său.
                </p>
                <p>
                  <strong>Capitolul XVI: </strong>Capitolul începe cu prezentare
                  lui Stănică și a familiei acestuia, ilustrându-se unitatea și
                  legăturile strânse care uneau pe membrii familiei, fie ei de
                  condiții sociale diferite. Cu ocazia aniversării mătușii
                  Agripina, Stănică îi face acesteia o vizită, găsind în modestă
                  ei camera o mulțime de rude venite în vizită. Ii cumpără
                  bătrânei alune și covrigi, și “împrumută” de la această patru
                  cești chinezești. Vărul său Toader îl duce acasă și cu aceasta
                  ocazie, Stănică observă frumusețea nepoatei sale Lili, căruia
                  ii promite că o va prezenta unui băiat “fain”, Felix. Acasă,
                  ii spune Olimpiei că îi cumpărase patru cești de la un
                  anticar, aducându-i și prăjituri, spre deliciul femeii. Cu
                  toate acestea, Stănică nu primește dovezi de iubire din partea
                  nevestei sale, fapt care îi întărește o decizie de mult luată,
                  și aceea că va divorță cât mai repede și cum se va ivi
                  împrejurarea prielnică.
                </p>
                <p>
                  <strong>Capitolul XVII: </strong>Trezindu-se, Felix aude pe
                  cineva cântând la pian și își dă seama că Otilia se întorsese.
                  Fata îi dă cadou cravate, însă pe dată se afișează Stănică.
                  Acesta o aduce cu sine pe Aurelia, căreia Otilia îi face cadou
                  o sticluță de parfum, un guler scrobit de broderie, uitând
                  parcă toate faptele urâte ale verișoarei sale. Pascalopol vine
                  să ii viziteze, cu această ocazie, între el și Felix ivindu-se
                  un ușor conflict, rezolvat ulterior. Pascalopol îl cheamă pe
                  Felix să discute “despre o persoană importantă pentru amândoi”
                  și îl invită pe tânăr la el acasă. Îi aduce la cunoștință că
                  Stănică creează zvonuri care mai care mai incredibile și mai
                  defăimătoare, avocatul spunându-i lui Felix ca motivul
                  călătoriei la Paris fusese de fapt un avort. Costache începe
                  să construiască o casă pentru Otilia. Felix îi este prezentat
                  lui Lili, care devine foarte interesat de student, spre
                  invidia celor din familia Tulea.
                </p>
                <p>
                  <strong>Capitolul XVIII: </strong>Într-o zi foarte călduroasă,
                  Costache se împletici din mers și căzu moale jos. Aflând de
                  subitul atac al fratelui ei, Aglae, sperând într-o apropiată
                  moarte a acestuia se grăbește să cheme doctorul și merge la
                  Costache acasă. Deși bătrânul nu avea nimic, Aglae și Stănică
                  fac tot ceea ce pot pentru a-l aduce într-o stare gravă,
                  mâncând de față cu el toate bunătățile strânse cu greu de
                  bătrân, spre durerea acestuia. Felix îl roagă pe Pascalopol să
                  îi ajute, iar acesta aduce un doctor de renume, Stratulat și
                  acesta observă că bătrânul nu are nimic, însă un la un alt
                  atac nu ar mai supraviețui. Costache îi zice în secret lui
                  Pascalopol că îi va da trei sute de mii de lei pentru Otilia,
                  să îi pună într-un cont, banii fiind ascunși sub pat. Deși își
                  anunță aceasta dorință, amână momentul în care îi va da banii
                  moșierului și Pascalopol deschide un cont în care îi virează
                  Otiliei o sută de mii de lei.
                </p>
                <p>
                  <strong>Capitolul XIX: </strong>Aglae vine și îi povestește
                  bătrânului de moarte ceea ce îl dispera pe acesta, Costache
                  începând să meargă la doctori, să ia medicamente și să aducă
                  părintele în casă pentru a binecuvânta casa și pentru a alunga
                  spiritele rele. În acest scop este chemat popa Țuică
                  (interesat mai mult de țuică decât de treburile bisericești).
                  Weissmann vine să îi facă un control lui Costache, cu această
                  ocazie cunoscând-o pe Aurica, fata începând să aibă o pasiune
                  pentru tânăr. Cum Weissmann era evreu, fata se duce la popa
                  Țuica care îi dă dezlegare. Aurica îi sugerează tânărului o
                  legătură, pe care acesta ghicind-o, o înlătură, spunând că “nu
                  profesează decât amorul liber”.
                </p>
                <p>
                  <strong>Capitolul XX: </strong>Cum Lili se arată foarte
                  încântată de Felix, și cum acesta nu afișa nici un interes
                  pentru față, Stănică îi spune Otiliei despre o presupusă idilă
                  între ei, rugând-o să îl convingă pe medicinist să o ia pe
                  lili de nevastă. Felix îi mărturisește că Lili îi este
                  indiferentă, declarându-i din nou iubirea sa. Costache se
                  simte din nou rău, Aglae și Stănică crezând că est mortsi
                  considerând că nu mai trebuie chemat doctorul. Felix și Otilia
                  însă îl cheamă pe Pascalopol și pe doctor. Cel din urmă
                  consideră că bătrânul va scapă și de data aceasta, iar
                  Costache îi dă o sută de mii de lei pentru contul Otiliei.
                  Stănică începe să caute ascunzătoarea banilor, iar Aglae,
                  Aurica și ceilalți membri încep să mute lucrurile din casă,
                  Aurică atentând chiar la pianul Otiliei. Cum Otilia nu pleca
                  niciodată de acasă, Stănică recurge la un siretlic ce se va
                  dovedi mortal. Îi spune fetei ca Pascalopol ii va face o
                  vizită și aceasta fuge pentru jumătate de oră pentru a-și
                  cumpăra o pudrieră, în acest timp, avocatul intră în cameră
                  bătrânului și îi ia banii de sub saltea, sub privirile
                  acestuia, care disperat, își dă duhul. Bătrânul este
                  înmormântat și toate proprietățile trec în posesia Aglaei.
                  Banii găsiți sub saltea de Stănică nu sunt niciodată înapoiat
                  de acesta, care divorțează de Olimpia. Otilia merge în camera
                  lui Felix și o roagă să o lase să doarmă în patul lui,
                  propunându-i să trăiască împreună ca bărbat și soție, fără
                  însă să se căsătorească. Felix însă nu profită de față. La
                  trezire, Otilia dispăruse, și Felix află că tânăra plecase la
                  Paris împreună cu Pascalopol, ulterior tânărul aflând că cei
                  doi se căsătoriseră. Anii au trecut și Felix devenise un
                  doctor de renume, având multe cărți publicate, multe tratate
                  și toată lumea știind cine este. Se întâlnește cu Pascalopol
                  într-o zi pe stradă și acesta îi arătă o poză făcută cu
                  Otilia, Felix nemaiputând să o recunoască pe față nebunatică
                  în spatele femeii mature ce devenise.
                </p>
              </div>
              <div class="section">
                <span className="title">Caracterizarea personajelor: </span>
                <p>
                  <strong>Felix Sima:</strong> Felix Sima este personajul
                  protagonist al romanului Enigma Otiliei de George Călinescu,
                  dinamic și complex, participând la acțiune în calitate de
                  "martor și actor", precum spune autorul. Aceaste ipostaze
                  narative constituie tehnici de caracterizare argumentative
                  pentru care Felix este un personaj realist modern. Deschide
                  romanul prin descrierea casei lui Costache Giurgiuveanu,
                  privită în detaliu și îl încheie cu aceeași imagine a clădirii
                  văzute din perspectiva eroziunii timpului. Romanul pune în
                  central narativ al acțiunii formarea personalității lui Felix,
                  de aceea poate fi considerat un bildungsroman.Acesta
                  reprezintă tipul intelectualului ambițios și în egală măsură
                  "tânărul îndrăgostit, naiv, care ezită să ia o decizie fermă
                  și, astfel, își pierde iubita." ( Gheorghe Glodeanu )Felix
                  este fiul doctorului Iosif Sima, de la Iași, care murise și îl
                  lăsase pe băiat în grija tutorelui Costache Giurgiuveanu, care
                  îi administra moștenirea până la vârsta majoratului. Astfel,
                  după terminarea liceului, timp în care locuise la internat,
                  vine la București spre a urma facultatea de medicina ca și
                  tatăl său.Ca și mijloace de realizare ale personajului, acesta
                  este caracterizat atât în mod direct cât și indirect. Prin
                  intermediul caracterizării directe realizate de către narator
                  aflăm că este un tânăr de 18 ani, iar portretul fizic îl
                  descrie ca având un aer aproape feminin: "față îi era juvenilă
                  și prelungă, aproape feminină", cu "șuvițe mari de păr ce-i
                  cădeau de sub șapcă", obrazul de "culoare măslinie", iar nasul
                  "de o tăietură elenică" îi dădea o "notă voluntară".
                </p>
                <p>
                  Ca și trăsături morale, naratorul precizează că Felix este "un
                  suflet limpede, incapabil de dimisulație, profund în
                  snetimente." Indirect, prin comportamentul și faptele sale,
                  este conturată încă din primele pagini o fire rațională,
                  lucidă, cu o mare nevoie de certitudini, o fire analitică și
                  un spirit de observație foarte dezvoltat. Multe din
                  trăsăturile acestuia sunt dezvăluite prin intermediul relației
                  acestuia cu celelalte personaje ale romanului. Relația dintre
                  Felix și Otilia scoate la iveală "simțiri până atunci latente"
                  din interiorul tânărului, care se îndrăgostește de cea cu care
                  împărtășește condiția de orfan. Dragostea lui Felix pentru
                  Otilia este neschimbată, statornică, indiferent de gesturile
                  feței, iar asta arată faptul ca atunci când vine vorba de
                  iubire, este sensibil, timid, impulsiv, imatur și naiv. Scena
                  din camera Otiliei îl înfățișează descoperind parcă o lume
                  nouă, un tărâm al visului și a misterului feminității. Scena
                  de la moșie este scena în care acesta începe să o cunoască mai
                  bine pe Otilia, și din care reiese că ar face orice pentru
                  aceasta, urmând-o oriunde s-ar duce.
                </p>
                <p>
                  Totodată, este prima dată când conștientizează sentimente de
                  gelozie bărbătească, privindu-l pe Pascalopol ca pe un rival
                  și este atât de înnebunit de gesturile sale afectuoase încât
                  atunci când Otilia se joacă cu arme, "nu nota atât primejdia,
                  cât îmbrățișarea lui Pascalopol". Scena ultimei nopți cu
                  aceasta, respectiv dispariția ei îl lasă la fel de derutat ca
                  și comportamentul ei de până atunci, și nu își poate explica
                  schimbările bruște ale acesteia. Ultima scenă, cea a
                  întâlnirii cu Pascalopol în tren, îl tulbură pe Felix întrucât
                  în momentul în care vede fotografia cu Otilia, realizează că
                  nu a cunoscut-o niciodată pe Otilia cu adevărat.
                </p>
                <p>
                  Din relația lui Felix cu Costache Giurgiuveanu reiese faptul
                  că protagonistul are o fire miloasă, întrucât acesta da dovadă
                  de grijă și îngrijorare sinceră față de bătrân, chiar și în
                  condițiile în care acesta refuza să îi dea banii care i se
                  cuvin. Relația dintre Felix și Pascalopol constă în sentimente
                  contradictorii, constând atât în gelozie cât și în simpatie.
                  Știe că Pascalopol concurează pentru inima Otiliei și că ii
                  poate oferi mai multe decât el, motiv pentru care îl urăște,
                  însă pentru faptul că o sprijină în orice situație și vrea să
                  o vadă fericită, indiferent dacă asta înseamnă să o lase
                  împreună cu el, îl îndeamnă pe Felix să îl respecte pe moșier.
                </p>
                <p>
                  În ceea ce privește relația dintre Felix și familia Tulea,
                  acesta îi suportă numai din timididate și datorită educației,
                  de cele mai multe ori îndurând atacurile verbale asupra sa sau
                  asupra celor apropiați lui. Chiar și așa însă, Felix nu le
                  dorește gând rău, ci încearcă numai să-i evite spre a nu-i
                  jigni refuzându-i în vreun fel, încă o dovadă că acesta are un
                  caracter nobil, ce nu dorește scandal și care este orientat
                  spre binele celorlalți.
                </p>
                <p>
                  <span className="citat">
                    "În opinia mea, George Călinescu a creat un personaj care se
                    completează cu adevărat pe parcursul romanului, a cărui
                    evoluție are loc atât în ceea ce privește dragostea, cât și
                    din punct de vedere social, reușind să se situeze deasupra
                    superficialității lumii burgheze, care deși încearcă la
                    fiecare pas să-l atragă în ghearele sale și să-l transforme,
                    acesta reușește până la urmă să se salveze, devenind un
                    doctor renumit."
                  </span>
                </p>
                <p>
                  <strong>Otilia Marculescu: </strong>Otilia este personajul
                  principal din romanul Enigma Otiliei, ea este fața adoptivă a
                  lui Moș Costache, numele ei de familie fiind Mărculescu. Ea
                  este o cântăreață la pian foarte talentată, și care dorește să
                  studieze muzica. Clanul Aglae dorește să alunge pentru a putea
                  pune mâna mai ușor pe averea lui Costache Giurgiuveanu. Prima
                  întâlnire în roman cu Otilia este la începutul lui când Felix
                  intră în casa lui Moș Costache pentru a doua dată(prima dată
                  moșul spunându-i că acolo nu stă nimeni), și ea vine în
                  salvarea lui recunoscându-l.Despre Otilia autorul spunea: „
                  Otilia era eroina mea lirică, proiecția sa în afară, o imagine
                  lunară și feminină”. Chipul adolescentin al Otiliei este de la
                  început o apariție angelică, descris în mod expres de autor,
                  din momentul sosirii lui Felix în casa lui Moș Costache, așa
                  cum o observă tânărul. Mai întâi voce cristalină, auzită de
                  sus, apoi, ”un cap prelung și tânăr de față,încărcat cu bucle,
                  căzând până pe umeri”.Părerea lui Felix despre Otilia la prima
                  vedere este: „fata părea să aibă vreo 18-19 ani.Fața măslinie,
                  cu nasul mic și ochii foarte albăstrii, arătă și mai
                  copilăroasă între multele bucle și gulerul de dantelă.Însă în
                  trupul subțiratic, cu oase delicate de ogar, de un stil
                  perfect, fără acea slăbiciune suptă și păstrată a Aureliei,
                  era o mare libertate de mișcări, o stăpânire desăvârșită de
                  femeie.”Aglae o vede că o piedică în luarea averii lui Moș
                  Costache.
                </p>
                <p>
                  Aurica este invidioasă pe Otilia și ar vrea la fel ca Aglae să
                  o alunge. Mos Costache o iubește dar de zgârcit n-a putut să-i
                  asigure un viitor fără probleme, nedepozitând decât foarte
                  puțini bani în banca restul ținându-i ascunși sub salteaua
                  lui. Pascalopol o iubește pe Otilia și îi împlinește toate
                  poftele ei, Otilia căsătorindu-se cu el, dar apoi acesta i-a
                  dat libertate ea căsătorindu-se cu un conte din Spania. Otilia
                  este unul dintre cele mai complexe personaje feminine din
                  proză românească, ea este fascinantă, mereu imprevizibilă,
                  dilematica prin comportament; iubită de părintele nesigur, de
                  Felix care-i înfioară corzile inimii, ocrotită de un străin,
                  Otilia este totuși fără cuib. Otilia trăiește drama
                  singurătății, viitorul ei este ambiguu, departe de mult visată
                  fericire. Comportamentul ei este derutant: capabilă de emoții
                  puternice trece brusc de la o stare la alta. Ea este
                  copilăroasă și în același timp și mătură dovedind o grijă
                  maternă față de Felix pe care-l protejeza ca pe un orfan.
                  Călătoria la Paris o maturizează, devine mai sigură mai
                  conștientă schimbare pe care Felix o percepe, se simte
                  inferior. Este poate o prefigurare a finalului, a opțiunii
                  Otiliei, care rămâne o enigmă a feminității.Eu cred despre
                  Otilia că va rămâne întotdeauna o prezentă a misterului, a
                  necunoscutului și a neînțelesului.
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
