import React, { Component } from "react";
import { Layout, Icon } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class RigaCryptoSiLaponaEnigel extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Riga Crypto și Lapona Enigel</h1>
              <h4>Ion Barbu</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section poezie">
                <p>
                  Menestrel trist, mai aburit <br />
                  Ca vinul vechi ciocnit la nuntă, <br />
                  De cuscrul mare dăruit <br />
                  Cu pungi, panglici, beteli cu funtă, <br />
                </p>
                <p>
                  Mult îndărătnic menestrel, <br />
                  Un cântec larg tot mai încearcă, <br />
                  Zi-mi de lapona Enigel <br />
                  Şi Crypto, regele-ciupearcă! <br />
                </p>
                <p>
                  - Nuntaş fruntaş! <br />
                  Ospăţul tău limba mi-a fript-o, <br />
                  Dar, cântecul, tot zice-l-aş, <br />
                  Cu Enigel şi riga Crypto. <br />
                </p>
                <p>
                  - Zi-l menestrel! <br />
                  Cu foc l-ai zis acum o vară; <br />
                  Azi zi-mi-l strâns, încetinel, <br />
                  La spartul nunţii, în cămară. <br />
                </p>
                <p>*</p>
                <p>
                  Des cercetat de pădureţi <br />
                  În pat de râu şi-n humă unsă, <br />
                  Împărăţea peste bureţi <br />
                  Crai Crypto, inimă ascunsă, <br />
                </p>
                <p>
                  La vecinic tron, de rouă parcă! <br />
                  Dar printre ei bârfeau bureţii <br />
                  De-o vrăjitoare mânătarcă, <br />
                  De la fântâna tinereţii. <br />
                </p>
                <p>
                  Şi răi ghioci şi toporaşi <br />
                  Din gropi ieşeau să-l ocărască, <br />
                  Sterp îl făceau şi nărăvaş, <br />
                  Că nu voia să înflorească. <br />
                </p>
                <p>
                  În ţări de gheaţă urgisită, <br />
                  Pe-acelaşi timp trăia cu el, <br />
                  Laponă mică, liniştită, <br />
                  Cu piei, pre nume Enigel. <br />
                </p>
                <p>
                  De la iernat, la păşunat, <br />
                  În noul an, să-şi ducă renii, <br />
                  Prin aer ud, tot mai la sud, <br />
                  Ea poposi pe muşchiul crud <br />
                  La Crypto, mirele poienii. <br />
                </p>
                <p>
                  Pe trei covoare de răcoare <br />
                  Lin adormi, torcând verdeaţă: <br />
                  Când lângă sân, un rigă spân, <br />
                  Cu eunucul lui bătrân, <br />
                  Veni s-o-mbie, cu dulceaţă: <br />
                </p>
                <p>
                  - Enigel, Enigel, <br />
                  Ţi-am adus dulceaţă, iacă. <br />
                  Uite fragi, ţie dragi, <br />
                  Ia-i şi toarnă-i în puiacă. <br />
                </p>
                <p>
                  - Rigă spân, de la sân, <br />
                  Mulţumesc Dumitale. <br />
                  Eu mă duc să culeg <br />
                  Fragii fragezi, mai la vale <br />
                </p>
                <p>
                  -Enigel, Enigel, <br />
                  Scade noaptea, ies lumine, <br />
                  Dacă pleci să culegi, <br />
                  Începi, rogu-te, cu mine. <br />
                </p>
                <p>
                  -Te-aş culege, rigă blând... <br />
                  Zorile încep să joace <br />
                  Şi eşti umed şi plăpând: <br />
                  Teamă mi-e, te frângi curând, <br />
                  Lasă. - Aşteaptă de te coace. <br />
                </p>
                <p>
                  -Să mă coc, Enigel, <br />
                  Mult aş vrea, dar vezi, de soare, <br />
                  Visuri sute, de măcel, <br />
                  Mă despart. E roşu, mare, <br />
                  Pete are fel de fel; <br />
                  Lasă-l, uită-l, Enigel, <br />
                  În somn fraged şi răcoare. <br />
                </p>
                <p>
                  - Rigă Crypto, rigă Crypto, <br />
                  Ca o lamă de blestem <br />
                  Vorba-n inimă-ai înfipt-o! <br />
                  Eu de umbră mult mă tem, <br />
                </p>
                <p>
                  Că dacă-n iarnă sunt făcută, <br />
                  Şi ursul alb mi-e vărul drept, <br />
                  Din umbra deasă, desfăcută, <br />
                  Mă-nchin la soarele-nţelept. <br />
                </p>
                <p>
                  La lămpi de gheaţă, supt zăpezi, <br />
                  Tot polul meu un vis visează. <br />
                  Greu taler scump cu margini verzi <br />
                  De aur, visu-i cercetează. <br />
                </p>
                <p>
                  Mă-nchin la soarele-nţelept, <br />
                  Că sufletu-i fântână-n piept, <br />
                  Şi roata albă mi-e stăpână, <br />
                  Ce zace-n sufletul-fântână. <br />
                </p>
                <p>
                  La soare, roata se măreşte; <br />
                  La umbră, numai carnea creşte <br />
                  Şi somn e carnea, se dezumflă, <br />
                  Dar vânt şi umbră iar o umflă... <br />
                </p>
                <p>
                  Frumos vorbi şi subţirel <br />
                  Lapona dreaptă, Enigel, <br />
                  Dar timpul, vezi, nu adăsta, <br />
                  Iar soarele acuma sta <br />
                  Svârlit în sus, ca un inel. <br />
                </p>
                <p>
                  - Plângi, preacuminte Enigel! <br />
                  Lui Crypto, regele-ciupearcă. <br />
                  Lumina iute cum să-i placă? <br />
                  El se desface uşurel <br />
                  De Enigel, <br />
                  De partea umbrei moi, să treacă... <br />
                </p>
                <p>
                  Dar soarele, aprins inel, <br />
                  Se oglindi adânc în el; <br />
                  De zece ori, fără sfială, <br />
                  Se oglindi în pielea-i cheală. <br />
                </p>
                <p>
                  Şi sucul dulce înăcreşte! <br />
                  Ascunsa-i inimă plesneşte, <br />
                  Spre zece vii peceţi de semn, <br />
                  Venin şi roşu untdelemn <br />
                  Mustesc din funduri de blestem; <br />
                </p>
                <p>
                  Că-i greu mult soare să îndure <br />
                  Ciupearcă crudă de pădure, <br />
                  Că sufletul nu e fântână <br />
                  Decât la om, fiară bătrână, <br />
                  Iar la făptură mai firavă <br />
                  Pahar e gândul, cu otravă, <br />
                </p>
                <p>
                  Ca la nebunul rigă Crypto, <br />
                  Ce focul inima i-a fript-o, <br />
                  De a rămas să rătăcească <br />
                  Cu altă faţă, mai crăiască: <br />
                </p>
                <p>
                  Cu Laurul-Balaurul, <br />
                  Să toarne-n lume aurul, <br />
                  Să-l toace, gol la drum să iasă, <br />
                  Cu măsălariţa-mireasă, <br />
                  Să-i ţie de împărăteasă. <br />
                </p>
              </div>
              <div class="section">
                <span className="title">Introducere:</span>
                <p>
                  Poezia Riga Crypto şi lapona Enigel face parte din volumul Joc
                  secund, publicat în anul 1930. Autorul îşi subintitulează
                  poezia Baladă şi o include în ciclul Uvedenrode, deci în etapa
                  baladică şi orientală. Este o perspectivă modernă asupra
                  acestei specii literare epice, fiind de fapt un poem alegoric.
                  Asemănarea cu alt poem alegoric al literaturii române,
                  Luceafărul de Mihai Eminescu, este evidentă.
                </p>

                <p>
                  Autorul însuşi îşi intitulează opera „un Luceafăr întors”.
                  Ambele poezii tratează aceeaşi temă, a iubirii imposibile şi a
                  incompatibilităţii dintre două fiinţe care aparţin de lumi
                  diferite.
                </p>
                <p>
                  Caracterul alegoric al poeziei este dat de faptul că se pune
                  accentul pe problema cunoaşterii: la fel ca şi față de
                  împărat, riga Crypto doreşte să-şi depăşească statutul de
                  fiinţă inferioară, dar în final constată că acest lucru este
                  imposibil.
                </p>

                <p>
                  Diferenţa esenţială este că în cazul poeziei lui Barbu fiinţa
                  superioară este omul, lapona Enigel, care aspiră spre o
                  cunoaştere absolută. Drama este însă aceeaşi: cineva trebuie
                  să plătească tentaţia şi tentativa de a-şi depăşi condiţia.
                  Motivele poetice care se întâlnesc sunt: transhumanţa,
                  tentaţia Sudului, atracţia pentru Soare, visul şi
                  „sufletul-fântână”.
                </p>
                <p>
                  Un alt element comun între cele două poezii este interferenţa
                  genurilor epice. Deşi amestecarea genurilor este o trăsătură
                  specifică romantismului, faptul că poezia modernă a lui Barbu
                  are la bază o baladă, explică această particularitate. Fiind o
                  „baladă”, se păstrează structura narativă deoarece poezia are
                  un fir epic şi „personaje”.
                </p>
                <p>
                  Caracterul dramatic este oferit de dialogurile dintre
                  „personaje”, dar şi în această poezie acestea au valoare de
                  simbol, fapt care subliniază lirismul.
                </p>
              </div>
              <div class="section">
                <span className="title">Titlul:</span>
                <p>
                  Titlul baladei este construit pe ideea cuplului, dar în acest
                  caz acesta nu se întregeşte datorită incompatibilităţii dintre
                  cei doi eroi, care fac parte din regnuri diferite.
                </p>

                <p>
                  Fiecare se află pe o treaptă superioară în propria lume, dar
                  povestea de dragoste este imposibilă mai ales datorită
                  aspiraţiilor fiecăruia. Poezia este structurată în două părţi,
                  fiecare dintre ele reprezentând câte o nuntă. Se împrumută
                  formula narativă specifică genului epic, şi anume povestirea
                  în ramă.
                </p>

                <p>
                  Prima nuntă este una posibilă, care s-a realizat deja şi are
                  rolul unui cadru pentru nunta fantastică, aceasta având un
                  final nedorit, riga Crypto fiind nevoit să accepte căsătoria
                  cu „măsălăriţa-mireasă”.
                </p>
              </div>
              <div class="section">
                <span className="title">Structură și compoziție:</span>

                <p>
                  {" "}
                  <strong>Prima parte</strong> cuprinde primele patru strofe
                  care au rolul de prolog al baladei şi reprezintă dialogul
                  menestrelului cu „nuntaşul fruntaş”.. Se face trimiterea spre
                  un timp mitic, spre Evul Mediu, prin adresarea directă către
                  cel care va rosti povestea tragică a laponei Enigel şi a lui
                  riga Crypto: „Menestrel trist”, „Mult îndărătnic menestrel”.
                  Sintagma „acum o vară” presupune o plasare temporală, dar ea
                  nu face decât să plaseze mai mult în imprecis momentul
                  rostirii, în aceeaşi situaţie fiind şi adverbul „azi”.
                </p>

                <p>
                  În cea de-a doua parte, povestea este proiectată tot în
                  trecut, dar într-un timp nedeterminat în raport cu momentul
                  rostirii, fapt subliniat şi de folosirea imperfectelor:
                  „împărţea”, „bârfeau”, „ieşeau”, „nu voia”, „trăia” etc.
                </p>

                <p>
                  Cântecul despre povestea tragică de iubire trebuie spus însă
                  într-o atmosferă prielnică, în afara cotidianului, „la spartul
                  nunţii, în cămară.” Menestrelul are multiple modalităţi de
                  interpretare:
                  <span className="citat">
                    „Cu foc l-ai zis acum o vară; / Azi zi-mi-l stins,
                    încetinel”,
                  </span>
                  dar trebuie să o aleagă pe cea care să se potrivească cel mai
                  bine cu atmosfera şi cu povestea.
                </p>

                <p>
                  Tot aici se face o trimitere la zeul Dyonisos (zeul vinului,
                  dar şi al muzicii), atât prin faptul că cel care povesteşte
                  este un cântăreţ, cât şi prin comparaţia:
                  <span className="citat">
                    „mai aburit / Că vinul vechi ciocnit la nuntă”.
                  </span>
                  Repetarea cântecului spus şi în trecut sugerează un ritual al
                  zicerii unei poveşti exemplare.
                </p>

                <p>
                  <strong>A doua parte</strong> conţine povestea propriu-zisă
                  dintre riga Crypto şi lapona Enigel şi este realizată din mai
                  multe secvenţe poetice: portretul şi descrierea împărţiei
                  regelui ciupercilor (strofele 5-7), portretul şi descrierea
                  mediului în care trăieşte lapona Enigel (strofele 8 şi 9),
                  întâlnirea în vis a celor doi (strofa 10), cele trei chemări
                  ale rigăi şi primele două refuzuri ale laponei (strofele
                  11-15), refuzul categoric al laponei şi relevarea simbolului
                  solar după care îşi ghidează existenţa (strofele 16-20),
                  încheierea întâlnirii dintre cei doi (strofele 21 şi 22),
                  pedepsirea lui Crypto din final (strofele 23-27).
                </p>

                <p>
                  În prima secvenţă sunt prezentate personajele: în lumea lui,
                  Crypto este un ales, are un statut superior:
                  <br />
                  <span className="citat">
                    „Des cercetat de pădureţi / În pat de râu şi-n humă unsă, /
                    Împărăţea peste bureţi / Crai Crypto, inimă ascunsă.”
                  </span>
                  <br />
                </p>
                <p>
                  Deşi admirat, are parte şi de adversităţi: este considerat
                  „sterp” şi „nărăvaş” de celelalte plante ale pădurii care nu
                  înţeleg de ce el nu îşi acceptă condiţia de ciupercă şi
                  încearcă să o depăşească. Fiinţa la care visează aparţine de
                  regnul uman, este deosebită în lumea ei datorită faptului că
                  doreşte ea o împlinire absolută reprezentată prin lumină
                  solară.
                </p>

                <p>
                  Ea pendulează anual între cotidian: „de la iernat” şi idealul
                  reprezentat de Sud, de Soare: „la păşunat, [...] tot mai la
                  sud.” Singura asemănare dintre cele două fiinţe este statutul
                  superior pe care îl au în propria lume, dar acest element nu
                  va fi suficient pentru împlinirea prin iubire. Deosebirile
                  sunt prezentate cu ajutorul antitezelor şi a imaginilor care
                  descriu mediul existenţial al celor doi.
                </p>

                <p>
                  Lumea lui Crypto se defineşte prin umezeală şi răcoare: „În
                  pat de râu şi humă”, în timp ce lumea laponei este un spaţiu
                  rece: „În ţări de gheaţă urgisită”, fapt care explică atracţia
                  ei spre soare şi lumină.
                </p>

                <p>
                  <strong>În drumul ei spre sud,</strong> „lapona mică,
                  liniştită” Enigel este rugată de către regele ciupercilor să
                  rămână alături de el în lumea umedă şi răcoroasă a plantelor
                  pădurii. Întâlnirea celor doi se realizează în lumea visului,
                  acest motiv făcând din nou trimitere la poemul Luceafărul.
                </p>
                <p>
                  Regele ciupercilor încearcă să o ademenească în lumea lui prin
                  intermediul a trei chemări. Valoarea de descântec şi de
                  incantaţie a celor trei chemări, precum şi împletirea
                  limbajului popular cu cel literar („iacă”, „ţie dragi”,
                  „puiacă”, „te frângi”, „somn fraged”, „lamă de blestem”),
                  subliniază şi mai mult caracterul de baladă al poeziei.
                </p>

                <p>
                  <strong>În prima chemare</strong> riga Crypto o îmbie pe
                  laponă cu „dulceaţă” şi cu „fragi”, elemente definitorii
                  pentru mediul vegetal din care provine. Darul lui este refuzat
                  categoric de Enigel şi exprimă aspiraţia ei spre Soare:
                  <br />
                  <span className="citat">
                    „- Rigă spân, de la sân, / Mulţumesc Dumitale. / Eu mă duc
                    să culeg / Fragii fragezi, mai la vale.”
                  </span>
                  <br />
                </p>
                <p>
                  <strong>În a doua chemare</strong> îndrăgostitul merge şi mai
                  departe propunându-i sacrificiul de sine:
                  <br />
                  <span className="citat">
                    „Dacă pleci să culegi, / Începi, rogu-te cu mine.”.
                  </span>
                  <br />
                </p>
                <p>
                  Lapona înţeleaptă îl refuză şi de data aceasta, arătându-i
                  motivele pentru care nuntirea lor este imposibilă: Crypto este
                  „umed şi plăpând” şi este sfătuit să aştepte să se coacă:
                  „Teamă mi-e, te frângi curând, / Lasă.
                </p>

                <p>
                  Aşteaptă de te coace.” Opoziţia copt / necopt se referă de
                  fapt la opoziţia soare / umbră, care subliniază de fapt
                  relaţia fiecăruia cu universul, incompatibilitatea peste care
                  niciunul dintre ei nu poate trece fără să se piardă pe sine.
                  Crypto se teme de lumina soarelui:
                  <span className="citat">
                    „de soare, / Visuri sute, de măcel, / Mă despart. E roşu,
                    mare, / Pete are fel de fel”,
                  </span>
                  în timp ce lapona aspiră spre acesta:
                  <span className="citat">
                    „Că dacă-n iarnă sunt făcută, / Şi ursul alb mi-e vărul
                    drept, / din umbră deasă, desfăcută, / Mă-nchin la
                    soarele-nţelept.” .
                  </span>
                  <br />
                </p>

                <p>
                  <strong>Ultima încercare</strong> pe care o face riga este
                  urmată de un refuz şi mai categoric. Fiinţa superioară îşi dă
                  seama că iubirea lor nu este posibilă şi că opoziţia dintre
                  soare şi întuneric, dintre raţiune şi instinct nu va putea fi
                  niciodată anulată
                  <br />
                  <span className="citat">
                    „La soare, roată se măreşte; / La umbră numai carnea
                    creşte”.
                  </span>
                  <br />
                </p>

                <p>
                  Refuzul laponei este fatal pentru rigă deoarece puterea
                  descântecului se întoarce asupra lui, atributele lumii pe care
                  Enigel o alege se întorc asupra lui. Dorind să-şi urmeze
                  aspiraţia spre fiinţa superioară, „regele-ciupearcă” este
                  pedepsit pentru îndrăzneala lui şi transformat în ciupercă
                  otrăvitoare:
                  <br />
                  <span className="citat">
                    „Dar soarle, aprins inel, /<br /> Se oglindi adânc în el;
                    [...] Şi sucul dulce înăcreşte! /<br /> Ascunsă-i inimă
                    plesneşte.”.
                  </span>
                  <br />
                </p>
                <p>
                  Astfel, încercarea lui Crypto de a schimba ordinea firească a
                  lumii eşuează, iar ordinea este în final restabilită.
                </p>

                <p>
                  Cel ajutat{" "}
                  <span className="citat">
                    „De-o vrăjitoare mânătarcă, / De la fântâna tinereţii”
                  </span>{" "}
                  pentru a se putea împlini, ajunge, prin oglindire (un alt
                  motiv esenţial al poeziei), să se însoţească cu o fiinţă din
                  lumea lui,
                  <span className="citat">
                    „Cu măsălăriţa-i mireasă, / Să-i ţie de împărăteasă.”
                  </span>
                </p>

                <p>
                  Opoziţia dintre cele două fiinţe, una umană, cealaltă
                  vegetală, este subliniată şi în final prin antiteză „fiară
                  bătrână” / „făptură mai firavă” :
                  <span className="citat">
                    „Că sufletul nu e fântână / Decât la om, fiară bătrână, /
                    Iar la făptură mai firavă / Pahar e gândul, cu otravă.”
                  </span>
                </p>
                <p>
                  Omul este o fiinţă supusă greşelii şi încercării repetate
                  datorită existenţei îndelungate, în timp ce creatura vegetală
                  trăieşte un an şi, prin urmare, nu are dreptul decât la o
                  şansă care îi este fatală.
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
