import React, { Component } from "react";
import { Layout } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class HarapAlb extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Povestea lui Harap Alb</h1>
              <h4>Ion Creangă</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section">
                <span className="title">Definiția basmului:</span>
                <p>
                  Specie a genului epic, în proză, în care sunt narate
                  întâmplări fabuloase ale unor personaje imaginare înzestrate
                  cu puteri supranaturale.
                </p>
              </div>
              <div class="section">
                <span className="title">Trăsături:</span>
                <p>
                  Opera lui Creangă este o specie basm, deoarece predomină
                  fabulosul, supranaturalul, iar personajele au puteri
                  miraculoase, de exemplu cei cinci giganți, fata împăratului
                  Roș, ele întruchipând binele și răul.
                </p>

                <p>
                  Locul și timpul sunt vag sugerate "amu' cică era odată.." iar
                  tema este luptă dintre bine și rău. Apar cifrele magice de
                  exemplu 7, 9, 49 și formulele de început "Amu cică", mediane "
                  Că cuvântul din poveste.." și cele finale " Pe la noi cine are
                  bani bea și mănâncă, iar cine nu se uită.."
                </p>
              </div>
              <div class="section">
                <span className="title">Apariție:</span>
                <p>La 01 august 1877 în revista "Convorbiri literare"</p>
              </div>
              <div class="section">
                <span className="title">Titlul:</span>
                <p>
                  Cuvântul "povestire" are sensul de născocire, opera urmând să
                  prezinte pățaniile protagonistului a cărui onomastică apare
                  pentru prima dată în scena în care Spânul îl păcălește pe fiul
                  craiului să intre în fântână după care îi i-a locul făcându-l
                  slujitorul lui și dându-i numele de "Harap-Alb".
                </p>
                <p>
                  Numele lui însemând "rob alb", deoarece "Harap" înseamnă
                  "negru rob", el devine "robul-țigan", deși era alb,
                  nedumerindu-i astfel pe toți.
                </p>
              </div>
              <div class="section">
                <span className="title">Tema:</span>
                <p> O reprezintă lupta dintre bine și rău.</p>
              </div>
              <div class="section">
                <span className="title">Viziunea autorului:</span>
                <p>
                  Viziunea autorului expusă în acest basm este aceea că nu
                  trebuie să ai puteri supranaturale pentru a reuși, ci trebuie
                  să ai calități, precum bunătatea, curajul, milostivenie,
                  demnitate sau putere de a fi loial pentru a-i determinape cei
                  din jurul tău să te ajute să izbândești.
                </p>
              </div>

              <div class="section">
                <span className="title">Perspectiva narativă:</span>
                <p>
                  Evenimentele sunt relatate dintr-o prismă exterioară,
                  naratorul fiind heterodiegetic.
                </p>

                <p>
                  Caracteristic acestui tip de narator este povestită la
                  persoană a III-a și tipul clasic de a dirija totul "din spate"
                  cunoscând și anticipând evoluția personajelor. Avem narator
                  omniscient , omniprezent și omnipotent.
                </p>
              </div>

              <div class="section">
                <span className="title">Tehnici narative:</span>
                <p>
                  Personajele lui Creangă vorbesc folosind permanent proverbe și
                  zicători. Una din principalele caracteristici ale lui Creangă
                  este oralitatea, reflectată prin dialogul permanent al
                  povestitorului cu cititorul, întrebuințarea interjecților, a
                  propozițiilor interogative și exclamative, a antalogiei.
                </p>
              </div>

              <div class="section">
                <span className="title">Povestirea:</span>
                <p>
                  Un crai are trei fii. Fratele său, Verde-Împărat, care
                  stăpânea o ţară îndepărtată, neavând moştenitori băieţi, îi
                  scrie să-i trimită pe unul dintre feciori pentru a-l lăsa pe
                  tron la moartea sa.Fiul cel mare al craiului pleacă la drum.
                </p>

                <p>
                  Ca să-i încerce puterile, tatăl său îi iese în cale îmbrăcat
                  în piele de urs. Flăcăul se sperie şi se întoarce înapoi,
                  stârnind batjocura părintelui său. Tot aşa păţeşte şi
                  mijlociul. Mezinul este sfătuit de o bătrână cerşetoare cum să
                  procedeze pentru a nu mai păţi ruşinea fraţilor săi. Îi cere
                  tatălui său calul, armele şi hainele din tinereţe.
                </p>

                <p>
                  Deşi uimit, craiul este totuşi de acord. Calul se dovedeşte a
                  fi un cal fermecat, care nu se sperie de urs atunci când îi
                  iese în faţă. Astfel, mezinul pleacă mai departe, primind de
                  la tatăl său blană de urs şi sfatul să se ferească de oamenii
                  spâni sau roşii.
                </p>

                <p>
                  Cutreieră mult timp, până când, într-un codru întâlneşte un om
                  spân, care îi apare de trei ori sub trei înfăţişări diferite,
                  oferindu-se să-i fie slugă. În cele din urmă, fiul cel mic al
                  craiului, nemaiştiind pe ce cărări să o apuce, acceptă ultima
                  propunere a spânului. Cei doi străbat o bucată de drum, după
                  care spânul, prin vicleşug, îl convinge pe fiul craiului să
                  coboare într-o fântână ca să se răcorească. Îl închide acolo
                  şi îl obligă să i se supună.
                </p>

                <p>
                  Cei doi schimbă între ei hainele şi identitatea. Fiul craiului
                  devine sluga spânului şi este botezat Harap-Alb. Cei doi îşi
                  continuă drumul spre ţara lui Verde-Împărat. Ajunşi acolo,
                  spânul este primit şi tratat cu tot respectul cuvenit
                  moştenitorului tronului, iar Harap-Alb este trimis la
                  grajduri. Curând după aceea, spânul, pentru a-l impresiona pe
                  împărat, îl trimite pe Harap-Alb să aducă o salată rară, din
                  grădina ursului, apoi o piatră nestemată din fruntea cerbului,
                  două sarcini foarte periculoase.
                </p>

                <p>
                  Cu ajutorul bătrânei cerşetoare – care se dovedeşte a fi
                  Sfânta Duminică -, tânărul reuşeşte să ducă la bun sfârşit
                  cele două îndatoriri. A treia oară este trimis s-o aducă pe
                  fața împăratului Roş, cunoscută ca mare vrăjitoare. Drumul
                  către împărăţia acestuia este lung. Harap-Alb are ocazia să
                  facă fapte bune: să ajute nişte furnici şi un roi de albine,
                  primind de la acestea câte o aripă fermecată. Se întovărăşeşte
                  apoi cu cinci făpturi ciudate, care se oferă să îl ajute:
                  Gerilă, Flămânzilă, Setilă, Ochilă şi Păsări-Lăţi-Lungilă.
                </p>

                <p>
                  Cu ajutorul acestora şi cu darurile de la furnici şi albine,
                  Harap-Alb reuşeşte să depăşească încercările la care îl supune
                  împăratul Roş şi să o capete pe fiica acestuia pentru a o duce
                  spânului. Pe drum cei doi tineri prind drag unul de altul.
                  Când ajung la palatul împăratului Verde, fata dezvăluie
                  tuturor adevărată identitate a lui Harap-Alb. Spânul, mânios
                  că a fost trădat, se repede şi îi tăie acestuia capul.
                </p>

                <p>
                  Imediat, calul lui Harap-Alb îl ucide pe spân, azvârlindu-l
                  din înaltul cerului. Fata împăratului Roş îl readuce la viaţă
                  pe Harap-Alb cu apă moartă şi apă vie. Cei doi se căsătoresc
                  şi împăratul Verde le dă binecuvântarea şi împărăţia totodată.
                </p>
              </div>

              <div class="section">
                <span className="title">Caracterizare Harap-Alb:</span>
                <p>
                  Este personajul principal din basm, reprezentând binele. Spre
                  deosebire de personajele din basmul popular, Harap-Alb, nu
                  este un erou, deoarece nu ucide zmei, și nu are puteri magice.
                  El este un tânăr obișnuit, un flăcău harnic și amenos,
                  înzestrat cu un suflet milos și sensibilitate. Eroul o
                  milostivește pe bătrână, îl ascultă pe calul năzdrăvan, este
                  săritor la nevoie, prietenos și înțelegător față de cei 5
                  năzdrăvani, cinstit.
                </p>

                <p>
                  Cumsecadeeria personajului nu este o trăsătură supranaturală,
                  dar în multele încercări prin care va trece, nu eroismul,
                  curajul sau înzestrările supranaturale vor fi cele care îl vor
                  ajută să izbândească, ci ajutorul dat de cei care îl iubesc. O
                  singură dată nu a ascultat și a fost aspru pedepsit. Personaj
                  simbolic, Haral-Alb, îndeplinește în basm destinul care ia
                  fost hărăzit prin credința în bine și în omenie.
                </p>

                <p>
                  Este fiul cel mai mic al Craiului și nepoul lui Verde-Împărat.
                  Are respect pentru frații mai mari, lăsându-i să plece primii,
                  astfel beneficiind de experiența acestora. Se arată milostiv,
                  ajutând bâtrâna cerșetoare. Dă dovadă de naivitate atunci când
                  îl primește în slujba lui pe Spân.
                </p>
              </div>

              <div class="section">
                <span className="title">Caracterizare Spân:</span>
                <p>
                  Este personajul negativ al basmului, prin atitudinea lui
                  umană. El reprezintă răul, ispita, care se înfățișează sub
                  aparența binelui, îi iese în cale lui Harap-Alb de trei ori
                  sub diferite aspecte până ce reușește să îl păcălească. În
                  afară de atributului suprem, Spânul, mai este mincinos și
                  răzbunător.
                </p>

                <p>
                  El seamănă în lume teroare, răutate și violență. Ca personaj
                  real, el întruchipează individul perfid, care obține avantaje
                  și bogați prin înșelăciune. Prin naștere, spânul este un om
                  rău și de o proastă condiție socială. Este conturat atât prin
                  caracterizarea directă cât și prin caracterizarea indirectă,
                  comportamentul său și relațiile cu ceilalți compunând un
                  portret uman înfricoșător.
                </p>

                <p>
                  Portrelul fizic reducându-se la o singură trăsăturăm exprimată
                  direct, "un om spân", sugestie asupra concepției populare
                  potrivit căreia unei anomali fizice îi corespunde o deficență
                  majoră. Conform ideii, oameni însemnați sunt periculoși și
                  trebuie să te ferești de ei. Este un personaj simbolic.
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
