import React, { Component } from "react";
import { Layout, Icon } from "antd";
import ExtraContent from "./ExtraContent";
// Ant design
const { Content } = Layout;

export default class HanulAncutei extends Component {
  render() {
    return (
      <div className="content_wrapper">
        <Content
          style={{
            background: "#fff",
            padding: 24,
            marginTop: "3em",
            minHeight: 280
          }}
        >
          <div className="content">
            <div className="header">
              <h1>Hanul Ancuței</h1>
              <h4>Mihail Sadoveanu</h4>
            </div>

            <ExtraContent />
            <div className="body">
              <div class="section">
                <span className="title">Definiția povestirii:</span>

                <p>
                  Ca specie literară, povestirea reprezintă o narațiune
                  subiectivizată (supusă din punct de vedere al
                  povestitorului,implicat ca narator,ca participant sau ca
                  mesager al întâmplării) care se limitează la nararea unui
                  singur fapt.
                </p>

                <p>
                  Povestirea în ramă (povestirea în povestire sau povestire cu
                  cadru) este o categorie a genului epic, în care interesul se
                  centrează asupra situației neobișnuite povestite și nu în
                  jurul personajelor.
                </p>

                <p>
                  Timpul narativ se situează într-un plan al trecutului, iar
                  principala modalitate de expunere este evocarea. Spațiul
                  desfășurării acțiunii este unul privilegiat și ocrotitor (un
                  topos în care mai mulți povestitori relatează întâmplări
                  pilduitoare, respectând un ceremonial prestabilit și
                  desfășurând o artă a discursului memorabil.
                </p>
                <p>
                  În volumul "Hanu-Ancuței", Mihail Sadoveanu recurge la la
                  tehnica literară numită poveste în poveste sau povestire în
                  ramă, sau narațiune în narațiune, deoarece în cadrul firului
                  epic al volumului intervin istorisiri relatate de fiecare dată
                  de alt oaspete de la hanul Ancuței, acesta particularizându-se
                  printr-un stil propriu.
                </p>

                <p>
                  Personajele-povestitori sunt fie participanți direct, fie
                  martori ai întâmplărilor istorisite cu plăcere și cu ambiția
                  că povestirea lui va fi mai impresionantă decât cele
                  anterioare, deoarece "Cine le spune mai frumos, acela are
                  laudă mai mare", susține comisul Ioniță.
                </p>
              </div>
              <div class="section">
                <span className="title">Trăsături ale povestirii:</span>

                <p>
                  Că specie este o povestirea deoarece prezintă dimensiuni
                  reduse (mai mari decât ale schiței dar mai mici decât ale
                  nuvelei.Naratorul are o mare implicare în faptele povestite.
                </p>

                <p>
                  Accentul cade pe situația narată și nu pe personaj,se observă
                  caracterul epic. Construcția subiectului este mai puțin
                  riguroasă, acțiunea dsfășurânduse mai relaxat.
                </p>

                <p>
                  Presupune o atmosferă propice și un anume ceremonial al
                  spunerii,respectă ritualul captării atenției prin stârnirea
                  curiozitații. Timpul evocat este cel trecut,chiar mitic
                  recreat prin povestire. Și nu în ultimul rând presupune o
                  relație specială de comunicare între narator și receptor,ceea
                  ce imprimă textului oralitatea stilului.
                </p>

                <p>
                  Este o povestire în ramă deoarece timpul narativ este cel al
                  trecutului,modalitatea principală de relatare este
                  evocarea,există un topos specific al spunerii,un loc
                  privilegiat,ocrotitor în care naratorii isorisesc pe rând câte
                  o întâmplare. Se respectă un ceremonial prealabil,accentul
                  cade pe situațiile neobișnuite relatate și prea puțin pe
                  personaje ,din fiecare povestire se desprinde o morală,un
                  talc,care dă caracter epic spunerii.
                </p>
              </div>
              <div class="section">
                <span className="title">Apariția:</span>
                <p>"Hanul Ancuței" a apărut în anul 1928.</p>
              </div>
              <div class="section">
                <span className="title">Compoziția:</span>
                <p>
                  Structural "Hanul Ancuței" reprezintă o suită de 9 povestiri
                  independente, a căror unitate este asigurată de atmosfera de
                  petrecere a ritualului spunerii.
                </p>
                <ol>
                  <li>
                    <strong>Iapa lui Vodă</strong>
                  </li>
                  <li>
                    <strong>Haralambie</strong>
                  </li>
                  <li>
                    <strong>Balaurul</strong>
                  </li>
                  <li>
                    <strong>Fântâna dintre plopi</strong>
                  </li>
                  <li>
                    <strong>Cealaltă Ancuta</strong>
                  </li>
                  <li>
                    <strong>Județ al sărmanilor</strong>
                  </li>
                  <li>
                    <strong>Negustor Lipscan</strong>
                  </li>
                  <li>
                    <strong>Orb sarac</strong>
                  </li>
                  <li>
                    <strong>Istorisirea Zahariei fântânarului</strong>
                  </li>
                </ol>
              </div>
              <div class="section">
                <span className="title">
                  Îmbinarea axelor temporale și a reperelor spațiale:
                </span>
                <ol>
                  <li>
                    Timpul prezent al reuniunii la Hanul Ancuței care coexistă
                    cu timpul mitic al apariției balaurului în acel an de
                    Santilie (Sf. Ilie)
                  </li>
                  <li>
                    Timpul trecut al întâmplărilor, reperele sale temporale
                    fiind existența diferitelor domnii,în cadrul acestei axe a
                    trecutului se fac aluzii la timpul stihiilor (Balaurul,Orb
                    sărac)
                  </li>
                  <li>
                    Timpul neutru al naratorului [rezent,dar care nu devine
                    narator al niciunei povestiri.
                  </li>
                </ol>

                <p>
                  Că spații domina satul (în Haralambie,Balaurul, Județ al
                  sărmanilor) și Hanul Ancuței (Iapa lui Vodă,Fântâna dintre
                  plopi, Orb sărac, Istorisirea Zahariei fântânarul) iar din
                  când în când se lărgește orizontul spre locurile haiduciei
                  (Haralambie), a stânei de la munte (Județ al sărmanilor,Orb
                  sărac) sau țărilor străine (Negustor Lipscan, Cealaltă
                  Ancuta).
                </p>
              </div>
              <div class="section">
                <span className="title">Povestirea:</span>
                <p>
                  <strong>Iapa lui Vodă:</strong>
                  <br />
                  Este o povestire, având începutul de basm, întâmplările
                  petrecându-se "într-o îndepărtată vreme, demult", când oamenii
                  au văzut "un balaur negru în nouri" și niște păsări mari care
                  vesteau război și belșug la viță de vie. Oamenii din
                  Ţară-de-Jos au făcut atâta vin că nu mai aveau unde să pună
                  mustul și-au pornit să ducă vin spre munte.
                </p>
                <p>
                  Comisul Ioniță venise la han, călare pe un "cal pintenog de
                  trei picioare ", care era calul din poveste înainte de a mânca
                  tipsia cu jar. Întâmplarea paovestită are legătură cu iapă din
                  care se trăgea acest cal și care se petrecuse pe vremea lui
                  Mihai Vodă Sturza și-l are ca protagonist pe comisul Ioniță
                  atunci când era tânăr și când hangița era "cealaltă Ancuta,
                  mama acesteia ". După ce se odihnise la han, comisul Ioniță
                  urează sănătate unui boier care tocmai se oprise la han și
                  care-l întrebă de unde este și încotro se duce.
                </p>
                <p>
                  Ioniță îi relatează că este razes din Drăgănești, de lângă
                  Suceava și avea de gând să meargă la Vodă, ca să-i rezolve un
                  proces pe care-l avea de mulți ani cu un "corb mare boieresc
                  ", pentru pământul moștenit din moși- strămoși. Comisul Ioniță
                  ducea cu el toate documentele doveditoare, iar dacă nici Vodă
                  nu-i va face dreptate, "atunci să poftească măria să să-i pupe
                  iapă nu departe de coadă!".
                </p>
                <p>
                  Ajuns la Curtea Domnească, Ioniță constată că ca boierul cu
                  care vorbise la han era însuși Vodă. Acesta îi face dreptate
                  și-l întrabă ce s-ar fi întâmplat dacă nu i-ar fi rezolvat
                  necazul. Ioniță îi răspunse râzând : "Eu vorba nu mi-o iau
                  înapoi. Iapa-i peste drum!" De aceea, spune el acum, toți
                  trebuie să se uite la calul său cel roib, ca la un lucru rar,
                  pentru că el se trage din Iapa lui Vodă."
                </p>
                <p>
                  <br />
                  <strong>Haralambie:</strong>
                  <br />
                  Este povestită de călugărul Gherman, care coborâse pentru
                  prima oară de la schitul de munte și se ducea la Iași.Toți se
                  uită cu uimire cum s-a ridicat "părintele Gherman din barba
                  sa", ca și când până atuncistatuse într-un caier uriaș, iar
                  imaginea a fost impresionantă prin reliefarea extraordinară de
                  sugestivă a personajului.
                </p>
                <p>
                  El se născuse în satul Bozieni și nu-și cunoscuse tatăl,
                  fusese crescut doar de mama lui, iar când aceasta murise, a
                  fost trimis la Mănăstirea Durăului ca "să răscumpăr păcate
                  trecute". Gherman se ducea acum la biserica Sf. Haralambie din
                  Iași, ca să se roage pentru iertarea păcatelor părinților.
                </p>
                <p>
                  Haralambie fusese "un arnăut domnesc" care se făcuse haiduc și
                  "mulți boieri și negustori și norod" avuseră de suferit din
                  cauza cruzimolor lui. Vodă Ipsilant a dat porunca să fie
                  prinși vrăjmașii, dar mereu biruia Haralambie, care se "trăgea
                  pe poteci și prin munți" numai de el știute. Atunci,
                  Domnitorul a luat hotărârea să-l trimită pe Gheorghie
                  leondari, fratele haiducului, care era tufecci-basa, "om
                  cinstit și viteaz", singurul care putea să-l prindă, pentru că
                  îi cunoștea obiceiurile și sălașurile.
                </p>
                <p>
                  După o hăituire de opt zile, Haralambie s-a refugiat acasă la
                  Gherman, care era copil și-l cunoștea, pentru că venea deseori
                  pe la el și-l mângâia pe creștet. Abia acum își dă seama
                  copilul ca acesta era tatăl lui.
                </p>
                <p>
                  Casa era înconjurată de oamenii lui Vodă, conduși de
                  Gheorghie, care, după ce și-a somat fratele să se dea prins, "
                  l-a păcălit cu hamgerul și l-a doborât". Gheorghie s-a
                  înfățișat la Divan și a pus capul fratelui său "pe năframa
                  rosa, la picioarele lui Vodă", apoi a cerut să fie eliberat
                  din armata domnească și lăsat să se retragă pe "pământurile
                  lui".
                </p>
                <p>
                  Apoi, "pentru durerea și ispășirea sa și pentru iertarea
                  sufletului cel rătăcit", a construit o biserică în Iași, cu
                  hramul Sfântului Haralambie, la care se ducea călugărul
                  Gherman să se închine pentru iertarea păcatelor părinților
                  săi. După ce își revine din tulburarea produsă de istorisirea
                  monahului, comisul Ioniță le spune oaspeților că o să le
                  povestească ceva "cu mult mai minunat și mai înfricoșător".
                </p>
                <p>
                  <br />
                  <strong>Balaurul:</strong>
                  <br />
                  Este povestită de moș Leonte, care văzuse balaurul atunci când
                  era flăcău "trecut de douăzeci de ani", pe când învăța de la
                  tatăl său meșteșugul de "zodier și vraci". Trăise pe vremuri,
                  în satul Tupilați, "un boier mare și fudul", pe nume Năstasa
                  Bolomir, care avea "o barbă mare cât o coadă de păun".
                </p>
                <p>
                  El fusese însurat prima oară cu fața unui boier, care neputând
                  îndura "mâniile și asprimile lui", s-a întors plângând la
                  părinții săi. A doua nevastă era văduva unui grec, Negrupunte,
                  femeie frumoasă și bogată, care a murit după doi ani, când
                  devenise "galbană și oviliță". Deși toate femeile fugeau de
                  boier, căruia i se dusese vestea că-i mureau nevestele,
                  Năstasa Bolomir s-a însurat prin surprinderea tuturor, cu "o
                  copilă ca de șaptesprezece ani", care "râdea parcă era
                  soarele" și pe care o adusese de la Iași.
                </p>
                <p>
                  Cucoana Irinuta nu se ofilea, ba, dimpotrivă, era îmbujorată
                  și veselă, iar boierul era din ce în ce mai posac și-i făcea
                  toate poftele.Într-o zi boierul veni la tatăl lui moș Leonte
                  ca să-i destăinuiască rușinoasa comportare a soției sale,
                  care-l înșela cu Alexăndrel Vuza, fiul vornicului și să-l
                  roage să-i citească în zodii. Temându-se pentru ce ar putea
                  păți femeia zodierul îi spune boierului că toate cele aflate
                  de el sunt vorbe mincinoase, că Irinuta avea să se întoarcă la
                  Roman, așa cum îi promisese.
                </p>
                <p>
                  Zodierul se duce apoi în grabă mare la hanul celeilalte Ancuțe
                  și-i spune că boierul își bănuiește nevasta și că el se teme
                  pentru ce o să pățească Irinuta la întoarcere. Hotărât să o
                  aștepte la han pentru a o preveni, tatăl lui moș Leonte opri
                  trăsura în care era cucoana Irinuta însoțită de tânărul și
                  frumosul Alexăndrel și le spune de primejdie.
                </p>
                <p>
                  Tocmai atunci, într-un nor de pulbere, vine ca vântul boierul
                  cu slujitorii după el și poruncește ca zodierul să fie jupuit
                  de piele pentru necredință, iar Irinuța și Alexăndrel să fie
                  legați de roțile căruței și să fie duși "numa-ntr.o goană la
                  Ieși, până-n ogadă la sfânta Mitropolie!". Cuconiță a sărit
                  însă, "sprintenă, subțire și mânioasă ca o viperă" și l-a
                  înfruntat cu ură pe bărbatul ei, iar copilului Leonte i s-a
                  părut că-i crescuse ghiare la mâini și cornițe în păr.
                </p>
                <p>
                  Dintr-o dată cerul s-a mișcat rotindu-se, un muget îngrozitor
                  a umplut văile și totsi cei de față au văzut "balaurul venind
                  în vârtej răsucit, cu mare iuțeală", luându-l pe boier și
                  amestecâdu-i "barba cu vârtejul", arucându-l aproape mort
                  într-o râpă. De aici i s-a tras boieruli Bolomir moartea , iar
                  de Irinută, "drăcușorul cel bălan", nu s-a mai auzit nimic și
                  nimeni n-a mai văzut-o vreodată.
                </p>
                <p>
                  <br />
                  <strong>Fântâna dintre plopi:</strong>
                  <br />
                  Este o idila. În lumina "soarelui auriu" care stralucea într-o
                  "liniste ca din veacuri", oaspetii de la hanul Ancutei zaresc
                  pe drum un calaret care, nu peste mult timp, poposi si el la
                  han.Comisul Ionita îl întâmpina cu bucurie, recunoscând în
                  drumetul singuratic pe Neculai Isac, capitan de mazâli. Ancuta
                  o auzise pe mama ei vorbind despre acest capitan, pe care "
                  erau sa-l omoare niste tigani", o "poveste înfricosata" pe
                  care nu o mai tinea minte. Ancuta îi aduce oaspetelui "un
                  cofaiel plin" si o "ulcica noua", lautarii venisera mai
                  aproape, "sunând din strune", iar comisul Ionita îl invita "
                  sa cinstim domnia ta o ulcica de vin nou "si-l roaga sa le
                  povesteasca "întâmplarea de demult". Neculai Isac accepta sa
                  bea vinul, care este bun în tovarasie, numai "dragostea cere
                  singuratate".
                </p>
                <p>
                  Căpitanul Isac își începe povestea petrecută în tinerețe, cu
                  douăzeci și cinci de ani înainte, când îi plăcea să cutreiere
                  Moldova, spre disperarea mamei sale, care dădea leturghii la
                  biserică pentru ca el să se liniștească pe drumuri și să se
                  însoare. Într-o toamnă, tot pe vremea celeilalte Ancuțe, pe
                  cân ducea antale cu vinuri la Suceava. Poposise la un han și
                  era "bezmetic și singur ca un cuc" pentru că îl părăsise
                  iubita.
                </p>
                <p>
                  Într-o sâmbătă, "pe la toacă", Neculai mergea îngândurat pe
                  drumul spre Suceava, când pe malul unei gârle vede o "liotă de
                  țigani", care, încercând să prindă pește, răcneau și țopăiau
                  "ca niște diavoli". Tânărul vede "o fetișcană de optsprezece
                  ani", cu o fustă roșie, care îl tulbura peste măsură,
                  "parcă-aș fi înghițit o băutură tare".
                </p>
                <p>
                  Ţigăncușă, Marga este certată de un țigănmai bătrân, pentru că
                  fata se uită țintă la boier și nu se cădea o astfel de
                  obrăznicie. Neculai le aruncă fiecăruia câte un bănuț de
                  argint și o porni spre han. A doua zi, Neculai o întâlnește pe
                  Marga la "fântâna dintre plopi", care-l aștepta să-i
                  mulțumească pentru bănuțul de argint, pă cre-și cumpărase
                  niște ciubotele, apoi fața "se mistui undeva, pe sub zidurile
                  hanului".
                </p>
                <p>
                  Neculai și-a continuat drumul spre Pașcani, urmat de câinele
                  lui credincios, Lupei, dar gândurile îi erau la frumoasa
                  țigancă, "nu v-aș putea spune ce-am grăit cu oamenii pe unde
                  am trecut, nici ce-am văzut ; căci aveam în minte chipuri și
                  vedenii care mă duceau ca-n zbor aiurea". După ce și-a
                  terminat treburile, în miez de noapte a pornit "într-o
                  întinsoare, cu șuiet de vânt în urechi" și în goana calului
                  s-a îndreptat spre hanul Ancuței. Dar ocolește hanul și se
                  duse direct "la fântâna cu patru plopi", unde o găsește,
                  așteptându-l, pe frumoasa țigancă.
                </p>
                <p>
                  Neculai promite fetei că îi va aduce de la Pașcani "o
                  scurteică de vulpe", iar ea îi spune că o să-l aștepte cu
                  nerăbdare "ș-am să mor lână fântâna dacă nu vii!". Întors de
                  la Pașcani, Neculai vînd bine antale de vin, cumpără "o
                  blăniță cu gasa de postav roș", cu gândul la plăcerea pe care
                  o vedea în ochii țigăncusei și se întoarce, cu chimirul plin
                  spre hanul Ancuței, cu intenția de a se opri mai întâi la
                  fântână.
                </p>
                <p>
                  Marga îl așteaptă în întuneric și, când el o ajută să îmbrace
                  scurteica, fata îi destăinuie că unchiul Hasanache o pusese să
                  îl atragă în locul acela pustiu, pentru că el, împreună cu cei
                  doi frați mai mici, să-i fure calul și banii pe care-i avea
                  asupra lui. Ea se teme că va fi înjunghiată dacă ei își vor da
                  seama că i-a trădat. Cu glasul încărcat de groază, Marga îl
                  îndeamnă să fugă, dându.și seama că țiganii o auziseră că-l
                  prevenise.
                </p>
                <p>
                  Neculai se aruncă pe cal, câinele se luptă în tufe cu ceva,
                  dar tânărul o luase la goană. În urma lui , șiganii urlau ca
                  niște "diavoli negri" și, la un moment dat, îl ajunseră și
                  aruncară cu prăjini în el.
                </p>
                <p>
                  Tânărul căzu de pe cal și, simțind "o lovitură de fier ascuțit
                  la coada ochiului drept", începu să tragă cu pistolul,
                  nimerindu-l pe un țigan între ochi, în timp ce Lupei "rupea pe
                  celălalt". Ochiul drept îi era plin de sânge, dar cu ochiul
                  teafăr zări lumina hanului și începu să strige cu disperare,
                  "cu-n glas schimbat de tulburare și de durere".
                </p>
                <p>
                  Cei aflați în han ies cu făclii și o pornesc cu toții înapoi,
                  spre fântâna unde se întâlnise Neculai cu Marga. Pe colacul
                  fântânii, "lucea sânge proaspăt", semn că țiganii omorâseră
                  fața pentru că-i trădase și o aruncaseră în fântână. Din
                  ochiul lui Neculai Isac, sângele "se prelingea prin mustăți
                  și-mi intră în gură. și parcă gustam din sângele împrăștiat pe
                  colacul fântânii ".
                </p>
                <p>
                  Ascultătorii acestei istorisiri groaznice rămăseseră "tăcuți
                  și mâhniți", fântână nu mai există, se distrusese "ca toate
                  ale lumii". Căpitanul Neculai "stă împovărat în locul lui,
                  neclintit și cu capul plecat", iar ochiul "cel viu, mare și
                  neguros, privea țintă în jos în neagra fântână a trecutului".
                </p>
                <p>
                  Abia pe înserat, când s-a aprins din nou focul, căpitanul Isac
                  a prins pe Ancuța de mână și a cerut pentru toții oaspeții
                  "vin vechi în oale nouă".
                </p>
                <p>
                  <br />
                  <strong>Cealaltă Ancuță:</strong>
                  <br />
                  Este personajul principal în această povestire de aventuri,
                  petrecută în "vremea veche" și este relatată de Ienache
                  coropcarul, un negustor ambulant care își prtă marfa tocmai în
                  vârful muntelui, că numai oamenii de acolo "n-au văzut încă
                  târgurile". Pe când se afla la târg la Iași îl întâlnește pe
                  arnăutul Costea Căruntul care ducea legat un "razas nebun și
                  nemernic pe nume Todirița Cătană", care urma să fie închis în
                  turnul Goliei așteptând condamnarea la moarte. Plecând de la
                  Iași, după ce trece de Târgul Frumos, coropcarul îl întâlnește
                  pe Toderiță Cătană scăpat din mâinile jandarmilor.
                </p>
                <p>
                  La hanul Ancuței se organizează prinderea lui Cătană
                  bănuindu-se că aici va veni.Ienache declară că-l cunoaște pe
                  Cătană și că acesta o apucase cu "mare spaimă spre Timișești".
                  Ancuța spune ca omul intenționa să o răpească pe duduca
                  Varvara și este însoțit și ajutat de și de alți răufăcători
                  bezmetici.
                </p>
                <p>
                  Atunci Costea plănuiește prinderea lui Cătană la Timișești,
                  lângă podul plutitor spre Agapia. Acolo moș Bâra "cu plete-n
                  ochi și surd" le spune că podul plutitor nu suportă atâta
                  greutate deoarece apele Moldovei se umflaseră peste măsură. La
                  sugestia Ancuței, Costea și Varvara hotărăsc să trecă primii
                  râul, ceilalți urmând să-l treacă cu următorul transport.
                </p>
                <p>
                  Ajunși pe celălalt mal cu podul plutitor, nu se auzea nici un
                  zgomot, nu se simțea nici o mișcare. Neliniștiți oamenii au
                  început să strige, "dar nimeni n-a înțeles ce-a fost și ce s-a
                  întâmplat". În zori gospodării din Tupilați au găsit pe
                  moșneag legat, iat pe Costea Căruntu "strâns cu funii până la
                  sânge, cu căluș de rășină în gură".
                </p>
                <p>
                  Când l-au eliberat, jupânul era turbat de furie și "atât de
                  prăpădit, încât a trebuit să-l culce oamenii lui în căruță, ca
                  să-l poată duce înapoi la agie". Ienache bănuiește că Ancuța
                  făcuse vrăji, deoarece în liniștea nopții și la lumina lunii,
                  ea părea că aude ce se întâmplă pe malul celălalt.
                </p>
                <p>
                  Nimeni n-a putut să afle ce fusese în noaptea aceea, jupânul
                  Costea n-a povestit nimănui, dar Ancuța reușise să scape cu
                  duduca Varvara în țara ungurească.
                </p>
                <p>
                  <br />
                  <strong>Județ al sărmanilor:</strong>
                  <br />O adevărată legendă, este a șasea povestire din volum.
                  Constantin Moțoc, un cioban "mătăhălos" de prin părțile
                  Rarăului, se ducea într-un sat de pe malul Șiretului ca să
                  vadă dacă mai trăiește sora lui, pe care n-o mai văzuse din
                  tinerețe.
                </p>
                <p>
                  Poposind la hanul Ancuței, el își amintește de un prieten bun,
                  care-i spusese că, dacă ajunge aici, să bea în cinstea lui
                  atâtea oale de vin până când "oi vedea tulbure", ca să nu
                  poată spune nimănui întâmplările pățite de el în aceste
                  locuri. Prietenul "acesta al meu" trăia în satul Fierbinți,
                  unde stăpânea Răducan Chioru, un boier bogat, dar "statut și
                  vioi" care atunci când avea plăcere chemă la el câte o muiere
                  din sat.
                </p>
                <p>
                  Aflând de la oamenii ca boierul o poftise pe Ilinca, nevasta
                  lui, și că acesta se întorsese acasă cu un "testemel nou, roș
                  ca focul", a simțit "că-i crește pe spinare par de câine
                  turbat". Mânios pe măsură, prietenul se repede acasă și începe
                  să o bată pe Ilinca, dar această neagă cu înverșunare că ar fi
                  fost undeva, neștiind despre ce poate fi vorba.
                </p>
                <p>
                  După ce pleacă bărbatul la treburi, muierea își pune
                  testemelul roșu , iese prin fundul grădinii și se duce direct
                  la curtea boierească. Bărbatul se duse și el să descarce sacii
                  în hambare, apoi venise la curte ca să fie scris în catastif
                  de către gramatic. Dar în cerdac iese boierul care-l întreabă
                  ce are cu muierea de o bate și o asuprește și apoi "i-a
                  fulgerat cu palma peste gură".
                </p>
                <p>
                  Bărbatul o vede pe Ilinca la fereastra conacului, dar în clipa
                  aceea boierul îl lovește cu harapnicul peste tot corpul. Când
                  scapă de boier, plin de sânge, "haidaii curții" l-au bătut cu
                  pumnii, iar omul a "bolit trei zile, mușcând scândura laiții".
                </p>
                <p>
                  După ce a scăpat cu chiu cu vai din toate acestea, a fugit în
                  munți către Rarau și s-a angajat ca slujba la niște ciobani,
                  până când a prins și el meșteșugul și "a avut el mioare și
                  berbeci". Într-o seară de primăvară, după ce a stat la un
                  pahar de vin cu vestitul haiduc Vasile cel Mare, acesta îl
                  încurajează pe Moțoc să facă dreptate în privința boierului și
                  a Ilincai. și astfel au plecat amândoi la drum până în satul
                  Fierbinți.
                </p>
                <p>
                  Ajunși aici chiar în ziua Înălțării, tocmai ieșeau oamenii din
                  biserică, iar între ei era și Răducan Chioru. După un mic
                  scandal din fața Bisericii, ciobanul "stătu iarăși închis în
                  mâhnire ca mai nainte, fără bucurie și fără lumină, ca-n
                  negurile muntelui".
                </p>
                <p>
                  <br />
                  <strong>Negustor Lipscan:</strong>
                  <br />
                  Este un reportaj și a șaptea povestire din volumul "Hanu
                  Ancuței". Într-o seară, sosesc la han, cu larma mare, niște
                  oameni îmbrăcați în alb și căruțe cu coviltir, din care "un
                  bărbat bărbos" se îndreptă către Ancuța cu urări de bun găsit.
                  Hangița îl recunoaște pe negustorul Damian Cristișor. El venea
                  cu marfă de la Lipsca și se îndreptă spre Iași.
                </p>
                <p>
                  Damian Cristișor începe să povestească despre cele văzute de
                  el în călătoriile pe alte meleaguri, pentru că fusese la Liov,
                  iar acum un an plecase la Lipsca. Negustorul le povestește
                  apoi cum arată un tren, cum arată casele nemților, portul
                  străinilor și multe altele.
                </p>
                <p>
                  O altă minunăție este că în toate târgurile și satele sunt
                  școli și profesori, toată lumea învăță carte, și băieți și
                  fete. Auzind acestea, oaspeții Ancuței închină cu veselie
                  pentru așa "rânduiala", care trebuie neapărat să rămână numai
                  la dânșii, bucurându-se că acest obicei nu venise și pe la ei.
                  Negustorul le povestește, apoi, cum un morar s-a judecat
                  "pentr-un petic de moșioară" cu însuși împăratul, dar
                  judecătorul i-a dat dreptate mororului, deoarece acolo
                  stăpânește legea.
                </p>
                <p>
                  Damian nu a pățit nimic cât timp a umblat pe drumurile
                  nemțești, călătorind apoi cu trenul, până când a ajuns la
                  Suceava, unde a pus marfa în căruțe. Intrând în Ţara Moldovei
                  vameșii l-au întrebat dacă nu le-a adus câte-un dar de la
                  "ticăloșii aceia de nemți", așa că negustorul le-a dat câte-un
                  fular lung. În lunca Moldovei l-a oprit un călăreț cerându-i
                  banii pe care îi are asupra lui, dar pentru că nu vânduse încă
                  marfa, îi dăduse un baider, spre mulțumirea hoțului.
                </p>
                <p>
                  Oprind carele să poposească și să mănânce oamenii și
                  animalele, vine la el supraveghetorul acelor locuri și-i ceru
                  actele oficiale care-i permiteau să facă negoț cu marfă care o
                  avea. Damian avea toate actele necesare, dar ca să fie lăsat
                  în pace i-a dăruit omului un "baider ros de lână", dar când va
                  ajunge la Iași, negustorul va trebui să mai facă o dare către
                  maica Paraschiva și Părintele Mardare, apoi să dea ceva
                  nasului său, după care își va putea vedea de drumul său. În
                  veselia generală, Ancuța aduce "plăcinte cu poalele-n brâu",
                  iar negustorul îi prinde hangiței "o zgârdărită de mărgele" și
                  o sărută pe amândoi obrajii.
                </p>
                <p>
                  <br />
                  <strong>Orb sărac:</strong>
                  <br /> Este o specie aparte, o portretizare prin narațiune.
                  Dinspre carele negustorului lipscan vin la han o babă și un
                  moșneag. Ea pășea înainte, iar moșneagul din urmă ei era orb,
                  femeia trăgându-l după dânsa.
                </p>
                <p>
                  Comisul Ioniță se lauda din nou că va spune o povestire mai
                  frumoasă decât tot ce-au auzit, iar orbul se învoiește că va
                  cânta din cimpoi, ca să meargă vinul mai bine. Orbul le cântă
                  din gură și acompaniindu-se cu cimpoiul, balada Miorița,
                  impresionând până la lacrimi întreaga adunare din han. Atunci
                  bătrânul le povestește de ce a cântat tocmai această baladă.
                </p>
                <p>
                  Rămânând de mic copil fără vedere, a plecat din sat și s-a
                  așezat pe lângă niște ciobani care l-au învățat acest cântec,
                  punându-l să jure că nu-l va uita niciodată. După ce s-a
                  despărțit de ciobani, orbul s-a însoțit de Ierofei, un calic
                  bătrân. Au umblat prin toată lumea și nimeni nu i-au întrebat
                  nimic.
                </p>
                <p>
                  <strong>Istoria Zahariei Fântânarului</strong>
                  <br />
                  Încheie volumul de povestiri "Hanu Ancuței". Lelița Salomia se
                  arată nemulțumită de povestirea orbului, considerând că la han
                  sunt oameni mult mai vrednici care știu a istorisi întâmplări
                  mult mai interesante. Ea spune că moș Zaharia fântânarul,
                  trăise în tinerețe o întâmplare extraordinară și că ar trebui
                  povestită.
                </p>
                <p>
                  Când era flăcău, Zaharia fusese chemat la curte de boierul
                  Dimachi Mârza din Păstrăveni, ca să găsească ca să găsească
                  cel mai bun loc pentru a săpa o fântână. Zaharia avea și acum
                  la el o cumpănă din lemn pe care o moștenise.
                </p>
                <p>
                  Cu această cumpănă a reușit el găsească loc în poiana unde să
                  facă fântână. Lița Salomia povestește în continuare, că la
                  vânătoarea domnească, Zaharia trebuia să-l întâmpine pe Vodă
                  cu apă rece. A doua zi Zaharia s-a dus la fântâna să-l
                  întâmpine pe Vodă și o găsește acolo pe copila cuconului
                  Dimachi, care plângea.
                </p>
                <p>
                  Ea îi destăinuie fântânarului că nu poate trăi fără Ilieș
                  Ursachi și că se va arunca în fântână, iar atunci când va veni
                  Vodă, Zaharia să-i spună că fața boierului s-a aruncat în
                  fântână. Fata trimisese vorba lui Ilieș să vină aici, ca să
                  petreacă acest ultim ceas.
                </p>
                <p>
                  Zaharia o sfătuiește să meargă împreună cu iubitul în "coliba
                  de frunzar", iar apoi să vină Vodă să-i binecuvânteze. Între
                  timp, Vodă aflase de la un slujitor de ce plângea copilă, așa
                  că după ce bău paharul cu apă, în loc să intre în colibă,
                  chemă la el pe boier și-i porunci s-o aducă pe fiica lui la
                  masă. Cuconul Dimachi era foarte încurcat, pentru că aflase că
                  fata lui fugise din casa părintească și avea de gând să-și
                  facă seama.
                </p>
                <p>
                  Atunci, curajos, Zaharia scoate cumpăna din chimir,
                  prefăcându-se că ea îi arătă calea de a găsi fața, deschise
                  ușa colibei și Vodă văzu "pe tinerii îngenunchiați cu capetele
                  plecate. Cu toții s-au mirat de înțelepciunea cumpenei. Pe
                  urmă, Vodă și Doamna au cununat pe cei doi tineri și a fost o
                  nuntă mare cu veselie și s-au împăcat cu toții.
                </p>

                <p>
                  Comisul Ionițâ de la Drăgănești apreciază povestirea lui
                  Zaharia,însă e convins că alții știu istorisiri "și mai
                  frumoase și mai înrteresante". "Eroii nu povestesc spre a-și
                  ușura sufletul, ori spre a reda viața; ci pentru a se sustrage
                  vieții și morții" (Nicolae Manolescu-"Imaginarul sadovenian").
                  Ajunși în "târgul cel mare al Chiului" cei doi au trăit bine,
                  și-au deprins atunci și alte cântece de jale. După ce a murit
                  Ierofei, orbul s-a înhăitat cu alții cu care a străbătut lumea
                  întreagă.
                </p>
                <p>
                  Ducându-se în satul natal, află că nu mai trăia nimeni din
                  familia lui. Atunci el a auzit de hanul Ancuței, unde venise
                  cu mulți ani în urmă, iar acum e bucuros să poposească din nou
                  aici. Orbul se ducea și el "la moaștele sfintei Paraschiva de
                  la Trei- Sfetite" pentru că atunci când era copil auzise de la
                  un străbunic despre o minune a sfintei. Era în vremea lui
                  Duca-Vodă, care domnea asupra țării Moldovei, având o mare
                  lăcomie pentru aur și argint și punând biruri mari asupra
                  moldovenilor. Slujitorii domnului umblau călare prin sate și
                  luau bani.
                </p>
                <p>
                  Într-o zi nemaiputând îndura oprimarea niște necăjiți s-au dus
                  la moaștele sfintei și l-au pârât pe Duca-Vodă. Ca urmare, în
                  ziua de 14 octombrie s-a pornit viscol mare și până a doua zi
                  s-a așternut zăpadă mare.
                </p>
                <p>
                  Demonul adus de vânt a bătut în geam la curtea domnească și
                  i-a spus lui Duca-Vodă că venise vremea socotelilor. Speriat
                  de moarte, Vodă a strâns ce a putut și a fugit dar a fost
                  prins de niște leși care l-au prădat de bani.
                </p>
                <p>
                  Cumpărând cu ultimii bani o sanie și o iapă, Vodă ajunse la
                  acest han, dar a fost alungat de acolo și până la urmă Vodă
                  și-a găsit sfârșitul în niște locuri pustii. Ancuța se repede
                  la orb, recunoscând în uncheșul pribeag pe "unul Constandin",
                  despre care îi povestise mama ei, îi sărută mâna și îi dă să
                  mănânce.
                </p>
              </div>
            </div>
          </div>
        </Content>
      </div>
    );
  }
}
